import React from 'react';
import { StyleSheet, View, TouchableOpacity, Text, Image} from 'react-native';

import { ApplicationStyles, Helpers, Metrics, Fonts, Colors, Images } from '../Theme'

const Button = props => {
  var TextView = [<Text style={[styles.text, props.textStyle]} key={0}>{props.Text}</Text>]

  var ImageView = []
  if (props.icon) {
    var _Image = <Image style={Helpers.fullSize} source={props.icon} resizeMode={'contain'} />
    ImageView = (
      <View style={[styles.icon, props.iconStyle]}>
        {_Image}
      </View>
    )
  }
  return (
    <TouchableOpacity style={[styles.boxContainer, props.boxStyle]} onPress={props.onPress}>
      {props.icon?(
        <View style={[styles.button, props.buttonStyle, Helpers.rowCenter]}>
          <View style={[Helpers.colCenter, Helpers.fill]}>
            {TextView}
          </View>
            {ImageView}
        </View>
      ):(
        <View style={[styles.button, props.buttonStyle]}>
          {TextView}
        </View>
      )}
    </TouchableOpacity>
  );
};

const styles = StyleSheet.create({
  boxContainer:{
    ...Helpers.center,
    ...Metrics.tinyVerticalMargin,
  },
  button: {
    borderRadius: 15,
    ...Helpers.center,
    ...Helpers.fullWidth,
    ...Metrics.horizontalPadding,
    ...ApplicationStyles.button,
  },
  text: {
    ...ApplicationStyles.buttonText,
    ...Metrics.verticalPadding,
    ...Helpers.center,
    textAlign:"center",
  },
  icon: {
    ...Helpers.colCenter,
    ...Metrics.smallVerticalMargin,
    width: '25%', 
    height: 50,
  },
});

export default Button;