import React, { useEffect, Component } from 'react'
import { Text, View, ActivityIndicator, ScrollView, FlatList } from 'react-native'
import { Input, Icon } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { DataRefs, Actions, GetBatchEnd, CurvePredict, GetCurve_FromBatch, GetStats_FromBatch  } from '../../../Stores/Firebase/Selectors'

import { Button, TitleElement, ContentProp, ProgressBar } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

class Dashboard extends Component {
  constructor(props){
    super(props);

    const {Name, Manager, Mail, SectionsList, BatchList, MaxCap, CurCap} = this.setData(props)
    this.state = {
      farmName: Name,
      farmManager: Manager,
      farmMail: Mail, 

      BatchList: BatchList,

      MaxCap:MaxCap, 
      CurCap:CurCap,
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;
    const firebaseUser = this.props.firebase.firebaseUser

    var Name = "Not set"
    var Manager = "Not set"
    var Mail = "Not set"

    if (localDataStore && localDataStore.farms && firebaseUser.farms && localDataStore.farms[Object.keys(firebaseUser.farms)[0]]) {
      var Farm = localDataStore.farms[Object.keys(firebaseUser.farms)[0]]
      Name = Farm.name?Farm.name: Name
      Manager = Farm.name?Farm.manager: Manager
      Mail = Farm.name?Farm.email: Mail
    }

    var BatchList = {}//localDataStore.batchs
    var MaxCap = 0;
    var CurCap = 0;

    if (localDataStore.batchs) {
      Object.keys(localDataStore.batchs).filter(e => e !== '__jsogObjectId' && e !== 'update_time').map((BatchID) => {
        var Batch = localDataStore.batchs[BatchID]
        var Data = GetStats_FromBatch(Batch, localDataStore)
        if (Data) {
          var {StartDate, EndDate, TargetWeight, PenCount, Animals, AverageWeight, AverageGrowth, isClosed, isVirtual} = Data
          
          if (Batch.pens) {
            MaxCap += Object.keys(Batch.pens).reverse().filter(e => (e !== '__jsogObjectId')).length * 1;
          }
          CurCap += Animals;

          Batch.count = Animals
          Batch.weight = AverageWeight
          Batch.target = TargetWeight
          Batch.End = EndDate

          BatchList[BatchID] = Batch
        }
      })
    }

    if (MaxCap === 0) MaxCap++;
    return { Name, Manager, Mail, BatchList, MaxCap, CurCap }
  }

  getData() {
    const localDataStore = this.props.firebase.localDataStore;

    if (!localDataStore.sections) localDataStore.sections = {}
    if (!localDataStore.pens) localDataStore.pens = {}

    this.props.getFarm()
    this.props.getSections(localDataStore.sections?localDataStore.sections.update_time:null)
    this.props.getPens(localDataStore.pens?localDataStore.pens.update_time:null)
    this.props.getBatch()
    this.props.getWeights(localDataStore.weighings?localDataStore.weighings.update_time:null, localDataStore.sections)
    this.props.getCurve()
  }

  checkData(props) {
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);

      if (this.props.firebase.localDataStore && prevProps.firebase.localDataStore) {
        const firebaseUser = this.props.firebase.firebaseUser
        const localDataStore = this.props.firebase.localDataStore;

        if (localDataStore.farms && firebaseUser.farms && localDataStore.farms[Object.keys(firebaseUser.farms)[0]]) {
          var Farm = localDataStore.farms[Object.keys(firebaseUser.farms)[0]]

          if ((this.state.farmName !== Farm.name || this.state.farmManager !== Farm.manager || this.state.farmMail !== Farm.email) ||
              localDataStore.batchs && JSON.stringify(this.state.BatchList) !== JSON.stringify(localDataStore.batchs) ||
              localDataStore.graphs && localDataStore.graphs !== prevProps.firebase.localDataStore.graphs) {
            const {Name, Manager, Mail, BatchList, MaxCap, CurCap} = this.setData(this.props)
            this.setState({
              farmName: Name,
              farmManager: Manager,
              farmMail: Mail,
        
              BatchList: BatchList,

              MaxCap:MaxCap, 
              CurCap:CurCap,
            }, () => {
              this.forceUpdate()
            })
          }
        }
      }
    }
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const localDataStore = this.props.firebase.localDataStore

    var FarmProgress = 0
    if (this.state.BatchList) {
      var BatchList = Object.keys(this.state.BatchList).filter(e => e !== '__jsogObjectId' && e !== 'update_time' && (!this.state.BatchList[e].state || this.state.BatchList[e].state !== "Closed"))
      BatchList.forEach(element => {
        var Batch = this.state.BatchList[element]
        FarmProgress += (Batch.weight/Batch.target)*100;
      });
      FarmProgress = (FarmProgress/(100*BatchList.length))*100
    }

    return(
      <ScrollView style={[Metrics.verticalMargin, {paddingRight: 5}]}>
        <View
          style={[
            Helpers.mainContainer,
          ]}
        >
          <View style={[Helpers.contentContainer, Helpers.fullWidth, Helpers.fill]}>
            <TitleElement Text="Farm description"/>

            <View style={[Helpers.buttonContainer, Helpers.fullWidth, {height:110,}]}>
              <ContentProp texts={["Farm name", "Manager", "E-mail"]} datas={[this.state.farmName, this.state.farmManager, this.state.farmMail ]} />
            </View>
            <View style={{height:20}}/>
            <ProgressBar text={"Farm capacity: " + this.state.CurCap + " of " + this.state.MaxCap} progress={(this.state.CurCap/this.state.MaxCap) * 100 }/>
            <ProgressBar text={"Farm performance:"} progress={FarmProgress}/>

            {this.state.BatchList?(
              <View style={[Helpers.borderContainer, Helpers.fullWidth, {borderWidth:0, marginTop:10}]}>
                <Text>Batches:</Text>
                <FlatList
                  data={Object.keys(this.state.BatchList).filter(e => e !== '__jsogObjectId' && e !== 'update_time' && 
                                                                (!this.state.BatchList[e].state || this.state.BatchList[e].state !== "Closed"))}
                  renderItem={({item, i}) => {
                    var Batch = this.state.BatchList[item]
                    var TempCurve = GetCurve_FromBatch(Batch, localDataStore.graphs)
                    var End = GetBatchEnd(Batch, TempCurve)
                    var EndDate = Batch.End;
                    var Color = Colors.AgriGreen
                    if (EndDate > End) Color = Colors.error
                    var count = Batch.count
                    if (!count) count = 0
                    var progress = (Batch.weight/Batch.target)*100;

                    EndDate = new Date(EndDate).getDate() + "/" + (new Date(EndDate).getMonth() +1) + "/" + new Date(EndDate).getFullYear()
                    var StartDate = new Date(Batch.time).getDate() + "/" + (new Date(Batch.time).getMonth() +1) + "/" + new Date(Batch.time).getFullYear()
                    return(
                      <ProgressBar text={item + " Startdate: " + StartDate + " EndForcast: " + EndDate + ", " + count + " pigs"} progress={progress} color={Color}/>
                    )
                  }}
                  keyExtractor={(item, index) => index.toString()}
                />
              </View>
            ):(<></>)}
            <Button Text="Clear local datastore"
              onPress={() => {
                this.props.clearDataMem()
                this.getData()
              }}/>
          </View>
          {(IsLoading || this.props.firebase.viewIsLoading) &&
            <ActivityIndicator style={Helpers.loadingContainer} size='large' />
          }
        </View>
      </ScrollView>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    clearDataMem: () => dispatch(FirebaseActions.actionStarting(Actions.CLEAR, DataRefs.USERSDATA)),

    getFarm: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.FARMS, {})),
    getSections: (DataTime) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.SECTIONS, {DataTime: DataTime})),
    getPens: (DataTime) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.PENS, {DataTime: DataTime})),
    getBatch: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.BATCHS, {})),
    getCurve: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.GRAPH, {})),
    getWeights: (DataTime, MemSections) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.WEIGHING, {DataTime: DataTime, MemSections: MemSections})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Dashboard);
//export default Dashboard;
