import React, { useState } from 'react';
import { Platform, View, Text, TextInput, Image, StyleSheet, RecyclerViewBackedScrollViewComponent} from 'react-native';
import Button from './Button'
import { Icon, Input } from 'react-native-elements'
import { Operations } from '../Stores/Firebase/Selectors'

import { ApplicationStyles, Helpers, Metrics, Fonts, Colors } from '../Theme'

const Comment_Module = props => {
  const Title = props.Title;
  const Quantity = props.Quantity?props.Quantity:false
  const Weight = props.Weight?props.Weight:false
  
  const onDone = props.onDone;
  var Button1 = "Done";
  var Button2 = "Cancel";
  const [InputText, setInputText] = useState("");
  const [QuantityText, setQuantityText] = useState("1");
  const [WeightText, setWeightText] = useState("");

  var App = Platform.OS === "android" || Platform.OS === "ios" ? true: false
  return(
    <View style={[Helpers.buttonContainer, props.style] }>
      <View style={styles.textAreaContainer} >
        <View style={[App?{}:Helpers.row]}>
          {Title?(<Text style={[Fonts.normal, {height: 24, color:Colors.input}]}>{Title}</Text>):(<></>)}
          {Quantity?(
            <View style={[Helpers.row, App?{}:{marginLeft:50, marginBottom:4}]}>
              <Text style={[Fonts.normal, {height: 24, color:Colors.input}]}>Quantity:</Text>
              <TextInput style={[styles.textArea, {marginLeft: 10, height:22, width:80, paddingBottom:-10}]} 
                multiline={false}
                onChangeText={(text) => {
                  if (text === "" || !isNaN(text)) setQuantityText(text)
                }}
                value={QuantityText}
              />
            </View>
          ):(<></>)}
        </View>
        {Weight?(
          <View style={[Helpers.row, App?{}:{marginBottom:4}]}>
            <Text style={[Fonts.normal, {height: 24, color:Colors.input}]}>Average Weight:</Text>
            <TextInput style={[styles.textArea, {marginLeft: 10, height:22, width:80, paddingBottom:-10}]} 
              multiline={false}
              onChangeText={(text) => {
                if (text === "" || !isNaN(text)) setWeightText(text)
              }}
              value={WeightText}
            />
          </View>
        ):(<></>)}
        <TextInput
          style={styles.textArea}
          underlineColorAndroid="transparent"
          placeholder="Type something"
          placeholderTextColor="grey"
          numberOfLines={10}
          multiline={true}
          onChangeText={(text) => setInputText(text)}
          value={InputText}
        />
      </View>
      <Button key={"_Done"} Text={Button1}
        onPress={() => {
          if (onDone) {
            onDone({res: true, data: InputText, quantity: QuantityText, weight: WeightText})
          }
        }}/>
      <Button key={"_Cancel"} Text={Button2}
        onPress={() => {
          if (onDone) {
            onDone({res: false, data: InputText})
          }
        }}/>
    </View>
  )
};

const styles = StyleSheet.create({
  textAreaContainer: {
    borderColor: Colors.gray,
    borderWidth: 1,
    padding: 5
  },
  textArea: {
    height: 150,
    justifyContent: "flex-start",
    borderColor: "black",
    borderWidth: 0.5,
    padding: 5,
  }
})

export default Comment_Module;