/**
 * Reducers specify how the application's state changes in response to actions sent to the store.
 *
 * @see https://redux.js.org/basics/reducers
 */

import { createReducer } from 'reduxsauce'

import { ConnectionState, ConnectionMethode } from './Selectors'
import { DeviceTypes } from './Actions'

import { INITIAL_STATE } from './InitialState'

//BLE Saga's
import * as BLEReducers from './BLE/Reducers'
//BlueTooth Saga's
import * as BluetoothReducers from './BlueTooth/Reducers'

export const setDeviceError = (state, { errorMessage }) => ({
  ...state,
  deviceErrorMessage: errorMessage,
})

export const scannerStateUpdated = (state, {scannerState}) => {
  return({
    ...state,
    scannerState: scannerState,
  })
}

export const addDevice = (state, {device}) => {
  if (state.DeviceList.some(Device => Device.id === device.id )) { //&& device.isConnectable)){
    return({...state});
  } else {
    //device._manager = null; // Type.script Stringify ... not allowing function callback (fix see CreateStore.js)
    var NewList = [...state.DeviceList];
    NewList.push(device);

    return({
      ...state,
      DeviceList: NewList,
    });
  }
}

export const removeDevice = (state, {device}) => {
  var NewList = [...state.DeviceList];
  for( var i = 0; i < NewList.length; i++){ 
    if ( NewList[i] === device) {
      NewList.splice(i, 1); 
    }
  }

  return ({
    ...state,
    DeviceList: NewList,
  });
}

export const clearDevices = (state) => ({
  ...state,
  DeviceList: [],
  deviceData: {}
})

export const connectedDeviceUpdated = (state, {connectedDevice}) => {
  if (!connectedDevice) {
    return ({
      ...state,
      connectedDevice: connectedDevice,
      //deviceData: {}
    })
  } else {
    return ({
      ...state,
      connectedDevice: connectedDevice
    })
  }
}

// Connections *******************************************
export const connectionStateUpdated = (state, {connectionState}) => ({
  ...state,
  connectionState: connectionState
})
export const connectionMethodeUpdated = (state, {connectionMethode}) => ({
  ...state,
  connectionMethode: connectionMethode
})

export const setDeviceData = (state, {data}) => ({
  ...state,
  deviceData: data
})
export const updateDeviceData = (state, {data}) => {
  const newData = {...state.deviceData, ...data}
  return({
  ...state,
  deviceData: newData
})}

/**
 * @see https://github.com/infinitered/reduxsauce#createreducer
 */
export const HANDLERS = {
  //Response...
  [DeviceTypes.SET_DEVICE_ERROR]: setDeviceError,
  
  //Managers...
  [DeviceTypes.SCANNER_STATE_UPDATED]: scannerStateUpdated,

  [DeviceTypes.ADD_DEVICE]: addDevice,
  [DeviceTypes.REMOVE_DEVICE]: removeDevice,
  [DeviceTypes.CLEAR_DEVICES]: clearDevices,

  [DeviceTypes.CONNECTED_DEVICE_UPDATED]: connectedDeviceUpdated,
  
  //Connection...
  [DeviceTypes.CONNECTION_STATE_UPDATED]: connectionStateUpdated,
  [DeviceTypes.CONNECTION_METHODE_UPDATED]: connectionMethodeUpdated,

  //Data...
  [DeviceTypes.SET_DEVICE_DATA]: setDeviceData,
  [DeviceTypes.UPDATE_DEVICE_DATA]: updateDeviceData,

  //BLE Saga
  ...BLEReducers.HANDLERS,

  //Android BlueTooth Saga
  ...BluetoothReducers.HANDLERS,
}

export const reducer = createReducer(INITIAL_STATE, HANDLERS)