import {buffers, eventChannel} from 'redux-saga'
import { put, call, fork, take, cancelled, cancel, actionChannel, race, select } from 'redux-saga/effects'

import DeviceActions from '../../Stores/Device/Actions'
import { deviceService } from '../../Services/DeviceService'

import { ScannerState, ConnectionState, ConnectionMethode } from '../../Stores/Device/Selectors'

import { BleManager, BleError, Device} from 'react-native-ble-plx'
//Include BLE Saga
import * as BLE from './BLE/BLESaga'
export {BLE}
//Include BlueTooth Saga
import * as BlueTooth from './BlueTooth/BlueToothSaga'
export {BlueTooth}

export function* deviceStartup() {
  //Check and setup BLE.... / Bluetooth
  yield put(DeviceActions.setDeviceError(""))
  yield put(DeviceActions.clearDevices());
  yield put(DeviceActions.connectedDeviceUpdated(null)); // TODO: load connected device if possible
  yield put(DeviceActions.connectionStateUpdated(ConnectionState.DISCONNECTED));

  yield put(DeviceActions.bleStartup());
  yield put(DeviceActions.bluetoothStartup());
  
  console.log('Device Startup : Done');
}

export function* connectDevice(device: Device){
  yield put(DeviceActions.scannerStateUpdated(ScannerState.IDLE))
  //console.log("Device: ", device); // dummy call to connect...
}

export function* disconnectDevice(methode){
  //console.log("Device: ", device); // dummy call to disconnect...
}

export function* readDeviceData(){
  var connectedDevice = yield select((state) => state.device.connectedDevice)
  if (connectedDevice != null) {
    var connectionMethode: $Keys<typeof ConnectionMethode> = yield select((state) => state.device.connectionMethode)
    if (connectionMethode == ConnectionMethode.BLE) {
      yield put(DeviceActions.readBleDeviceData())
    } else if (connectionMethode == ConnectionMethode.BLUETOOTH) {
      yield put(DeviceActions.readBlueDeviceData())
    }
  }
}

