import React, { useEffect, Component } from 'react'
import { Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, Picker } from 'react-native'
import { Input } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject, DataStringCheck } from '../../../Stores/Firebase/Selectors'

import { Button, TitleElement } from '../../../Components'
import Style from '../../../Containers/Cloud/CloudScreenStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

class DataEditor extends Component {
  constructor(props){
    super(props);

    const {Sections, Section, Pens, Pen} = this.setData(props)
    this.state = {
      Sections: Sections,
      Section: Section,

      Pens: Pens,
      Pen: Pen,
      NewSection: (Sections&&Object.keys(Sections).length > 0)?false:true,
    }
  }

  getData() {
    const localDataStore = this.props.firebase.localDataStore;
    if (!localDataStore.sections) localDataStore.sections = {}
    this.props.getSections(localDataStore.sections.update_time)
  }

  setData(Props) {
    //Set Sections
    const localDataStore = Props.firebase.localDataStore;
    var Sections = false;
    
    if (localDataStore && localDataStore.sections && localDataStore.sections !== Sections) {
      Sections = sortObject(localDataStore.sections)
    }

    //Set Section
    const CurrentData = Props.firebase.currentData;
    var Section = Sections? Object.keys(Sections)[0] : ""
    if (CurrentData && CurrentData.data && CurrentData.data.section && Sections[CurrentData.data.section]) {
      if (CurrentData.data.section != Section) {
        Section = CurrentData.data.section
      }
    }

    //Pens & Pen
    var Pens = (Sections&&Sections[Section])? sortObject(Sections[Section].pens): {}
    var Pen = Sections? Object.keys(Pens)[0]: ""
    if (CurrentData && CurrentData.dataId && Pens[CurrentData.dataId] && CurrentData.dataId != Pen){
      Pen = CurrentData.dataId
    }

    if (!Sections) Sections = {}
    return {Sections, Section, Pens, Pen}
  }

  checkData(props) {
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase)  {
        if (this.props.firebase.localDataStore) {
          if (this.props.firebase.localDataStore.sections && JSON.stringify(this.state.Sections)!==JSON.stringify(sortObject(this.props.firebase.localDataStore.sections))) {
            
            const {Sections, Section, Pens, Pen} = this.setData(this.props)

            this.setState({
              Sections: Sections,
              Section: Section,
              Pens: Pens,
              Pen: Pen,
              NewSection: (Sections&&Object.keys(Sections).length > 0)?false:true,
            })
          }
        }
        if (this.props.firebase.currentData) {
          if (this.props.firebase.currentData != prevProps.firebase.currentData){
            this.forceUpdate()
          }
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ErrorMessage = this.props.firebase.viewError

    const localDataStore = this.props.firebase.localDataStore
    const CurrentData = this.props.firebase.currentData

    var DataView = (<View></View>);

    switch(CurrentData.type){
      case DataRefs.PENS:
        DataView = PenEditor_View_Creator(this, CurrentData, localDataStore)
        break;
      case DataRefs.PIGS:
        DataView = PigEditor_View_Creator(this, CurrentData, localDataStore)
        break;
    }

    return(
      <View
      style={[
        Helpers.mainContainer,
      ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
          {ErrorMessage ? (
            <Text style={ApplicationStyles.error}>{ErrorMessage}</Text>
          ):(<></>)}
          {DataView}
        </View>
        {(IsLoading || this.props.firebase.viewIsLoading) &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const PenEditor_View_Creator = (Control, CurrentData, localDataStore) => {
  var Sections = Control.state.Sections;
  if (CurrentData.action === Actions.CREATE) {
    Sections["New Section"] = {}
  }

  return(
    <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
      <PenEditor_Titles CurrentAction={CurrentData.action}/>
      
      <View style={ [Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
        <Section_Picker Control={Control} CurrentData={CurrentData} localDataStore={localDataStore}/>
        {CurrentData.action === Actions.CREATE ?(
          <Input
            label="Pen name"
            style={{ ...Style.input}}
            onChangeText={text => {
              if (text){
                if (DataStringCheck(text)){
                  Control.setState({Pen: text})
                }
              }else {
                Control.setState({Pen: text})
              }
            }}
            value={Control.state.Pen}
          />
        ):(<></>)}
        {CurrentData.action === Actions.DELETE ?(
          <Picker
            mode="dropdown"
            selectedValue={Control.state.Pen}
            style={{ height: 50, width: 150 }}
            onValueChange={(itemValue, itemIndex) => {
              Control.setState({
                Pen: itemValue
              })
            }}
          >
            {Object.keys(Control.state.Pens).map((Pen, i) => {
                return (<Picker.Item label={Pen} value={Pen} key={i}/>)
            })}
          </Picker>
        ):(<></>)}
      </View>

      {CurrentData.action === Actions.CREATE || CurrentData.action === Actions.DELETE ?(
        <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
          <Button Text="Submit"
            onPress={() => {
              if (!Control.state.Section || Control.state.Section === ""){
                Control.props.setViewError("A section is needed")
              } else if (!Control.state.Pen || Control.state.Pen === ""){
                Control.props.setViewError("A pen name is needed")
              } else if (!isNaN(Control.state.Section) || !isNaN(Control.state.Section[0])){
                Control.props.setViewError("Section name cannot be or start with a number")
              } else if (!isNaN(Control.state.Pen) || !isNaN(Control.state.Pen[0])){
                Control.props.setViewError("Pen name cannot be or start with a number")
              }
              else {
                var exists = false;

                Object.keys(Control.state.Sections).map((section, i) => {
                  if (Control.state.Sections[section].pens){
                    var test = Object.keys(Control.state.Sections[section].pens).filter(e => e === Control.state.Pen).length
                    if (test) exists = true
                  }
                })

                if (exists && CurrentData.action === Actions.CREATE) {
                  Control.props.setViewError("Given pen name already exists")
                } else if (!exists && CurrentData.action === Actions.DELETE){
                  Control.props.setViewError("Cannot remove non-existing pen")
                }
                else {
                  Control.props.setViewError(null)
                  Control.props.setViewLoading(true)
                  Control.props.penAction(CurrentData.action, Control.state.Section, Control.state.Pen)
                  NavigationService.navigate("UserOperations")
                }
              }
            } }/>
        </View>
      ):(<></>)}
    </View>
  )
}

const PenEditor_Titles = (props) => {
  switch(props.CurrentAction) {
    case Actions.CREATE:
      return(
        <TitleElement Text={"Add Pen"}/>
      )
    case Actions.DELETE:
      return(
        <TitleElement Text={"Remove Pen"}/>
      )
    default:
      return(<></>)
  }
}

const PigEditor_View_Creator = (Control, CurrentData, localDataStore) => {
  var Sections = Control.state.Sections;
  if (CurrentData.action === Actions.CREATE) {
    //Sections["New Section"] = {}
  }

  return(
    <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
      <TitleElement Text={"Add Pig: " + CurrentData.pigtag}/>
      
      <View style={ [Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
        <Section_Picker Control={Control} CurrentData={CurrentData} localDataStore={localDataStore}/>
        {Control.state.NewSection?(
        <Input
          label="Pen name"
          style={{ ...Style.input}}
          onChangeText={text => {
            if (text){
              if (DataStringCheck(text)){
                Control.setState({Pen: text})
              }
            }else {
              Control.setState({Pen: text})
            }
          }}
          value={Control.state.Pen}
        />
        ):(
          <Picker
            mode="dropdown"
            selectedValue={Control.state.Pen}
            style={{ height: 50, width: 150 }}
            onValueChange={(itemValue, itemIndex) => {
              Control.setState({
                Pen: itemValue
              })
            }}
          >
            {Object.keys(Control.state.Pens).map((Pen, i) => {
                return (<Picker.Item label={Pen} value={Pen} key={i}/>)
            })}
          </Picker>
        )}
        {CurrentData.action === Actions.CREATE?(
        <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
          <Button Text="Submit"
            onPress={() => {
              if (!Control.state.Section || Control.state.Section === ""){
                Control.props.setViewError("A section is needed")
              } else if (!Control.state.Pen || Control.state.Pen === ""){
                Control.props.setViewError("A pen name is needed")
              } else if (!isNaN(Control.state.Section) || !isNaN(Control.state.Section[0])){
                Control.props.setViewError("Section name cannot be or start with a number")
              } else if (!isNaN(Control.state.Pen) || !isNaN(Control.state.Pen[0])){
                Control.props.setViewError("Pen name cannot be or start with a number")
              }
              else {
                var exists = false;

                Object.keys(Control.state.Sections).map((section, i) => {
                  if (Control.state.Sections[section].pens){
                    var test = Object.keys(Control.state.Sections[section].pens).filter(e => e === Control.state.Pen).length
                    if (test) exists = true
                  }
                })

                if (exists && CurrentData.action === Actions.CREATE && Control.state.NewSection) {
                  Control.props.setViewError("Given pen name already exists")
                } else {
                  Control.props.setViewLoading(true)
                  Control.props.setViewError(null)
                  Control.props.pigAction(CurrentData.action, CurrentData.pigtag, Control.state.Pen)
                  NavigationService.navigate("UserPigs")
                }
              }
            } }/>
        </View>
      ):(<></>)}
      </View>
    </View>
  )
}

const Section_Picker = ({Control, CurrentData, localDataStore}) => {
  if (Control.state.NewSection && CurrentData.action === Actions.CREATE){
    return(
      <Input
        label="Section Name"
        style={{ ...Style.input}}
        onChangeText={text => {
          if (text){
            if (DataStringCheck(text)){
              Control.setState({Section: text})
            }
          }else {
            Control.setState({Section: text})
          }
        }}
        value={Control.state.Section}
      />
    )
  } else {
    return(
      <Picker
        mode="dropdown"
        selectedValue={Control.state.Section}
        style={{ height: 50, width: 150 }}
        onValueChange={(itemValue, itemIndex) => {
          var Section = itemValue
          var NewSection = Control.state.NewSection
          var Pens = Control.state.Pens
          var Pen = Control.state.Pen

          if (itemValue === "New Section"){
            NewSection = true;
            Section = "Section_" + Object.keys(Control.state.Sections).length
          } else {
            Pens = sortObject(Control.state.Sections[itemValue].pens)
            
            if (CurrentData.action === Actions.DELETE){
              Pen = Object.keys(Pens)[Object.keys(Pens).length-1]
            }
            else {
              if (CurrentData.type == DataRefs.PIGS){
                Pen = Object.keys(Pens)[0]
              }else {
                Pen = "Pen_" + (Object.keys(Pens).length +1)
              }
            }
          }
          
          Control.setState({
            Section: Section,
            NewSection: NewSection,
            Pens: Pens,
            Pen: Pen,
          })
        }}>
        {Object.keys(Control.state.Sections).map((Section, i) => {
            return (<Picker.Item label={Section} value={Section} key={i}/>)
        })}
      </Picker>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    getSections: (DataTime) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.SECTIONS, {DataTime: DataTime})),
    penAction: (action, senctionId, penId) => dispatch(FirebaseActions.actionStarting(action, DataRefs.PENS, {SectionId:senctionId, PenId:penId})),
    pigAction: (action, tagId, penId,) => dispatch(FirebaseActions.actionStarting(action, DataRefs.PIGS, {TagId:tagId, PenId:penId})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(DataEditor);
//export default DataEditor;
