import { takeLatest, all, take } from 'redux-saga/effects'

import { StartupTypes } from '../Stores/Startup/Actions'
import { ExampleTypes } from '../Stores/Example/Actions'
import { DeviceTypes } from '../Stores/Device/Actions'
import { FirebaseTypes } from '../Stores/Firebase/Actions'

import * as StartupSaga from './StartupSaga'
import * as ExampleSaga from './Example/ExampleSaga'
import * as DeviceSaga from './Device/DeviceSaga'
import * as FirebaseSaga from './Firebase/FirebaseSaga'

export default function* root(sagaArgs) {
  yield all([
    /**
     * @see https://redux-saga.js.org/docs/basics/UsingSagaHelpers.html
     */
    // Run the startup saga when the application starts
    takeLatest(StartupTypes.STARTUP, StartupSaga.startup), //Initer
    

    //Example
    takeLatest(ExampleTypes.FETCH_USER, ExampleSaga.fetchUser), // Call `fetchUser()` when a `FETCH_USER` action is triggered
    
    //Device Main Saga
    takeLatest(DeviceTypes.DEVICE_STARTUP, DeviceSaga.deviceStartup), //Initer

    takeLatest(DeviceTypes.CONNECT_DEVICE, DeviceSaga.connectDevice),
    takeLatest(DeviceTypes.DISCONNECT_DEVICE, DeviceSaga.disconnectDevice),
    takeLatest(DeviceTypes.READ_DEVICE_DATA, DeviceSaga.readDeviceData),

    takeLatest(DeviceTypes.FETCH_DEVICE, DeviceSaga.fetchDevice, sagaArgs),

    //BLE Saga
    takeLatest(DeviceTypes.BLE_STARTUP, DeviceSaga.BLE.bleStartup, sagaArgs), //Initer
    takeLatest(DeviceTypes.START_BLE_SCAN, DeviceSaga.BLE.startBleScan),
    takeLatest(DeviceTypes.STOP_BLE_SCAN, DeviceSaga.BLE.stopBleScan),
    takeLatest(DeviceTypes.READ_BLE_DEVICE_DATA, DeviceSaga.BLE.readBleDeviceData, sagaArgs),
    
    //Android BlueThooth Saga
    takeLatest(DeviceTypes.BLUETOOTH_STARTUP, DeviceSaga.BlueTooth.bluetoothStartup, sagaArgs), //Initer
    takeLatest(DeviceTypes.START_BLUETOOTH_SCAN, DeviceSaga.BlueTooth.startBluetoothScan),
    takeLatest(DeviceTypes.STOP_BLUETOOTH_SCAN, DeviceSaga.BlueTooth.stopBluetoothScan),
    takeLatest(DeviceTypes.BLUETOOTH_SCAN_ABLE, DeviceSaga.BlueTooth.bluetoothScanAble, sagaArgs),
    takeLatest(DeviceTypes.READ_BLUE_DEVICE_DATA, DeviceSaga.BlueTooth.readBlueDeviceData, sagaArgs),
    

    //Firebase Saga
    takeLatest(FirebaseTypes.FIREBASE_STARTUP, FirebaseSaga.firebaseStartup), //Initer
  ])
}
