import { Platform } from 'react-native'

import { applyMiddleware, compose, createStore } from 'redux'
import createSagaMiddleware from 'redux-saga'
import { persistReducer, persistStore, createTransform } from 'redux-persist'

import { NativeModules } from 'react-native'
import { BleManager, BleError, LogLevel } from 'react-native-ble-plx'

//** fix: error serializing state TypeError: Converting circular structure to JSON  */
// https://www.npmjs.com/package/jsog
// https://www.npmjs.com/package/flatted
import JSOG from 'jsog';
export const JSOGTransform = createTransform(
  (inboundState, key) => JSOG.encode(inboundState),
  (outboundState, key) => JSOG.decode(outboundState),
)

/**
 * This import defaults to localStorage for web and AsyncStorage for react-native.
 *
 * Keep in mind this storage *is not secure*. Do not use it to store sensitive information
 * (like API tokens, private and sensitive data, etc.).
 *
 * If you need to store sensitive information, use redux-persist-sensitive-storage.
 * @see https://github.com/CodingZeal/redux-persist-sensitive-storage
 */
import storage from 'redux-persist/lib/storage'

const persistConfig = {
  key: 'root',
  storage: storage,
  transforms: [JSOGTransform],
  /**
   * Blacklist state that we do not need/want to persist
   */
  blacklist: [
    // 'auth',
  ],
}

export default (rootReducer, rootSaga) => {
  const middleware = []
  const enhancers = []

  // Connect the sagas to the redux store
  const sagaMiddleware = createSagaMiddleware()
  middleware.push(sagaMiddleware)

  enhancers.push(applyMiddleware(...middleware))

  // Redux persist
  const persistedReducer = persistReducer(persistConfig, rootReducer)

  const store = createStore(persistedReducer, compose(...enhancers))
  const persistor = persistStore(store)

  if (Platform.OS === 'ios' || Platform.OS === 'android') {
    // First step is to create BleManager which should be used as an entry point
    // to all BLE related functionalities
    const blemanager = new BleManager();
    blemanager.setLogLevel(LogLevel.Verbose);

    const bluemanager = NativeModules.BlueToothControl;

    // SagaArgs Construcktor
    const sagaArgs = {BLEManager: blemanager, BlueManager: bluemanager}

    // Kick off the root saga
    sagaMiddleware.run(rootSaga, sagaArgs)
  }else {
    sagaMiddleware.run(rootSaga)
  }
  
  return { store, persistor }
}
