import React, { useEffect, useState, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, SafeAreaView, FlatList } from 'react-native'
import { ListItem, Input } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { Button, OperationsPanel, CustomListItem, ContentProp } from '../../../Components'
import Style from '../../../Containers/Cloud/CloudScreenStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../../Theme'

import { FlatGrid, SectionGrid } from 'react-native-super-grid';

class UserListBoard extends Component {
  constructor(props){
    super(props);

    const {UserList} = this.setData(props)
    this.state = {
      UserList: UserList,
      User: ""
    }
  }

  getData() {
    this.props.getUsers()
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore

    var UserList = null
    if (localDataStore && localDataStore.users) {
      UserList = sortObject(localDataStore.users)
    }
    
    return {UserList}
  }

  checkData(props) {
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props)
    this.getData()
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase) {
        if (this.props.firebase.localDataStore) {
          if (this.props.firebase.localDataStore.users && JSON.stringify(this.state.UserList)!==JSON.stringify(sortObject(this.props.firebase.localDataStore.users))) {
            const {UserList} = this.setData(this.props)
            this.setState({
              UserList: UserList,
            })
          }
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading

    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullWidth, Helpers.fill]}>
          <View style={[Helpers.fill, Helpers.buttonContainer, Helpers.fullWidth, Metrics.bottomMargin, Helpers.column]}>
            {this.state.UserList ? (
              <View style={[Helpers.fill, Helpers.borderContainer, Metrics.smallHorizontalPadding, Metrics.smallVerticalPadding, {maxWidth:800, marginTop:10}]}>
                <SafeAreaView style={styles.container}>
                  <FlatList
                    data={Object.keys(this.state.UserList).filter(e => e !== '__jsogObjectId')}
                    renderItem={({item, index})=>{
                      var User = this.state.UserList[item];
                      return(
                        <View style={[Helpers.fill, Helpers.rowCenter, Helpers.fullWidth, {height:35}]}>
                          <CustomListItem Title={User.name} itemStyle={[{minWidth: 190, height:35,}]} onPress={() => {
                              //this.props.setData(item, Section, Pen)
                              //NavigationService.navigate("DataDisplay")
                            }}/>

                          <CustomListItem itemStyle={[{width:70, height:35, justifyContent:"center"}]} Title={""} onPress={() => {
                              this.setState({
                                User: User.name
                              })
                            }}/>

                          <CustomListItem itemStyle={[{width:90, height:35}]} Title={User.state} onPress={() => {
                              this.setState({
                                User: User.name
                              })
                            }}/>
                        </View>
                      )
                    }}
                    keyExtractor={(item, index) => index.toString()}
                  />
                </SafeAreaView>
              </View>
            ):(<></>)} 
            <Input
              label={"User-tag:"}
              containerStyle={[{borderWidth:1, marginTop:10}]}
              style={[ApplicationStyles.input, ]}
              onChangeText={text => {
                this.setState({
                  User: text
                })
              }}
              value={
                this.state.User
              }
            />
          </View>
        </View>
        {(IsLoading || this.props.firebase.viewIsLoading) &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const styles = StyleSheet.create({
  circle: {
    width: 125,
    height: 125,
    borderRadius: 125/2,
    marginTop: Metrics.tiny,
    marginLeft: Metrics.tiny,
  },
});

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    getUsers: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.USERS, {UserId:""})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(UserListBoard);