import React, { useEffect, Component } from 'react'
import { Text, View, ActivityIndicator, Picker } from 'react-native'
import { Input } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject } from '../../../Stores/Firebase/Selectors'

import { Button, Datetime_Picker } from '../../../Components'
import Style from '../CloudScreenStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

class Weighing extends Component {
  constructor(props){
    super(props);

    const {Sections, Section, Pens, Pen, quantity} = this.setData(this.props)
    this.state = {
      Sections: Sections,
      Section: Section,

      Pens: Pens,
      Pen: Pen,
      weight: "",
      quantity: quantity,
      date: new Date().getTime()
    }
  }

  getData() {
    const localDataStore = this.props.firebase.localDataStore;
    if (!localDataStore.sections) localDataStore.sections = {}
    this.props.getSections(localDataStore.sections.update_time)
  }

  setData(Props) {
    //Set Sections
    const localDataStore = Props.firebase.localDataStore;
    var Sections = false;
    if (localDataStore && localDataStore.sections && localDataStore.sections !== Sections) {
      Sections = sortObject(localDataStore.sections)
    }

    //Set Section
    const CurrentData = Props.firebase.currentData;
    var Section = Sections? Object.keys(Sections)[0] : ""
    if (CurrentData && CurrentData.data && CurrentData.data.section && Sections[CurrentData.data.section]) {
      if (CurrentData.data.section != Section) {
        Section = CurrentData.data.section
      }
    }

    //Pens & Pen
    var Pens = Sections? sortObject(Sections[Section].pens): {}
    var Pen = Sections? Object.keys(Pens)[0]: ""
    if (CurrentData && CurrentData.penId && Pens[CurrentData.penId] && CurrentData.penId != Pen){
      Pen = CurrentData.penId
    }
    var quantity = 1
    if (localDataStore && localDataStore.pens) {
      var _Pen = localDataStore.pens[Pen]
      if (_Pen && _Pen.datas && _Pen.datas.count) quantity = _Pen.datas.count
    }

    if (!Sections) Sections = {}
    return {Sections, Section, Pens, Pen, quantity}
  }

  checkData(props) {
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        if (this.props.firebase.localDataStore.sections && JSON.stringify(this.state.Sections)!==JSON.stringify(sortObject(this.props.firebase.localDataStore.sections))) {

          const {Sections, Section, Pens, Pen, quantity} = this.setData(this.props)

          this.setState({
            Sections: Sections,
            Section: Section,
            Pens: Pens,
            Pen: Pen,

            quantity: quantity,
            weight: "",
          })
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ErrorMessage = this.props.firebase.viewError

    const localDataStore = this.props.firebase.localDataStore

    return(
      <View
      style={[
        Helpers.mainContainer,
      ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
          {ErrorMessage ? (
            <Text style={ApplicationStyles.error}>{ErrorMessage}</Text>
          ):(<></>)}
          <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
            <View style={[Helpers.buttonContainer, Helpers.midWidth, Metrics.bottomMargin] }>
              <Button Text="Weighing" buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
                onPress={() => {} }/>
            </View>
            
            <View style={[Helpers.borderContainer, Helpers.fullWidth, {marginBottom:20}] }>
              <Picker
                mode="dropdown"
                selectedValue={this.state.Section}
                style={{ height: 50, width: 150 }}
                onValueChange={(itemValue, itemIndex) => {
                  if (this.state.Sections[itemValue]){
                    const Pens = sortObject(this.state.Sections[itemValue].pens)
                    var quantity = 1
                    if (localDataStore && localDataStore.pens) {
                      var Pen = localDataStore.pens[Object.keys(Pens)[0]]
                      if (Pen && Pen.datas && Pen.datas.count) quantity = Pen.datas.count
                    }
                    this.setState({
                      Section: itemValue,
                      Pens: Pens,
                      Pen: Object.keys(Pens)[0],
                      quantity: quantity,
                    })
                  }
                }}
              >
                {Object.keys(this.state.Sections).map((Section, i) => {
                    return (<Picker.Item label={Section} value={Section} key={i}/>)
                })}
              </Picker>

              <Picker
                mode="dropdown"
                selectedValue={this.state.Pen}
                style={{ height: 50, width: 150 }}
                onValueChange={(itemValue, itemIndex) => {
                  var quantity = 1
                  if (localDataStore && localDataStore.pens) {
                    var Pen = localDataStore.pens[itemValue]
                    if (Pen && Pen.datas && Pen.datas.count) quantity = Pen.datas.count
                  }
                  this.setState({
                    quantity: quantity,
                    Pen: itemValue
                  })
                }}
              >
                {Object.keys(this.state.Pens).map((Pen, i) => {
                    return (<Picker.Item label={Pen} value={Pen} key={i}/>)
                })}
              </Picker>

              <View style={[Helpers.fullWidth, {borderWidth:0 ,height: 65, marginTop:10, paddingHorizontal:5, paddingTop:0, borderRadius:0, alignItems:"center"}]}>
                <View style={[Helpers.fullWidth, {alignItems:"flex-start"}]}>
                  <Text style={[ApplicationStyles.text, {marginHorizontal:5, marginTop:2, color:Colors.input, fontSize: 15, fontWeight: "bold"}]}>Weight date:</Text>
                </View>
                <View style={[Helpers.fullWidth, {alignItems:'flex-start'}]}>
                  <Datetime_Picker Selected={this.state.date}
                      onValueChange={(date)=>{
                        this.setState({
                          date: date.getTime()
                        })
                    }}/>
                </View>
              </View>
              
              <Input
                label="Animal quantity"
                style={{ ...Style.input}}
                onChangeText={text => {
                  if (!isNaN(text)){
                    this.setState({
                      quantity: text
                    })
                  }
                }}
                value={this.state.quantity}
              />
              <Input
                label="Total Weight"
                style={{ ...Style.input}}
                onChangeText={text => {
                  this.setState({
                    weight: text
                  })
                }}
                value={this.state.weight}
              />
            </View>

            <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
              <Button Text="Submit"
                onPress={() => {
                  if (!this.state.Section){
                    this.props.setViewError("A section is needed")
                  } else if (!this.state.Pen){
                    this.props.setViewError("A pen is needed")
                  } else if (isNaN(this.state.quantity) || this.state.quantity === "" || this.state.quantity <= 0){
                    this.props.setViewError("Quantity have to be a number bigger than 0")
                  } else if (isNaN(this.state.weight) || this.state.weight === "") {
                    this.props.setViewError("Weight have to be a number")
                  }
                  else {
                    this.props.setViewLoading(true)
                    this.props.setViewError(null)
                    this.props.newWeight(this.state.Pen, parseFloat(this.state.weight), this.state.date, parseFloat(this.state.quantity))
                    this.setState({
                      weight: "",
                      date: new Date().getTime()
                    })
                  }
                } }/>
            </View>
          </View>
        </View>
        {(IsLoading || this.props.firebase.viewIsLoading) &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    getSections: (DataTime) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.SECTIONS, {DataTime: DataTime})),
    newWeight: (penId, weight, date, quantity) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.WEIGHING, {PenId: penId, Weight:weight, Quantity:quantity, Date:date})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Weighing);
//export default Weighing;