import React, { Component } from 'react'
import { Platform, Text, Image, View, ActivityIndicator } from 'react-native'
import { Input } from 'react-native-elements'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import {Actions, DataRefs, UserStates} from '../../../Stores/Firebase/Selectors'

import { Loading } from '../../../Components'
import Style from './AuthStyle'
import { ApplicationStyles, Helpers, Metrics, Images } from '../../../Theme'

let auth;
if (Platform.OS === 'ios' || Platform.OS === 'android') {
  //#region App version imports
  auth = require('@react-native-firebase/auth').default;
  //#endregion
} else {
  //#region Web Version imports
  var firebase = require('firebase/app').default;
  /*require('firebase/auth').default;

  auth = firebase.auth;*/
  //#endregion
}

const PanelsSorter = (userType) => {
  switch(userType) {
    case UserStates.ADMIN:
      return "AdminPanel"
    case UserStates.OWNER:
      return "ConsysPanel"
    case UserStates.FREE:
      return "UserPanel"
    case UserStates.STARTER:
      return "CreateUserData"
    default:
      return "LoginUser"
  }
};

class UserLoadingScreen extends Component {
  constructor(props){
    super(props);
    this.state = {
      ISLOADPAGE: true,
      FocusSubscription: null,
    }
  }

  getData() {
  }

  checkData(props) {
    const firebaseData = this.props.firebase;
    if (!firebaseData) {
      this.props.setFirebaseError("Internal rootdata Error")
      NavigationService.navigate('MainStack')
      return;
    }
    
    var page = "LoginUser";
    const firebaseUser = firebaseData.firebaseUser
    if (firebaseUser && Object.keys(firebaseUser).length > 1) {
      if (firebaseUser.state === UserStates.STARTER && !firebaseUser.emailVerified) {
        var User = auth().currentUser;
        if(!User.emailVerified) {
          page = "VerifyScreen";
        }else {
          page = PanelsSorter(firebaseUser.state)
        }
      } else {
        page = PanelsSorter(firebaseUser.state)
      }
    }

    NavigationService.navigate(page)
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      if (this.props.firebase.firebaseUser != prevProps.firebase.firebaseUser) { //Only checking user changes
        var now = this.props.firebase.firebaseUser
        var prev = prevProps.firebase.firebaseUser
        if (!now || now.uid !== prev.uid || (now.state !== prev.state && prev.state !== UserStates.STARTER) || now.emailVerified != prev.emailVerified) {
          console.log("UserLoading")
          this.checkData(this.props);
        }
      }
    }
  }

  componentDidMount() {
    this.props.setFirebaseError(null)
    this.props.getUser(null)
    this.checkData(this.props);
    this.getData();

    const FocusSubscription = this.props.navigation.addListener(
      'willFocus',
      payload => {
        this.props.setViewLoading(false)
        this.props.setViewError(null)
      }
    );
    this.setState({
      FocusSubscription: FocusSubscription
    })
  }

  componentWillUnmount() {
    if (this.state.FocusSubscription) {
      this.state.FocusSubscription.remove()
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ISLOADPAGE = this.state.ISLOADPAGE;
    
    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        {!ISLOADPAGE ?(
          <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
            <Text style={ApplicationStyles.text}>This page contains an error.</Text>
            <Text style={ApplicationStyles.text}>If you are going back, please press back again.</Text>
          </View>
        ):(
          <View style={[Helpers.fillRowCenter, Helpers.center, Helpers.logoContainer]}>
            <Image style={Helpers.fullSize} source={Images.Hexa_icon} resizeMode={'contain'} />
          </View>
        )}
        {(IsLoading || ISLOADPAGE) &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    clearDataMem: () => dispatch(FirebaseActions.actionStarting(Actions.CLEAR, DataRefs.USERSDATA)),
    getUser: (UserId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.USERSDATA, {UserId: UserId})), //TODO: mabye not get user 
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(UserLoadingScreen);
//export default UserLoadingScreen;
