import React from 'react'
import { createAppContainer } from 'react-navigation'
import { createStackNavigator } from 'react-navigation-stack'
import { TouchableOpacity, View, Text } from 'react-native'
import { Icon } from 'react-native-elements'

import NavigationService from '../Services/NavigationService'

import AdminFrontScreen from '../Containers/Cloud/AdminPanel/FrontScreen'

import AdminUserBoard from '../Containers/Cloud/AdminPanel/UserBoard'
import AdminSystemBoard from '../Containers/Cloud/AdminPanel/SystemBoard'

import UserPanelNavigation from '../Navigators/UserPanelNavigator'

import { ApplicationStyles } from '../Theme'

/**
 * The root screen contains the application's navigation.
 *
 * @see https://reactnavigation.org/docs/en/hello-react-navigation.html#creating-a-stack-navigator
 */
const StackNavigator = createStackNavigator(
  {
    // Create the application routes here (the key is the route name, the value is the target screen)
    // See https://reactnavigation.org/docs/en/stack-navigator.html#routeconfigs
    
    AdminFront: {
      screen: AdminFrontScreen,
      navigationOptions: () => ({
        title: 'Admin Panel',
        headerLeft: () => (
          <View style={{padding:6, paddingLeft: 15}}>
            <Icon type='ionicon' name="md-arrow-back" size={25} color="black" onPress={() => NavigationService.navigate('MainStack')}/>
          </View>
        ),
      }),
      path: 'admin_front',
    },

    UserBoard: {
      screen: AdminUserBoard,
      navigationOptions: () => ({
        title: 'User Board',
      }),
      path: 'admin_user_board',
    },
    SystemBoard: {
      screen: AdminSystemBoard,
      navigationOptions: () => ({
        title: 'System Board',
      }),
      path: 'admin_system_board',
    },

    UserPanel: {
      screen: UserPanelNavigation,
      navigationOptions: () => ({
        title: 'Admin Panel',
        headerLeft: () => (
          <View style={{padding:6, paddingLeft: 15}}>
            <Icon type='ionicon' name="md-arrow-back" size={25} color="black" onPress={() => NavigationService.navigate('SystemBoard')}/>
          </View>
        ),
      }),
      path: 'admin_front',
    },
  },
  {
    initialRouteName: 'AdminFront', 

    // See https://reactnavigation.org/docs/en/stack-navigator.html#stacknavigatorconfig
    headerMode: 'float',
    defaultNavigationOptions:{
      headerTitleAlign: 'center',
      headerTitleStyle:{...ApplicationStyles.navigationHeader},
      headerRight: () => (<View style={{padding:6}}></View>),
    },
  }
)

export default StackNavigator
