class DataDisplay extends Component {
  constructor(props){
    super(props);

    this.state = {
      currentData:{}
    }
  }

  getData() {
    if (this.props.firebase && this.props.firebase.localDataStore) {
      const localDataStore = this.props.firebase.localDataStore
      if (this.props.firebase.currentData && this.props.firebase.currentData.type){
        const CurrentData = this.props.firebase.currentData;
        switch(CurrentData.type){
          case DataRefs.PIGS:
            //this.props.getWeights()
            break;
        }
      }
    }
  }

  checkData(props) {
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        const localDataStore = this.props.firebase.localDataStore
        if (this.props.firebase.currentData && this.props.firebase.currentData.type &&
            JSON.stringify(this.state.currentData)!==JSON.stringify(this.props.firebase.currentData)) {
          
          const CurrentData = this.props.firebase.currentData;

          switch(CurrentData.type){
            case DataRefs.PENS:
              if (localDataStore.pens) {
                var Pens = sortObject(localDataStore.pens)
                if (Pens[CurrentData.penId]){
                  if (JSON.stringify(CurrentData.data)!==JSON.stringify(Pens[CurrentData.penId])){
                    CurrentData.data = Pens[CurrentData.penId]
                  }
                  
                  this.props.setCurrentDataElement(CurrentData)
                }
              }
              this.setState({
                currentData:CurrentData
              })
              break;
            case DataRefs.PIGS:
              this.setState({
                currentData:CurrentData
              })
              break;
            default:
              this.setState({
                currentData:CurrentData
              })
              break;
          }
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading

    const localDataStore = this.props.firebase.localDataStore
    const ErrorMessage = this.props.firebase.viewError
    const CurrentData = this.state.currentData

    var DataView = (<View></View>);
    switch(CurrentData.type){
      case DataRefs.PENS:
        DataView = (<DataDisplay_Pens currentData={this.state.currentData}/>)
        break;
      case DataRefs.PIGS:
        DataView = PigData_View(this, CurrentData, localDataStore) //(<DataDisplay_Pigs currentData={this.state.currentData}/>)
        break;
    }

    return(
      <ScrollView style={[Helpers.fullSize, Helpers.fill]}>
        <View
          style={[
            Helpers.mainContainer,
          ]}
        >
          <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
            {ErrorMessage ? (
              <Text style={ApplicationStyles.error}>{ErrorMessage}</Text>
            ):(<></>)}
              {DataView}
          </View>
          
          {(IsLoading || this.props.firebase.viewIsLoading) &&
            <ActivityIndicator style={Helpers.loadingContainer} size='large' />
          }
        </View>
      </ScrollView>
    )
  }
}

function onPageSwipe(Control, localDataStore, CurrentData, dir) {
  const section = localDataStore.pens[CurrentData.penId].section
  const Pens = sortObject(localDataStore.sections[section].pens)

  if (CurrentData.type === DataRefs.PENS && CurrentData.data && Pens[CurrentData.penId]) {
    var index = Object.keys(Pens).indexOf(CurrentData.penId)

    switch (dir) {
      case true: //forward
        index = index +1
        break;
      case false: //backward
        index = index -1
        break;
    }

    if (index < 0) index = Object.keys(Pens).length - 1
    else if (index >= Object.keys(Pens).length) index = 0

    Control.props.setCurrentData(Object.keys(Pens)[index])
    NavigationService.replace("DataDisplay")
  }
}

