import React, { useEffect, Component } from 'react'
import { Text, View, ActivityIndicator, ScrollView } from 'react-native'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs } from '../../../Stores/Firebase/Selectors'

import { Button, Button_Panel } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Images } from '../../../Theme'

class FrontScreen extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ISLOADPAGE: true,
    }
  }

  getData() {
    this.props.getFarm() //for future control
  }

  checkData(props) {
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);
      if (this.props.firebase.viewError != prevProps.firebase.viewError) {
        this.forceUpdate()
      }
    }
  }

  render() {
    const firebase = this.props.firebase;

    const IsLoading = firebase.firebaseIsLoading
    const firebaseErrorMessage = firebase.firebaseErrorMessage
    const ErrorMessage = this.props.firebase.viewError
    const firebaseUser = firebase.firebaseUser

    return(
      <ScrollView style={[Helpers.fullSize, Helpers.fill]}>
        <View
          style={[
            Helpers.mainContainer,
          ]}
        >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
          {firebaseErrorMessage ? (
            <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
          ):(<></>)}
          {firebaseUser?(
          <View style={ Helpers.textContainer }>
            <Text style={ApplicationStyles.text}>Welcome: {firebaseUser.displayName ? firebaseUser.displayName : firebaseUser.email}</Text>
          </View>):(<></>)}

          <Button_Panel 
            Texts={["Dashboard", "Pens", "Batchs", "Growth curves", "Operations", "AutoPigs", "Feeding", "Pigs"]}
            Images={[Images.DashBoard, Images.Pens, Images.Section, Images.GCurves, Images.System, Images.AutoPig, Images.Feed, Images.Sow ]}
            onPress={(ele) => {
              switch(ele.text){
                case "Dashboard":
                  NavigationService.navigate("UserDashBoard")
                  break;
                case "Pens":
                  NavigationService.navigate("UserPens")
                  break;
                case "Batchs":
                  NavigationService.navigate("UserBatchs")
                  break;
                case "Growth curves":
                  NavigationService.navigate("UserCurves")
                  break;
                case "Operations":
                  NavigationService.navigate("UserOperations")
                  break;
                case "AutoPigs":
                  NavigationService.navigate("AutoPig")
                  //this.props.getAutoPig()
                  break;
                case "Feeding":
                  break;
                case "Pigs":
                  NavigationService.navigate("UserPigs")
                  break;
              }
            }}
          />
          
          {firebaseUser && firebaseUser.state === "admin"?(<></>):(
          <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
            <Button Text="LogOut"
              onPress={() => {
                this.props.logOut()
              }}/>
          </View>
          )}
        </View>
        {(IsLoading || this.props.firebase.viewIsLoading) &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
      </ScrollView>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    getFarm: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.FARMS, {})),

    logOut: () => dispatch(FirebaseActions.actionStarting(Actions.SIGN, DataRefs.USERSDATA)),

    getAutoPig: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.CONSYS, {sysId:2}))
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(FrontScreen);
//export default FrontScreen;
