import React, { useEffect, useState, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Image } from 'react-native'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../Services/NavigationService'

//import DeviceActions from '../../Stores/Device/Actions'
import FirebaseActions from '../../Stores/Firebase/Actions'

import { Button_Panel } from '../../Components'
import Style from './FrontScreenStyle'
import { ApplicationStyles, Helpers, Images } from '../../Theme'

class FrontScreen extends Component {
  constructor(props){
    super(props);

    this.state = {
      ShowMainError: false,
      FocusSubscription: null,
    }
  }

  setData(Props) {
  }

  getData() {
  }

  checkData(props) {
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);
      if (this.props.firebase.viewError != prevProps.firebase.viewError) {
        this.forceUpdate()
      }
    }
  }

  componentDidMount() {
    this.props.setFirebaseError(null)
    this.checkData(this.props);
    this.getData();

    const FocusSubscription = this.props.navigation.addListener(
      'willFocus',
      payload => {
        this.props.setViewLoading(false)
        this.props.setViewError(null)
      }
    );
    this.setState({
      FocusSubscription: FocusSubscription
    })
  }
  componentWillUnmount() {
    if (this.state.FocusSubscription) {
      this.state.FocusSubscription.remove()
    }
  }

  render() {
    const ErrorMessage = this.props.firebase.viewError
    const firebaseErrorMessage = this.props.firebase.firebaseErrorMessage

    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Style.logoContainer]}>
          <Image style={[Helpers.fullSize]} source={Images.Logo} resizeMode={'contain'} />
        </View>

        <View style={[Helpers.contentContainer, Helpers.fullSize]}>
          <View style={[Helpers.textContainer, Helpers.fullWidth]}>
            {this.state.ShowMainError && firebaseErrorMessage ? (
              <Text style={ApplicationStyles.error}>{firebaseErrorMessage}</Text>
            ):(<></>)}
            {ErrorMessage ? (
              <Text style={ApplicationStyles.error}>{ErrorMessage}</Text>
            ):(<></>)}
            <Text style={ApplicationStyles.text}>Welcome to Agrisys App</Text>
            <Text style={ApplicationStyles.text}>Please choose your action</Text>
          </View>

          <Button_Panel 
            Texts={["Connect to Agrisys Cloud", "Connect to Device"]}
            Images={[Images.Haxa_globe, null]}
            onPress={(ele) => {
              switch(ele.index){
                case 0:
                  NavigationService.navigate('AuthStack')
                  break;
                case 1:
                  if (Platform.OS === 'ios' || Platform.OS === 'android') {
                    NavigationService.navigate("DeviceScreen")
                  }
                  break;
              }
              /*<Button Text="Example"
              onPress={() => NavigationService.navigate("ExampleScreen")}/>*/
            }}
          />
        </View>
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(FrontScreen);
//export default FrontScreen;
