//#region Formaters
function format_num(num) {
  if (num < 10){
    num = "0"+num
  }
  return num;
}
export function format_Time(time) {
  var s = new Date(parseInt(time));
  var datetime = format_num(s.getDate()) + "/" + format_num(s.getMonth() +1) + "/" + s.getFullYear() + " " 
                  + format_num(s.getHours()) + ":" + format_num(s.getMinutes()) + ":" + format_num(s.getSeconds());
  return datetime
}

var reA = /[^a-zA-Z]/g;
var reN = /[^0-9]/g;
function sortAlphaNum(a, b) {
  var aA = a.replace(reA, "");
  var bA = b.replace(reA, "");
  if (aA === bA) {
    var aN = parseInt(a.replace(reN, ""), 10);
    var bN = parseInt(b.replace(reN, ""), 10);
    return aN === bN ? 0 : aN > bN ? 1 : -1;
  } else {
    return aA > bA ? 1 : -1;
  }
}

export function sortObject(o) {
  var sorted = {},
  key, a = [];

  for (key in o) {
    if (o.hasOwnProperty(key) && key !== "__jsogObjectId" && key !== "update_time") {
      a.push(key);
    }
  }
  a.sort(sortAlphaNum);

  for (key = 0; key < a.length; key++) {
    sorted[a[key]] = o[a[key]];
  }
  return sorted;
}

export function DataStringCheck(data, Check_num = true) {
  if (!data || data === ""){
    return false
  }
  if (!typeof(data) === 'string'){
    return false
  }else {
    if (Check_num) {
      var first = data[0]
      var numbers = '0123456789';
      for(var i = 0; i < numbers.length;i++){
        if(first === numbers[i]){
          return false
        }
      }
    }

    var specialChars = '/[`!@#$%^&*()+\-=\[\]{};\':"\\|,.<>\/?~]/¨´';
    for(var i = 0; i < specialChars.length;i++){
      if(data.indexOf(specialChars[i]) > -1){
        return false
      }
    }
  }
  return true
}
