//#region Data gets
export function GetWeight_FromPen(PenId, WeighingsStore = null, AfterTime = null, BeforeTime = null) {
  //PenId: Parrent Get ellement (ID),
  //WeighingsStore: LocalDataStore.weighings,
  //AfterTime: Only data after specefied Time,
  //BeforeTime: Only data before specefied Time
  var Weighing = null;

  if (PenId, WeighingsStore){
    for (const _weighing in WeighingsStore[PenId]) {
      var weighing = WeighingsStore[PenId][_weighing]
      if (weighing && (!Weighing || Weighing.time < weighing.time)) {
        if ((!AfterTime || weighing.time >= AfterTime) &&
          (!BeforeTime || weighing.time <= BeforeTime)) {
          Weighing = weighing
        }
      }
    }
  }
  return Weighing;
}

export function GetBatch_FromPen(localDataStore, PenId) {
  if (localDataStore.batchs) {
    //Get batch form pen
    for (const _batch in localDataStore.batchs) {
      var batch = localDataStore.batchs[_batch]
      if (batch.hasOwnProperty("pens") && batch.pens[PenId] != null) {
        return localDataStore.batchs[_batch]
      }
    }
  }
  return null;
}

export function GetStats_FromPen(PenId, PenData, localDataStore, StartDate, EndDate, PenWeighings = null){
  var Animals = 0;
  var PenWeight = 0
  var PenGrowth = 0
  var AveragePenGrowth = 0;

  if (PenData.datas && PenData.datas.count || PenData.count) {
    if (PenData.count) Animals += PenData.count
    else Animals += PenData.datas.count
  }
  
  if (localDataStore.weighings && localDataStore.weighings[PenId]) {
    var GrowthCount = 0

    var lastDate = 0
    var LastWeight = undefined;
    var LastWeightDay = undefined;
    var FirstWeight = undefined;

    if (!PenWeighings) PenWeighings = localDataStore.weighings[PenId]
    if (PenWeighings) {
      Object.keys(PenWeighings).forEach((weight) => {
        var Weight = PenWeighings[weight]
        if (Weight.time >= StartDate && Weight.time <= EndDate && Weight.time > lastDate) {
          PenWeight = Weight.weight
          if (Weight.count) PenWeight = Weight.weight / Weight.count;

          if (LastWeight !== undefined) {
            PenGrowth = PenWeight - LastWeight
            GrowthCount++;
          }

          lastDate = Weight.time
          LastWeight = PenWeight
          LastWeightDay = Weight.time
          if (!FirstWeight) FirstWeight = Weight.time;
        }
      })
    }

    var now = new Date().getTime()
    var isVirtual = false;
    if (StartDate && EndDate) {
      var Batch = GetBatch_FromPen(localDataStore,PenId)
      var TempGraph = GetCurve_FromBatch(Batch, localDataStore.graphs)
      var BatchEndDate = EndDate;
      var Predict = CurvePredict(localDataStore, PenId, Batch, TempGraph)
      if (Predict != null && Predict.Now && now > new Date(lastDate).getDate()) {
        PenWeight = Predict.Now
        if (LastWeight !== undefined) {
          PenGrowth += PenWeight - LastWeight
          GrowthCount++;
        }
        var _date = new Date(Predict.lastWeighing.time + Predict.Addition)
        _date.setDate(_date.getDate() + Predict.days)
        LastWeightDay = _date.getTime()
        isVirtual = true
      }
      if (Predict && Predict.lastWeighing && Predict.days) {
        var tempEnd = new Date(EndDate).getTime()
        var date = new Date(Predict.lastWeighing.time)
        date.setDate(date.getDate() + Predict.days)
        tempEnd = date.getTime()
        if (Predict.Addition) {
          tempEnd += Predict.Addition
        }
        if (EndDate === BatchEndDate || EndDate < tempEnd) {
          EndDate = tempEnd;
        }
      }
    }
    if (GrowthCount > 0) {
      var Growth = (PenGrowth / GrowthCount)
      var days = (LastWeightDay - FirstWeight) / 86400000
      Growth = Growth / days
      AveragePenGrowth += Growth //AverageGrowth for specefic pen
    }
  }
  return { Animals, PenWeight, AveragePenGrowth, EndDate, isVirtual }
}

export function GetStats_FromBatch(Batch, localDataStore, Closed=true) {
  var isVirtual = false;
  var PenCount = 0;
  var Animals = 0
  var CollectedWeight = 0;
  var CollectedGrowth = 0;
  
  var isClosed = (Batch.state && Batch.state === "Closed")?true:false

  var TempGraph = GetCurve_FromBatch(Batch, localDataStore.graphs)
  if (!TempGraph) return null;
  var TargetWeight = (Batch && Batch.datas && Batch.datas.target)?Batch.datas.target:TempGraph.data[TempGraph.data.length -1]
  var EndDate = GetBatchEnd(Batch, TempGraph)
  var StartDate = Batch.time;

  var PenEnds = 0
  if (Batch && Batch.pens && localDataStore.pens) {
    Object.keys(Batch.pens).reverse().filter(e => (e !== '__jsogObjectId')).forEach(Batch_Pen => {
      if (localDataStore && localDataStore.pens && localDataStore.pens[Batch_Pen] && localDataStore.weighings && localDataStore.weighings[Batch_Pen] || isClosed) {
        var Pen = isClosed?Batch.pens[Batch_Pen]:localDataStore.pens[Batch_Pen]
        PenCount++
        
        var PenWeighings = isClosed?Batch.closed.weighings[Batch_Pen]:localDataStore.weighings[Batch_Pen]
        if (PenWeighings) {
          var PenStats = GetStats_FromPen(Batch_Pen, Pen, localDataStore, StartDate, EndDate, PenWeighings)
          Animals += PenStats.Animals;
          var PenWeight = PenStats.PenWeight
          if (PenWeight === 0) {
            PenCount--
          } else {
            CollectedWeight += PenWeight
            CollectedGrowth += PenStats.AveragePenGrowth
            if (PenEnds < PenStats.EndDate) PenEnds = PenStats.EndDate
            if (!isVirtual) isVirtual = PenStats.isVirtual
          }
        }
      }
    });
  }

  if (PenEnds > 0) EndDate = PenEnds
  var AverageWeight = CollectedWeight / PenCount
  AverageWeight = parseFloat(AverageWeight).toFixed(2)
  var AverageGrowth = CollectedGrowth / PenCount
  AverageGrowth = parseFloat(AverageGrowth).toFixed(2)

  return {StartDate, EndDate, TargetWeight, PenCount, Animals, AverageWeight, AverageGrowth, isClosed, isVirtual}
}

export function GetCurve_FromBatch(Batch, GraphsStore = null, CurrentData = null) {
  //Batch: Parrent Get ellement,
  //GraphsStore: LocalDataStore.graphs,
  //CurrentData: Data state get,
  if (Batch) {
    var Garph = null
    console.log(Batch)
    if (Batch.gcurve && Batch.state && Batch.state === DataStates.CLOSED) {
      Garph = Batch.gcurve;
    } else if (GraphsStore) {
      if (CurrentData && (typeof CurrentData === 'string' || CurrentData.curveId)) {
        var Id = (typeof CurrentData === 'string')? CurrentData: CurrentData.curveId
        Garph = GraphsStore[Id]
      }
      if (Batch.gcurve) {
        Garph = GraphsStore[Batch.gcurve];
      }
    }
    if (Garph) {
      var DataPoints = []
      var _index = 0;
      Object.keys(Garph.data).filter(e => e !== '__jsogObjectId').map((index) => {
        DataPoints[index] = Garph.data[index]
        for(var i = index-1; i > _index;i--) {
          DataPoints[i] = null
        }
        _index = index
      })
      Garph.data = DataPoints
      return Garph;
    }
  }
  return null;
}

export function GetBatchEnd(Batch, TempCurve) {
  if (Batch && Batch.time && TempCurve) {
    var currentTime = new Date(Batch.time) //Does not allow weight's after Batch end
    for (var i = 0; i < TempCurve.data.length; i++) {
      if (i < TempCurve.data.length -1) {
        currentTime = new Date(AccumulateTime(currentTime, TempCurve.periode))
      }
    }
    return currentTime.getTime();
  }
  return null
}
//#endregion

//#region Curve Functions
export function AccumulateTime(Time, PeriodeLength) {
  var NewTime = new Date(Time.getTime())
  switch(PeriodeLength) {
    case "Days":
    //86.400.000 ms
    NewTime.setDate(NewTime.getDate() + 1)
    break;
    case "Month":
    NewTime.setMonth(NewTime.getMonth() + 1)
    break;
    case "Weeks":
    default:
    NewTime.setDate(NewTime.getDate() + 7)
    break;
  }
  return NewTime;
}

//Get Today-point expected data and virtual simulation For PenId with Batch / or given
export function get_DataSpot_FromData(localDataStore, PenId, Batch, Closed = false) {
  if (localDataStore.weighings && localDataStore.weighings[PenId]) {
    var Weighing = null;

    //get newest weighing
    for (const _weighing in localDataStore.weighings[PenId]) {
      var weighing = localDataStore.weighings[PenId][_weighing]
      if (weighing && (!Weighing || Weighing.time < weighing.time)) { 
        Weighing = weighing
      }
    }

    if (Weighing) {
      var weight = Weighing.weight
      weight = weight / Weighing.count
      if (weight && !isNaN(weight)) {
        if (!Batch) {
          //Get batch form pen
          for (const _batch in localDataStore.batchs) {
            var batch = localDataStore.batchs[_batch]
            if (batch.hasOwnProperty("pens") && batch.pens[PenId] != null) {
              Batch = localDataStore.batchs[_batch]
              //break; //diffrenc from web ??? (needed in mobile..)
            }
          }
        }

        if (Batch && Batch.time && Batch.datas 
          && localDataStore.graphs && localDataStore.graphs[Batch.gcurve]) {
          var Curve = localDataStore.graphs[Batch.gcurve]
          var WeighingTime = Weighing.time

          if (Closed || (WeighingTime > Batch.time && (!Batch.state || Batch.state !== "Closed"))) {
            var StartTime = Batch.time
            var EndTime = Batch.time
            var index = 0;
            var currentTime = new Date(StartTime)
            for (index = 0; index < Curve.data.length-1; index++) {
              currentTime = new Date(AccumulateTime(currentTime, Curve.periode))
              EndTime = currentTime.getTime()
              if (EndTime >= WeighingTime) {
                break;
              } else {
                StartTime = EndTime
              }
            }
            //var EndDate = EndDate = currentTime.getTime();
            
            var Keys = Object.keys(Curve.data);
            var DataSpot = get_DataSpot([StartTime, EndTime], [Curve.data[Keys[index]], Curve.data[index +1]], new Date().getTime(), weight, WeighingTime)
            DataSpot.Batch = Batch
            return DataSpot
          }
        }
      }
    }
  }
  return null
}

//Get point expected data and virtual simulation from data.
//ExValue = Expected value if slope continues, for spots given (or inbetween).
//VirData = Virtual calculation for Data progress to DataTime, if slope continues.
export function get_DataSpot(MinMax_Time, MinMax_Data, FindTime, Data, DataTime) {
  var ExValue = null
  var VirData = null
  var ExTime = null
  if (FindTime) {
    var slope = GetSlope_Between(MinMax_Time, MinMax_Data)
    var FindX = FindTime- MinMax_Time[0]
    // ExValue = (FindX * Slope) + MinY             // Setting FindTime, due b then = Min data.. (b is y = 0)
    ExValue = (FindX * slope) + MinMax_Data[0]          // ( X * a ) + b = Expeted data for spot on CalcTime
    
    if (Data && DataTime) {
      var Findx2 = DataTime- MinMax_Time[0]
      var ExData = (Findx2 * slope) + MinMax_Data[0]    //Expeted data for given Time 2
      
      var DataDiff = ExData - Data;
      VirData = ExValue + DataDiff;

      ExTime = (Data - MinMax_Data[0]) / slope          // (y-b)/a = Expeted time for data
      ExTime = MinMax_Time[0] + ExTime
      ExTime = Math.floor(ExTime)
    }
  }
  return {ExValue, VirData, ExTime}
}

//Get Slope Between two points
function GetSlope_Between(MinMax_Time, MinMax_Data){
  var minX = MinMax_Time[0]
  var maxX = MinMax_Time[1]
  var minY = MinMax_Data[0]
  var maxY = MinMax_Data[1]

  //Slope = DeltaY / DeltaX
  var slope = (maxY - minY) / (maxX - minX)
  if (minY > minX) slope = -slope;
  return slope
}

export function CurvePredict(localDataStore, PenId, Batch, TempCurve) {
  if (localDataStore) {
    /* Get data */
    if (!PenId && Batch && Batch.pens){
      PenId = Object.keys(Batch.pens).filter(e => (e !== '__jsogObjectId'))[0]
    }
    if (!Batch) {
      Batch = GetBatch_FromPen(localDataStore, PenId)
    }
    if (Batch && Batch.time) {
      if (!TempCurve) {
          TempCurve = GetCurve_FromBatch(Batch, localDataStore.graphs)
      }
      if (TempCurve && TempCurve.data) {
        var BatchEndDate = GetBatchEnd(Batch, TempCurve)
        var Weighing = GetWeight_FromPen(PenId, localDataStore.weighings, Batch.time, BatchEndDate);
        if (Weighing && Weighing.weight) {
          var weight = Weighing.weight
          if (Weighing.count) weight = weight / Weighing.count
          var Weighing2 = GetWeight_FromPen(PenId, localDataStore.weighings, Batch.time, Weighing.time -1);
          var weight2 = Weighing2?Weighing2.weight:0
          if (Weighing2 && Weighing2.count) weight2 = weight2 / Weighing2.count

          var StartIndex = "none";
          var PredictCurve = { periode: TempCurve.periode, data: [] }
          var now = new Date(Date.now())
          now = new Date(now.getFullYear(), now.getMonth(), now.getDate()).getTime()
          var CurrentTime = new Date(Batch.time)
          var Temp_CurrentIndex = 0;

          /* find template fit */
          for (var index = 0; index < TempCurve.data.length; index++) {
            if (CurrentTime.getTime() <= now) {
              CurrentTime = new Date(AccumulateTime(CurrentTime, TempCurve.periode))
              Temp_CurrentIndex++;
            }

            if (weight <= TempCurve.data[index]) {
              if (StartIndex === "none") {
                StartIndex = index
                if (index === 0) {
                  var slope = GetSlope_Between([Weighing.time,AccumulateTime(new Date(Weighing.time), TempCurve.periode)],[TempCurve.data[0], TempCurve.data[1]])
                  var time = (TempCurve.data[0]- weight)/slope
                  Weighing2 = Weighing
                  weight2 = weight
                  Weighing = {weight: TempCurve.data[0], time: Math.floor(Weighing.time + time)}
                  weight = TempCurve.data[0]
                }
              }
              PredictCurve.data.push(TempCurve.data[index])
            }
          }

          if (weight < weight2) {
            StartIndex = StartIndex -1
            PredictCurve.data.unshift(TempCurve.data[StartIndex])
          }

          var PredictStart = new Date(Batch.time)
          for (let index = 0; index < StartIndex; index++) {
            PredictStart = new Date(AccumulateTime(PredictStart, TempCurve.periode))
          }

          PredictCurve.Now = weight
          PredictCurve.TempEx = TempCurve.data[Temp_CurrentIndex]
          if (StartIndex !== "none") {
            var DataSpot = null
            if (Weighing2) {
              DataSpot = get_DataSpot([Weighing2.time, Weighing.time], [weight2, weight], Weighing.time, TempCurve.data[StartIndex], PredictStart.getTime())
            }
            if (DataSpot && DataSpot.ExTime) {
              PredictCurve.FirstSpot = DataSpot.ExTime 
              PredictCurve.Addition = DataSpot.ExTime - Weighing.time
            } else {
              PredictCurve.FirstSpot = new Date(Weighing.time + 86400000)
              PredictCurve.Addition = 86400000
            }

            if (BatchEndDate) {
              var now = new Date(Date.now())
              /*if (BatchEndDate <= now) {
                now = new Date(BatchEndDate)
              }*/

              now = new Date(now.getFullYear(), now.getMonth(), now.getDate()).getTime()
              var Temp_CurrentTime
              CurrentTime = new Date(Weighing.time)
              var CurrentIndex = 0;
              for (var index = 0; index < PredictCurve.data.length; index++) {
                if (CurrentTime.getTime() < now) {
                  Temp_CurrentTime = CurrentTime
                  CurrentTime = new Date(AccumulateTime(CurrentTime, TempCurve.periode))
                  CurrentIndex ++;
                }
              }
              var WeightNow
              if (CurrentIndex >= PredictCurve.data.length) {
                DataSpot = get_DataSpot([Temp_CurrentTime, CurrentTime], [PredictCurve.data[PredictCurve.data.length-2], PredictCurve.data[PredictCurve.data.length-1]], Date.now())
                WeightNow = DataSpot.ExValue
              } else {
                CurrentIndex--
                WeightNow = PredictCurve.data[CurrentIndex];
              }
              if (WeightNow) PredictCurve.Now = WeightNow
              else PredictCurve.Now = weight
            }
          }

          var days = 0;
          if (TempCurve.periode === "Days") days = PredictCurve.data.length
          else if (TempCurve.periode === "Weeks") days = (PredictCurve.data.length) * 7
          PredictCurve.days = days
          PredictCurve.lastWeighing = Weighing

          Batch.datas.target = (Batch.datas.target)?Batch.datas.target:TempCurve.data[TempCurve.data.length-1]
          PredictCurve.Batch = Batch;
          return PredictCurve;
        }
      }
    }
  }
  return null;
}
