import { createActions } from 'reduxsauce'

import BleCreators, {BleTypes} from './BLE/Actions'
import BlueToothCreators, {BluetoothTypes} from './BlueTooth/Actions'

/**
 * We use reduxsauce's `createActions()` helper to easily create redux actions.
 *
 * Keys are action names and values are the list of parameters for the given action.
 *
 * Action names are turned to SNAKE_CASE into the `Types` variable. This can be used
 * to listen to actions:
 *
 * - to trigger reducers to update the state, for example in App/Stores/Example/Reducers.js
 * - to trigger sagas, for example in App/Sagas/index.js
 *
 * Actions can be dispatched:
 *
 * - in React components using `dispatch(...)`, for example in App/App.js
 * - in sagas using `yield put(...)`, for example in App/Sagas/ExampleSaga.js
 *
 * @see https://github.com/infinitered/reduxsauce#createactions
 */
const { Types, Creators } = createActions({
  //Device Saga
  deviceStartup: null,
  setDeviceError: ['errorMessage'], // Device-saga error occurred

  scannerStateUpdated: ['scannerState'],

  addDevice: ['device'],
  removeDevice: ['device'],
  clearDevices: null,

  connectedDeviceUpdated: ['connectedDevice'], 

  connectionStateUpdated: ['connectionState'],
  connectionMethodeUpdated: ['connectionMethode'],

  setDeviceData: ['data'],
  updateDeviceData: ['data'],

  connectDevice: ['device'],
  disconnectDevice: ['methode'],
  readDeviceData: null,

  fetchDevice: null, // Fetch device informations
})

export const DeviceTypes = {...Types, ...BleTypes, ...BluetoothTypes}
export default {...Creators, ...BleCreators, ...BlueToothCreators}