import React, { useEffect } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, FlatList, SafeAreaView, ScrollView } from 'react-native'
import { useDispatch, useSelector } from 'react-redux'
import NavigationService from '../../Services/NavigationService'

import DeviceActions from '../../Stores/Device/Actions'
import { ScannerState, ConnectionMethode } from '../../Stores/Device/Selectors'

import { Button, CustomListItem } from '../../Components'
import Style from './DeviceScreenStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../Theme'

import { Service, } from 'react-native-ble-plx'

const BLEButton = (dispatch, Active = false) => {
  if (!Active) {
    return (
      <Button buttonStyle={ApplicationStyles.button} Text="Search BLE devices"
              onPress={() => dispatch(DeviceActions.startBleScan())}/>
    )
  } else {
    return (
      <Button buttonStyle={ApplicationStyles.button} Text="Stop BLE Search" 
              onPress={() => dispatch(DeviceActions.stopBleScan())}/>
    )
  }
} 

const ConnectedDevice_View = (connectedDevice, DeviceData) => {
  const dispatch = useDispatch()
  return (
    <View>
      <CustomListItem Title={connectedDevice.localName} Subtitle={connectedDevice.id} itemStyle={{marginBottom: 10}} onPress={() => dispatch(DeviceActions.disconnectDevice())}/>
      { DeviceData && Object.keys(DeviceData).length > 1 ? (
        <View style={Style.deviceDataContainer}>
          {/* if battery is presented */}
          {DeviceData && DeviceData.Battery ? (
            <View style={Style.textContainer}>
              <Text>Device battery level</Text>
              <Text>{DeviceData.Battery + "%"}</Text>
            </View>
          ):(<></>)}

          {/* App scanners data */}
          {DeviceData.tagid ? (
          <View style={Style.textContainer}>
            <Text>TAG Id:</Text>
            <Text>{DeviceData.tagid}</Text>
          </View>
          ):(<></>)}
          {DeviceData.FatLayers && DeviceData.FatLayers > 0 ? (
            <View style={Style.textContainer}>
              <Text>Layers received: {DeviceData.FatLayers}</Text>
              <Text>Layer 1: {DeviceData.FatL1}</Text>
              <Text>Layer 2: {DeviceData.FatL2}</Text>
              <Text>Layer 3: {DeviceData.FatL3}</Text>
            </View>
          ):(<></>)}
          {DeviceData.Unknown ? (
            <View style={Style.textContainer}>
              <Text>unknon Id:</Text>
              <Text>{DeviceData.Unknown}</Text>
            </View>
          ):(<></>)}
        </View>
      ):(<></>)
      }
    </View>
  )
}

const DeviceScreen = () => {
  const dispatch = useDispatch()

  const deviceErrorMessage = useSelector((state) => state.device.deviceErrorMessage)

  const connectedDevice = useSelector((state) => state.device.connectedDevice)
  const DeviceData = useSelector((state) => state.device.deviceData)

  const scannerState = useSelector((state) => state.device.scannerState)
  const connectionMethode = useSelector((state) => state.device.connectionMethode)
  const Devices = useSelector((state) => state.device.DeviceList)

  useEffect(() => {
    //dispatch(DeviceActions.fetchDevice())
  }, [])

  return (
    <View
      style={[
        Helpers.fill,
        Helpers.rowMain,
        Metrics.mediumHorizontalMargin,
        Metrics.mediumVerticalMargin,
      ]}
    >
      <View>
        <View style={Style.textContainer}>
          {deviceErrorMessage ? (
            <View>
              <Text>There was a error while retrieving conected device</Text>
              
            </View>
          ) : (
            <View>
              <Text style={Style.text}>Corrently connected:</Text>
              <Text style={Style.text}>{connectedDevice && connectedDevice.name ? (connectedDevice.name):("None") /*mabye use localname*/}</Text>
            </View>
          )}
        </View>
        <View style={Style.buttonContainer}>
          {connectedDevice ? (
            <View>
              <Button buttonStyle={ApplicationStyles.button} Text="Disconnect Device" 
                onPress={() => dispatch(DeviceActions.disconnectDevice(connectionMethode))}/>
              {/*<Button buttonStyle={ApplicationStyles.button} Text="Activate device reading"
                onPress={() => {
                  dispatch(DeviceActions.readDeviceData())
                }}
              />*/}
            </View>
            ): (
            <View>
              {connectionMethode == ConnectionMethode.BLE && scannerState != ScannerState.IDLE ? (
                <Button buttonStyle={ApplicationStyles.button} Text="Stop BLE Search" 
                  onPress={() => dispatch(DeviceActions.stopBleScan())}/>
                  ) : (
                <Button buttonStyle={ApplicationStyles.button} Text="Search BLE devices"
                  onPress={() => dispatch(DeviceActions.startBleScan())}/>
                )
              }
              
              {Platform.OS === 'ios' ? (<></>):(
                <View>
                  {connectionMethode == ConnectionMethode.BLUETOOTH && scannerState != ScannerState.IDLE ? (
                    <Button buttonStyle={ApplicationStyles.button} Text="Stop Bluetooth Search" 
                      onPress={() => dispatch(DeviceActions.stopBluetoothScan())}/>
                      ) : (
                    <Button buttonStyle={ApplicationStyles.button} Text="Search Bluetooth devices" 
                      onPress={() => dispatch(DeviceActions.startBluetoothScan())}/>
                    )
                  }
                  <Button buttonStyle={ApplicationStyles.button} Text="Make Discoverable (Blu 30sec)" 
                      onPress={() => dispatch(DeviceActions.bluetoothScanAble())}/>

                  <Button buttonStyle={ApplicationStyles.button} Text="OnDevice-serial" 
                      onPress={() => {}}/>
                </View>
              )}
            </View>
          )}
        </View>
        <View style={Style.deviceContainer}>
          <Text style={Style.text2}>Device List:</Text>
            { connectedDevice ? 
              ConnectedDevice_View(connectedDevice, DeviceData)
              : (
                <ScrollView style={{paddingRight: 5}}>
                  {Devices.map((Dev, i) => (
                    <CustomListItem key={i} Title={Dev.localName} Subtitle={Dev.id} onPress={() => dispatch(DeviceActions.connectDevice(Dev))}/>
                  ))}
                </ScrollView> 
              )
            }
        </View>
      </View>
    </View>
  );
}

export default DeviceScreen;
