const HTTPS_START = 'https://';
export const Config = {
  API_URL: HTTPS_START+ 'jsonplaceholder.typicode.com/users/',

  CONSYS_CLIENT_ID: 'consysapp',
  CONSYS_CLIENT_SECRET: 'Kjsd832n8DIO32kmsdds3',
  CONSYS_AUTH: '.agrisys.cloud/oauthserver/getaccesstoken/?redirect_uri=urn:ietf:wg:oauth:2.0:oob',
  CONSYS_API: '.agrisys.cloud/consys-api-v1/',
}

export function GetToken_URL(Site_URL, Refresh_Token) {
  var res = HTTPS_START + Site_URL + Config.CONSYS_AUTH
  res = res + '&client_id='+Config.CONSYS_CLIENT_ID+'&client_secret='+Config.CONSYS_CLIENT_SECRET+'&refresh_token=' + Refresh_Token
  return res;
}

export function GetCheckToken_URL(Site_URL) {
  var res = HTTPS_START + Site_URL + Config.CONSYS_API + 'checkaccesstoken'
  return res;
}

export function GetConsumtion_URL(Site_URL) {
  var res = HTTPS_START + Site_URL + Config.CONSYS_API + 'getconsumptiondata?'
  return res;
}

export function GetConsumtionData_URL(Site_URL, Consumptiontype, SystemId, Offset=null, Limit=null, Starttime=null, Stoptime=null, Lastid=null){
  var res = GetConsumtion_URL(Site_URL)
  res = res + GetConsumtionData_Var(Consumptiontype, SystemId, Offset, Limit, Starttime, Stoptime, Lastid)
  return res;
}

export function GetConsumtionData_Var(Consumptiontype, SystemId, Offset=null, Limit=null, Starttime=null, Stoptime=null, Lastid=null){
  var res = 'consumptiontype=' + Consumptiontype + '&systemid=' + SystemId;
  if (Offset) res = res + '&offset=' + Offset;
  if (Limit) res = res + '&limit=' + Limit;
  if (Starttime) res = res + '&starttime=' + Starttime;
  if (Stoptime) res = res + '&stoptime=' + Stoptime;
  if (Lastid) res = res + '&lastid=' + Lastid;
  return res;
}