import {buffers, eventChannel} from 'redux-saga'
import { put, call, fork, take, cancelled, cancel, actionChannel, race, select } from 'redux-saga/effects'
import base64 from 'react-native-base64'
import { Buffer } from 'buffer'

import DeviceActions from '../../../Stores/Device/Actions'
import { deviceService } from '../../../Services/DeviceService'

import BleActions from '../../../Stores/Device/BLE/Actions'

import { ScannerState, ConnectionState, ConnectionMethode } from '../../../Stores/Device/Selectors'

import { BleManager, BleError, Device, State, Service, Characteristic, Descriptor } from 'react-native-ble-plx'

export function* bleStartup(sagaArgs) {
  //Check and setup BLE....
  const permitError = yield call(deviceService.isBlePermitted)
  if (permitError) {
    yield put( DeviceActions.setDeviceError(permitError))
  } else {
    // All below generators are described below...
    yield fork(handleScanning, sagaArgs.BLEManager)
    yield fork(handleBleState, sagaArgs.BLEManager)
    yield fork(handleConnection, sagaArgs.BLEManager)
  }
  
  console.log('BLE Startup : Done');
}

// This generator tracks our BLE state. Based on that we can enable scanning, get rid of devices etc.
// eventChannel allows us to wrap callback based API which can be then conveniently used in sagas.
function* handleBleState(manager: BleManager) {
  console.log("Handle BLE State...")

  const stateChannel = yield eventChannel((emit) => {
    const subscription = manager.onStateChange((state) => {
      emit(state)
    }, true)
    return () => {
      subscription.remove()
    };
  }, buffers.expanding(1))

  try {
    for (;;) {
      const newState = yield take(stateChannel)
      console.log("New BLE state: " + newState)
      yield put(BleActions.bleStateUpdated(newState))
    }
  } finally {
    if (yield cancelled()) {
      stateChannel.close()
    }
  }
}

