function* handleConnection(manager: BleManager) {
  console.log("Handle BLE Connection...")
  
  for (; ;) {
    // Take action
    var { device }: ConnectAction = yield take('CONNECT_DEVICE');

    var connectionMethode: $Keys<typeof ConnectionMethode> = yield select((state) => state.device.connectionMethode)
    if (connectionMethode != ConnectionMethode.BLE) continue;

    var connectedDevice = yield select((state) => state.device.connectedDevice)
    //if (connectedDevice != null) return;  // TODO: Allready connected ? if problem

    if (device === null || device._manager === null) {
      yield put(DeviceActions.setDeviceError("Could not find device.."));
      yield put(DeviceActions.connectionStateUpdated(ConnectionState.DISCONNECTED));
      return;
    }
    if (device.isConnected()) {
      console.log("is connected ??");
    }
    console.log("Connect device");

    try {
      const disconnectedChannel = yield eventChannel((emit) => {
          const subscription = device.onDisconnected((error) => {
            emit({ type: 'DISCONNECT_DEVICE', error: error });
          });
        return () => {
          subscription.remove();
        };
      }, buffers.expanding(1));
    
      const deviceActionChannel = yield actionChannel([
        'DISCONNECT_DEVICE',
      ]);

      try {
        yield put(DeviceActions.connectionStateUpdated(ConnectionState.CONNECTING));
        yield call([device, device.connect]);
        yield put(DeviceActions.connectionStateUpdated(ConnectionState.DISCOVERING));
        yield call([device, device.discoverAllServicesAndCharacteristics]);
        yield put(DeviceActions.connectionStateUpdated(ConnectionState.CONNECTED));
        yield put(DeviceActions.connectionMethodeUpdated(ConnectionMethode.BLE));

        yield put(DeviceActions.connectedDeviceUpdated(device));

        var _device = device;

        yield fork(readBleDeviceData, manager)

        for (; ;) {
          const { deviceAction, disconnected } = yield race({
            deviceAction: take(deviceActionChannel),
            disconnected: take(disconnectedChannel),
          });

          if (deviceAction) {
            if (deviceAction.type === 'DISCONNECT_DEVICE') {
              console.log('Disconnected by user...');
              yield call([_device, _device.cancelConnection]);
              yield put(DeviceActions.connectionStateUpdated(ConnectionState.DISCONNECTING));
              //TODO: search for lost "connected" device..
              break;
            }
          } else if (disconnected) {
            console.log('Disconnected by device...');
            if (disconnected.error != null) {
              console.log("Error ", disconnected.error);
            }
            break;
          }
        }
      } catch (error) {
        /*yield put(
            DeviceActions.setDeviceError(error)
        )*/
      } finally {
        disconnectedChannel.close();
        yield put(DeviceActions.connectedDeviceUpdated(null))
        yield put(DeviceActions.connectionStateUpdated(ConnectionState.DISCONNECTED));
      }
    } catch (error) {
      console.log('Error on device actions...');
      yield put(DeviceActions.removeDevice(device));
      yield put(DeviceActions.connectionStateUpdated(ConnectionState.DISCONNECTING));
    }
  }
}

