import React, { Component } from 'react'
import { Platform, Text, View, ScrollView, ActivityIndicator } from 'react-native'
import { Input } from 'react-native-elements'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, UserStates, DataRefs, ErrorTypes, DataStringCheck } from '../../../Stores/Firebase/Selectors'

import { Button, TitleElement } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../../Theme'

class FarmInformation extends Component{
  constructor(props) {
    super(props);

    const {FarmName, Address, Country, Cvr, Manager} = this.setData(props)
    this.state = {
      FarmName: FarmName,
      Address: Address,
      Country: Country,
      Cvr: Cvr,
      Manager: Manager,

      Sections: "0",
      Pens: "0",
    }
  }

  setData(Props) {
    var FarmName = ""
    var Address = ""
    var Country = ""
    var Cvr = ""
    var Manager = ""

    const localDataStore = Props.firebase.localDataStore;
    const firebaseUser = this.props.firebase.firebaseUser

    if (firebaseUser) {
      if (firebaseUser.lastN !== "") {
        FarmName = firebaseUser.lastN
      }
      if (firebaseUser.address !== "") {
        Address = firebaseUser.address
      }
      if (firebaseUser.country) {
        Country = firebaseUser.country
      }
      if (firebaseUser.email != "") {
        Manager = firebaseUser.email
      }
    }

    if (localDataStore && firebaseUser && localDataStore.farms && firebaseUser.farms && localDataStore.farms[Object.keys(firebaseUser.farms)[0]]) {
      var FarmData = localDataStore.farms[Object.keys(firebaseUser.farms)[0]]

      if (FarmData.name) FarmName = FarmData.name
      if (FarmData.address) Address = FarmData.address
      if (FarmData.country) Country = FarmData.country
      if (FarmData.cvr) Cvr = FarmData.cvr
      if (FarmData.manager) Manager = FarmData.manager
    }

    return {FarmName, Address, Country, Cvr, Manager}
  }

  getData() {
    
  }

  checkData(props) {
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)

    this.props.getUser(null)
    this.props.getFarm()

    this.checkData(this.props)
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);

      if (this.props.firebase.firebaseUser) {
        /*if ((this.props.firebase.firebaseUser.name != this.state.FirstN && this.state.FirstN === "") ||
            (this.props.firebase.firebaseUser.lastN != this.state.lastN && this.state.lastN === "") ||
            (this.props.firebase.firebaseUser.contact != this.state.Contact && this.state.Contact === "") ||
            (this.props.firebase.firebaseUser.address != this.state.Address && this.state.Address === "")
          ) {
          const {FarmName, Address, Cvr, Manager} = this.setData(this.props)
          this.state = {
            FarmName: FarmName,
            Address: Address,
            Cvr: Cvr,
            Manager: Manager,
          }
        }*/
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ErrorMessage = this.props.firebase.viewError

    return(
      <ScrollView style={[Metrics.verticalMargin, {paddingRight: 5}]}>
        <View
          style={[
            Helpers.mainContainer,
          ]}
        >
          <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
            {ErrorMessage && (!ErrorMessage.type || ErrorMessage.type === ErrorTypes.SYSTEM) ? (
              <Text style={ApplicationStyles.error}>{ErrorMessage.message?ErrorMessage.message:ErrorMessage}</Text>
            ):(<></>)}
            <TitleElement Text="Farm Informations"/>
            <View style={ [Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
              <Input
                label="Farm Name"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (text){
                    if (DataStringCheck(text)){
                      this.setState({FarmName:text})
                    }
                  }else {
                    this.setState({FarmName:text})
                  }
                }}
                value={this.state.FarmName}
              />
              <Input
                label="Address"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (text){
                    //if (DataStringCheck(text)){
                      this.setState({Address:text})
                    //}
                  }else {
                    this.setState({Address:text})
                  }
                }}
                value={this.state.Address}
              />
              <Input
                label="Country"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (text){
                    //if (DataStringCheck(text)){
                      this.setState({Country:text})
                    //}
                  }else {
                    this.setState({Country:text})
                  }
                }}
                value={this.state.Country}
              />
              <Input
                label="Cvr"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (!isNaN(text)){
                    this.setState({
                      Cvr:text
                    })
                  }
                }}
                value={this.state.Cvr}
              />
              <Input
                label="Manager contact"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (text) {
                    var test = text
                    test = test.replace('@','a')
                    test = test.replace('.','d')
                    if (DataStringCheck(test)) {
                      this.setState({Manager:text})
                    }
                  }else {
                    this.setState({Manager:text})
                  }
                }}
                value={this.state.Manager}
              />
              <Input
                label="Number of Sections"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (!isNaN(text)){
                    this.setState({
                      Sections:text
                    })
                  }
                }}
                value={this.state.Sections}
              />
              <Input
                label="Number of Pens in each Sections"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (!isNaN(text)){
                    this.setState({
                      Pens:text
                    })
                  }
                }}
                value={this.state.Pens}
              />
            </View>

            <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
              <Button Text="Save Farm information" 
                onPress={() => {
                  var _FarmData = {}
                  var _sections = {};
                  var _pens = {};

                  if (!this.state.FarmName || this.state.FarmName === ""){
                    this.props.setViewError("Farm name is needed")
                  } else if (!DataStringCheck(this.state.FarmName)) {
                    this.props.setViewError("First name contains invalid characters")
                  }
                  
                  else {
                    _FarmData = {name: this.state.FarmName}

                    if (this.state.Address !== "") _FarmData.address = this.state.Address
                    if (this.state.Country !== "") _FarmData.country = this.state.Country
                    if (this.state.Cvr !== "") _FarmData.cvr = this.state.Cvr
                    if (this.state.Manager !== "") _FarmData.manager = this.state.Manager

                    this.props.setViewLoading(true)
                    this.props.updateFarm(_FarmData)

                    if (this.state.Sections && this.state.Pens) {
                      this.props.setViewLoading(true)
                      var Pen = 1;
                      var i, i2;
                      for (i = 1; i <= this.state.Sections; i++) {
                        var sectionPens = {};
                        for (i2 = 1; i2 <= this.state.Pens; i2++) {
                          _pens['Pen_' + Pen] = {section: 'Section_' + i}
                          sectionPens['Pen_' + Pen] = true
                          Pen++;
                        }
                        
                        _sections['Section_' + i] = {pens: sectionPens}
                      }
                      console.log("Set", _sections, _pens)
                      this.props.updateFarmData(_sections, _pens)
                    }

                    if (this.props.OnDone) {
                      this.props.OnDone(true)
                    }
                  }
                }}/>
              </View>
          </View>
          {(IsLoading || this.props.firebase.viewIsLoading) &&
            <ActivityIndicator style={Helpers.loadingContainer} size='large' />
          }
        </View>
      </ScrollView>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    //setViewError: (error, type) => dispatch(FirebaseActions.setViewError(error, type)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    getUser: (UserId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.USERSDATA, {UserId: UserId})),
    getFarm: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.FARMS, {})),

    updateFarm: (FarmData) => dispatch(FirebaseActions.actionStarting(Actions.UPDATE, DataRefs.FARMS, {Data: FarmData})),
    updateFarmData: (sections, pens) => dispatch(FirebaseActions.actionStarting(Actions.UPDATE, DataRefs.FARMDATAS, {Data:{sections:sections, pens:pens}})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(FarmInformation);
//export default FarmInformation;