import React from 'react'
import { createAppContainer } from 'react-navigation'
import { createStackNavigator } from 'react-navigation-stack'
import { TouchableOpacity, Icon, View, Text } from 'react-native'

import SplashScreen from '../Containers/SplashScreen/SplashScreen'
import DeepLinkingScreen from '../Containers/DeepLinking/DeepLinkingScreen'

import FrontNavigation from '../Navigators/FrontNavigator'
import AuthNavigation from '../Navigators/AuthNavigator'

import UserPanelNavigation from '../Navigators/UserPanelNavigator'
import AdminPanelNavigator from '../Navigators/AdminPanelNavigator'
import { ApplicationStyles } from '../Theme'

/**
 * The root screen contains the application's navigation.
 *
 * @see https://reactnavigation.org/docs/en/hello-react-navigation.html#creating-a-stack-navigator
 */
const StackNavigator = createStackNavigator(
  {
    // Create the application routes here (the key is the route name, the value is the target screen)
    // See https://reactnavigation.org/docs/en/stack-navigator.html#routeconfigs
    SplashScreen: {
      screen: SplashScreen,
      navigationOptions: () => ({
        headerShown: false,
      }),
    },

    MainStack: {
      screen: FrontNavigation, // Called By StartupSaga.js
      path: 'Front',
    },
    
    AuthStack: AuthNavigation, // All user pages

    UserPanel: UserPanelNavigation, // 'Free' Users Pages... 

    AdminPanel: AdminPanelNavigator, // Admin Pages

    DeepLinkingScreen: {
      screen: DeepLinkingScreen,
      path: 'deeplink',
    },
  },
  {
    // By default the application will show the splash screen
    initialRouteName: 'SplashScreen',
    // See https://reactnavigation.org/docs/en/stack-navigator.html#stacknavigatorconfig
    headerMode: 'none',
    defaultNavigationOptions:{
      headerTitleAlign: 'center',
      headerRight: () => (<View style={{padding:6}}></View>),
    },
  }
)

export default createAppContainer(StackNavigator)
