import { PermissionsAndroid, NativeModules, NativeEventEmitter } from 'react-native'
import {buffers, eventChannel} from 'redux-saga'
import { put, call, fork, take, cancelled, cancel, actionChannel, race, select } from 'redux-saga/effects'

import DeviceActions from '../../../Stores/Device/Actions'
import { deviceService } from '../../../Services/DeviceService'

import BluetoothActions from '../../../Stores/Device/BlueTooth/Actions'

import { ScannerState, ConnectionState, ConnectionMethode } from '../../../Stores/Device/Selectors'

import { Device } from 'react-native-ble-plx'

export function* bluetoothStartup(sagaArgs) {
  //Check and setup Bluetooth....
  const permitError = yield call(deviceService.isBlePermitted) //BLE permits is same for bluetooth
  if (permitError) {
    yield put( DeviceActions.setDeviceError(permitError))
  } else {
    // All below generators are described below...
    yield fork(handleScanning, sagaArgs.BlueManager)
    yield fork(handleConnection, sagaArgs.BlueManager)
  }
  
  console.log('BlueTooth Startup : Done');
}

// This generator decides if we want to start or stop scanning depending on specific
// events:
// * SCANNER is currently AVAILABLE or allready SCANNING
// * ConnectionMethode is on BLUETOOTH
// * We are not allready Connected to a device..
function* handleScanning(manager) {
  console.log("Handle Bluetooh Scanning...")

  var scanTask = null
  var scannerState: $Keys<typeof ScannerState> = yield select((state) => state.device.scannerState)
  var connectionMethode: $Keys<typeof ConnectionMethode> = yield select((state) => state.device.connectionMethode)
  var connectionState: $Keys<typeof ConnectionState> = yield select((state) => state.device.connectionState)

  const channel = yield actionChannel([
    'SCANNER_STATE_UPDATED',
    'CONNECTION_METHODE_UPDATED',
    'CONNECTION_STATE_UPDATED',
  ])

  for (;;) {
    const action:
      | SCANNER_STATE_UPDATED
      | CONNECTION_METHODE_UPDATED 
      | CONNECTION_STATE_UPDATED = yield take(channel)

    switch (action.type) {
      case 'SCANNER_STATE_UPDATED':
        scannerState = action.scannerState
        break;
      case 'CONNECTION_METHODE_UPDATED':
        connectionMethode = action.connectionMethode
        break;
      case 'CONNECTION_STATE_UPDATED':
        connectionState = action.connectionState
        break;
    }

    const enableScanning =
      (scannerState === ScannerState.AVAILABLE ||
        scannerState === ScannerState.SCANNING) &&
      connectionMethode === ConnectionMethode.BLUETOOTH &&
      (connectionState === ConnectionState.DISCONNECTING ||
        connectionState === ConnectionState.DISCONNECTED)

    if (enableScanning) {
      if (scannerState != ScannerState.SCANNING) { //Dount need to start scanner if allready Scanning
        if (scanTask != null) {
          yield cancel(scanTask)
        }
        scanTask = yield fork(scanBluetooth, manager);
      }
    } else {
      if (scanTask != null) {
        yield cancel(scanTask)
        scanTask = null
      }
    }
  }
}

