export function* readBlueDeviceData(sagaArgs) {
  console.log("Blue Read");
  const manager = sagaArgs.BlueManager;

  var device: Device = yield select((state) => state.device.connectedDevice)
  if (device === null) return;

  const Message = yield call(manager.receiveData, device.id);
  console.log(Message);
  if (Message != "") {
    yield put(DeviceActions.updateDeviceData({TAGId: Message}))
  }
}

// As long as this generator is working we have enabled scanning functionality.
// **
// When we detect Tag-device, Device with localName or Name, We send device to device list
// Testing with isConnectable
function* scanBluetooth(manager) {
  yield put(DeviceActions.scannerStateUpdated(ScannerState.SCANNING))
  yield put(DeviceActions.clearDevices())

  console.log("Scanning started");

  yield call(manager.Discover);
  const bluetoothChannel = yield eventChannel((emit) => {
    const blueTootControlEmitter = new NativeEventEmitter(manager);
    const subscription = blueTootControlEmitter.addListener('blueToothDeviceFound', (device) => {
      if (device != null){
        var scannedDevice = new Device();
        scannedDevice.id = device.address
        scannedDevice.localName = (device.name != null ? device.name : "NONE");
        scannedDevice.name = device.alias
        
        if (scannedDevice.localName != null) {
          if (scannedDevice.localName === 'SensorTag') { // Catch Specefic SensorTag
            //console.log("Device with Tag found: " + scannedDevice.localName + " : " + scannedDevice.id);
            emit(scannedDevice);
          } else { //Else if not ....
            //console.log("Device found localName: " + scannedDevice.localName + " : " + scannedDevice.id);
            emit(scannedDevice);
          }
        } 
        else if (device.alias != null && device.alias != ""){
          scannedDevice.name = device.alias
          emit(scannedDevice);
          //console.log("Device found Name: " + scannedDevice.name);
        }
      }
    })
    return () => {
      subscription.remove();
    };
  }, buffers.expanding(1))

  try {
    for (;;) {
      const scannedDevice: Device = yield take(bluetoothChannel);
      if (scannedDevice != null) {
        console.log("Interest Device Found: " + scannedDevice.localName + " : " + scannedDevice.id);
        yield put(DeviceActions.addDevice(scannedDevice))
      }
    }
  } finally {
    if (yield cancelled()) {
      yield call(manager.StopDiscovery);
      yield put(DeviceActions.scannerStateUpdated(ScannerState.IDLE))
      console.log('Scanning stopped...');
      bluetoothChannel.close()
    }
  }
}

export function* startBluetoothScan(){
  var connectionMethode = yield select((state) => state.device.connectionMethode);
  const scannerState = yield select((state) => state.device.scannerState);
  
  if (connectionMethode !== ConnectionMethode.BLUETOOTH) {
    yield put(DeviceActions.connectionMethodeUpdated(ConnectionMethode.BLUETOOTH)) // rem, handleScanning should contain ConnectionMethode check and shutof scanning on change
  } 
  yield call(setBlueScannerStart);
}
// Aparently functions cannot call itself... so..
function* setBlueScannerStart(){
  var connectionMethode = yield select((state) => state.device.connectionMethode);
  const scannerState = yield select((state) => state.device.scannerState);

  if (scannerState === ScannerState.IDLE && connectionMethode === ConnectionMethode.BLUETOOTH) {
    yield put(DeviceActions.scannerStateUpdated(ScannerState.AVAILABLE));
  }
}
export function* stopBluetoothScan(){
  const connectionMethode = yield select((state) => state.device.connectionMethode);
  if (connectionMethode === ConnectionMethode.BLUETOOTH) {
    yield put(DeviceActions.scannerStateUpdated(ScannerState.IDLE));
  }
}

export function* bluetoothScanAble(sagaArgs){
  yield call(sagaArgs.BlueManager.BluetoothSearchAble);
}