var auth, messaging, database, firebase;
if (Platform.OS === 'ios' || Platform.OS === 'android') {
  //#region App version imports
  auth = require('@react-native-firebase/auth').default;
  messaging = require( '@react-native-firebase/messaging').default;
  database = require('@react-native-firebase/database').default;
  //#endregion
} else {
  //#region Web Version imports
  var _firebase = require('firebase/app').default;
  require('firebase/auth').default;
  require('firebase/messaging').default;
  require('firebase/database').default;

  firebase = _firebase;
  
  if (!firebase.apps.length) {
    console.log("Starting")
    var Constants = require('expo-constants').default;
    console.log(Constants.manifest.web.config.firebase)
    _firebase.initializeApp(Constants.manifest.web.config.firebase);
  }
  
  auth = _firebase.auth;
  if (_firebase.messaging.isSupported()) {
    messaging = _firebase.messaging;
  }
  database = _firebase.database;
  console.log("Started")
  //#endregion
}

import { UserStates, DataRefs, Actions, ConsysRefs } from '../../Stores/Firebase/Selectors'

export function* firebaseStartup() {
  console.log("Starting firebase saga")
  //Check and setup Firebase and permissions
  yield put(FirebaseActions.setFirebaseError(null))

  /*console.log("test")
  if (Platform.OS !== 'ios' && Platform.OS !== 'android') {
    console.log("Starting")
    console.log(Constants.manifest.web.config.firebase)
    firebase.initializeApp(Constants.manifest.web.config.firebase);
  }*/

  const permitError = yield call(firebaseService.requestUserPermission)
  if (permitError) {
    yield put(FirebaseActions.fetchFailure(permitError))
  }else {
    //yield put(FirebaseActions.firebaseIsLoading(true))
    console.log("Starting handlers")
    //yield put(FirebaseActions.firebaseIsLoading(true))
    yield fork(handleAuthState)
    yield fork(handleActions)
    console.log("auth & actions")

    if (messaging && messaging.isSupported()) {
      if (Platform.OS === 'ios' || Platform.OS === 'android') {
        yield fork(onMessageHandler)
        yield fork(onBackgroundMessageHandler)
      }
      else {
        yield fork(onMessageHandler)
        yield fork(onBackgroundMessageHandler)
      }
    }

    //var FirebaseToken = yield call(firebaseService.requestFirebaseToken);
    //console.log('Firebase Token: ', FirebaseToken);
    
    //yield fork(testmessage,FirebaseToken)
  }

  console.log('Firebase Startup : Done');
  if (Platform.OS === 'ios' || Platform.OS === 'android') {
    console.log('Firebase will disable yellow box debug')
    console.disableYellowBox = true;
  }
}

