import React from 'react';
import { View, Text, Image} from 'react-native';
import Button from './Button'

import { ApplicationStyles, Helpers, Metrics } from '../Theme'

const Button_Panel = props => {
  const Texts = props.Texts;
  const Images = props.Images;

  const onPress = props.onPress;

  var Buttons = []

  Texts.forEach((element, index) => {
    var image = null
    if (Images && Images[index]){
      image = Images[index]
    }
    Buttons.push(
      <Button key={index} Text={element} icon={image}
        iconStyle={props.iconStyle} itemStyle={props.itemStyle}
        onPress={() => {
          if (onPress) {
            onPress({index: index, text: element})
          }
        }}/>
    )
  });

  return(
    <View style={[Helpers.buttonContainer, Helpers.midWidth, {marginBottom:30}, props.style] }>
      {Buttons}
    </View>
  )
};

export default Button_Panel;