import React, { useEffect } from 'react'
import { StyleSheet, Text, View, Image, ActivityIndicator } from 'react-native'

import { Helpers, Images, Colors } from '../Theme'

const Loading = props => {
  if (props.loading){
    var styles = props.back ? styles.container: {}
    return (
      <View style={[Helpers.fillRowCenter, Helpers.center, Helpers.logoContainer, styles]}>
        <View style={[Helpers.center, Helpers.logoContainer]}>
          <Image style={Helpers.fullSize} source={Images.Hexa_icon} resizeMode={'contain'} />
        </View>
      </View>
    )
  } else return null;
}

const styles = StyleSheet.create({
  container: {
    backgroundColor: Colors.primary,
  },
});

export default Loading
