import { Platform, Alert } from 'react-native'

import { UserStates } from '../Stores/Firebase/Selectors'
import { Actions, DataRefs, DataStates, format_Time } from '../Stores/Firebase/Selectors';

let auth, messaging, database, firebase;
if (Platform.OS === 'ios' || Platform.OS === 'android') {
  //#region App version imports
  auth = require('@react-native-firebase/auth').default;
  messaging = require('@react-native-firebase/messaging').default;
  database = require('@react-native-firebase/database').default;
  //#endregion
} else {
  console.log("firebase load")
  //#region Web Version imports
  var _firebase = require('firebase/app').default;
  require('firebase/auth').default;
  require('firebase/messaging').default;
  require('firebase/database').default;

  firebase = _firebase

  if (!firebase.apps.length) {
    console.log("Starting")
    var Constants = require('expo-constants').default;
    console.log(Constants.manifest.web.config.firebase)
    _firebase.initializeApp(Constants.manifest.web.config.firebase);
  }

  auth = _firebase.auth;
  if (_firebase.messaging.isSupported()){
    messaging = _firebase.messaging;
  }
  database = _firebase.database;
  console.log("Started")
  //#endregion
}

//#region Firebase specefic services
async function isPermit_UserPermission() {
  console.log("permit test")
  if (messaging) {
    const authStatus = await messaging().requestPermission();
    const enabled =
      authStatus === messaging.AuthorizationStatus.AUTHORIZED ||
      authStatus === messaging.AuthorizationStatus.PROVISIONAL;

    if (!enabled) {
      console.log('Authorization status:', authStatus);
      return false;
    }
  }
  return true;
}

function requestUserPermission() {
  return new Promise((resolve, reject) => {
    if ((Platform.OS === "ios" || Platform.OS == "android") && !isPermit_UserPermission()) {
      resolve('This application needs premissions to be using notifications')
    }
    resolve(null)
  })
}

async function is_getToken() {
  var fcmToken = ""
  if (messaging) {
    try{
      fcmToken = await messaging().getToken();
      console.log('Device_token: ', fcmToken)
    }catch(error){
      console.log(error)
    }
  }
  return fcmToken;
}

function requestFirebaseToken() {
  return new Promise((resolve, reject) => {
    var token;
    if (Platform.OS === 'ios' || Platform.OS === 'android') {
      try {
        token = is_getToken();
      } catch{}
    } else if (messaging && messaging.isSupported()) {
      try {
        token = is_getToken();
      } catch{}
    }
    if (!token) {
      resolve('Could not find Firebase messaging Token')
    }
    resolve(token)
  })
}

function wrappedAlert(title, message, okText, _cancelable) {
  return new Promise(resolve => {
    Alert.alert(
      title,
      message,
      [{text: okText, onPress: function() { resolve(); }}],
      {cancelable: _cancelable, onDismiss: function() {resolve(); }},
    );
  });
}
//#endregion

//#region Firebase Auth services
function createFirebaseUserWithemailandpassword({Email, Pass}) {
  return new Promise((resolve, reject) => {
    auth().createUserWithEmailAndPassword(Email,Pass).then(() => {
      var User = auth().currentUser;
      var Name = User.displayName ? User.displayName : User.email
      actionStarting(DataRefs.USERSDATA, Actions.UPDATE, {UserId: User.uid, Data: {
        name: Name,
        state: UserStates.STARTER,
      }}).then((_data) => {
        User.updateProfile({
          displayName: Name
        }).then((data) => {
          User.sendEmailVerification().then(function() {
            // Email sent.
            resolve(null)
          }).catch(function(error) {
            console.log("error in send EmailVerification", error)
            resolve({error:error.code, viewError:'Verification could not be send'})
          });
        }).catch(function(error) {
          console.log("update profile")
          resolve({error:error.code, viewError:'Update error, please refresh'})
        });
      })
      
    }).catch(error => {
      console.log("UserCreate Error: ", error);
      if (error.code === 'auth/email-already-in-use') {
        resolve({error:error.code, viewError:'The email address is already in use.'})
        //Should not exploid the users... (this could be used as a find user-mails) ???
      }
      else if (error.code === 'auth/invalid-email') {
        resolve({error:error.code, viewError:'The email addres is invalid.'})
      }
      else if (error.code === 'auth/weak-password') {
        resolve({error:error.code, viewError: error.message})
      } else {
        resolve({error:error.code, viewError: 'Something went wrong'})
      }
    })
  })
}

function signinWithemailandpassword({Email, Pass}){
  if (auth().currentUser){
    if (auth().currentUser.email != Email){
      const res = logoutUser()
    }
    else {
      return new Promise((resolve, reject) => {
        resolve(true)
      })
    }
  }
  return new Promise((resolve, reject) => {
    auth().signInWithEmailAndPassword(Email,Pass).then(() => {
      resolve(true)
    }).catch(error => {
      console.log("User Signin Error: ", error);

      if (error.code === 'auth/invalid-email') {
        resolve({error:error.code, viewError:'The email addres is invalid.'})
      }
      else {
        resolve({error:'Could not singin', viewError:'Could not singin'})
      }
    })
  })
}

function sendResetPassword({Email}){
  return new Promise((resolve, reject) => {
    auth().sendPasswordResetEmail(Email).then(() => {
      resolve(true)
    }).catch(error => {
      console.log("User resetPass error:", error);
      if (error.code === 'auth/user-not-found'){
        //Should not exploid the users... (this could be used as a find user-mails)
      }
      resolve(null)
    })
  })
}

function logoutUser(){
  return new Promise((resolve, reject) => {
    auth().signOut().then(() => {
      resolve({type:Actions.UPDATE, path:DataRefs.FIREBASEUSER, data:null })
    }).catch(error => {
      console.log("User logout error:", error);
      resolve({type:Actions.UPDATE, path:DataRefs.FIREBASEUSER, data:null })
    })
  })
}

function refreshUser(){
  return new Promise((resolve, reject) => {
    var user = auth().currentUser
    if (user){
      auth().currentUser.reload().then((data) => {
        user = auth().currentUser
        console.log(data, user)
        //resolve({type:Actions.UPDATE, path:DataRefs.FIREBASEUSER, data:null })
      })
    }
    resolve(null)
  })
}
//#endregion

//#region Master Control
function actionStarting(ref, action, params = null){
  const CurrentUser = auth().currentUser;
  switch(ref){
    case DataRefs.USERSDATA:
      switch(action){
        case Actions.FETCH:
          return fetchUser(params)
        case Actions.CREATE:
          if (params.hasOwnProperty("UserData")) {
            return new Promise((resolve, reject) => {
              var Data = params[0]
              createFarm({Data:{name: params.Farms.name}}).then((farmid) => {
                updateFarmData({FarmId:farmid, Data:{sections:params.Sections, pens: params.Pens}}).then(() => {
                  updateUserData({Data:{state: params.UserData.state, farms: {[farmid]: true}}}).then(() => {
                    fetchUser(CurrentUser.uid).then((data) => {
                      var Data = data.data
                      resolve({type:Actions.UPDATE, path:DataRefs.FIREBASEUSER, data:Data })
                    });
                  })
                })
              })
            })
          }
          break;
        case Actions.UPDATE:
          return updateUserData(params)
        case Actions.SIGN:
          if (!params){
            return logoutUser()
          }else {
            if (params.hasOwnProperty("refresh")){ // TODO: verify
              return refreshUser()
            } else return signinWithemailandpassword(params)
          }
          break;
        case Actions.CLEAR:
          if (!CurrentUser) {
            return {type:Actions.CLEAR, path:DataRefs.USERS, data:{}}
          } else {
            return {type:Actions.CLEAR, path:DataRefs.USERSDATA, data:{}}
          }
          break;
      }
      break;

    case DataRefs.USERS:
      switch(action){
        case Actions.FETCH:
          return fetchUser(params, true)
        case Actions.CREATE:
          if (params.hasOwnProperty("Email") && params.hasOwnProperty("Pass")) return createFirebaseUserWithemailandpassword(params)
          else if (params.hasOwnProperty("Email")) return sendResetPassword(params)
          break;
        case Actions.UPDATE:
          return {type:Actions.UPDATE, path:DataRefs.FIREBASEUSER, data:params}
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.USERS, data:{}}
      }
      break;

    case DataRefs.FARMS:
      switch(action){
        case Actions.FETCH:
          return fetchFarm(params);
        case Actions.CREATE:
          break;
        case Actions.UPDATE:
          return updateFarm(params)
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.FARMS, data:{}}
      }
      break;

    case DataRefs.FARMDATAS:
      switch(action) {
        case Actions.UPDATE:
          return updateFarmData(params)
      }
      break;

    case DataRefs.SECTIONS:
      switch(action){
        case Actions.FETCH:
          return fetchSections(params);
        case Actions.CREATE:
          break;
        case Actions.UPDATE:
          break;
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.SECTIONS, data:{}}
      }
      break;

    case DataRefs.PENS:
      switch(action){
        case Actions.FETCH:
          return fetchPens(params);
        case Actions.CREATE:
          return createNewPen(params);
        case Actions.DELETE:
          return deletePen(params)
        case Actions.UPDATE:
          return updatePen(params);
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.PENS, data:{}}
      }
      break;

    case DataRefs.PIGS:
      switch(action){
        case Actions.FETCH:
          return fetchPigs(params);
        case Actions.CREATE:
          return updatePigdata(params);
        case Actions.UPDATE:
          break;
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.PIGS, data:{}}
      }
      break;

    case DataRefs.BATCHS:
      switch(action){
        case Actions.FETCH:
          return fetchBatchs(params)
        case Actions.CREATE:
          return createBatchs(params)
        case Actions.UPDATE:
          return updateBatchs(params)
        case Actions.DELETE:
          //return deleteBatch(params)
          break;
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.BATCHS, data:{}}
      }
      break;

    case DataRefs.WEIGHING:
      switch(action){
        case Actions.FETCH:
          return fetchWeights(params);
        case Actions.CREATE:
          return createWeight(params);
        case Actions.UPDATE:
          break;
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.WEIGHING, data:{}}
      }
      break;

    case DataRefs.COMMENTS:
      switch(action) {
        case Actions.FETCH:
          return fetchComments(params);
        case Actions.CREATE:
          return createComment(params);
      }
      break;

    case DataRefs.GRAPH:
      switch(action){
        case Actions.FETCH:
          return fetchGraphData(params);
        case Actions.CREATE:
          return updateGraphData(params);
        case Actions.UPDATE:
          return updateGraphData(params);
        case Actions.CLEAR:
          return {type:Actions.CLEAR, path:DataRefs.GRAPH, data:{}}
      }
      break;
  }
  return new Promise((resolve, reject) => {
    resolve({error: "Unknown Action"})
  })
}
//#endregion

function SetUpdateTime(DataRef) {
  return new Promise((resolve, reject) => {
    console.log("Setting Update Time")
    database().ref(DataRef).update({
      update_time: database.ServerValue.TIMESTAMP,
    }).then(() => {
      resolve(true)
    }).catch(error => {
      console.log(error)
      /*if (error.code === 'database/permission-denied') {
        console.log("Batch update was permission denied. ");
      }else if (error.code) {
        console.log("Batch update Error: ", error);
      }*/
      resolve(true)
    });
  })
}
function CheckDataTime(DataRef, DataTime) {
  return new Promise((resolve, reject) => {
    if (!DataTime) resolve(true)
    database().ref(DataRef + "/update_time").once('value').then((_Time) => {
      var UpdateTime = _Time.val()
      if (!UpdateTime) {
        SetUpdateTime(DataRef).then((res) => {
          resolve(true)
        })
      } else {
        if (DataTime) {
          if (DataTime === "SET"){
            SetUpdateTime(DataRef).then((res) => {
              resolve(res)
            })
          } else if (DataTime < UpdateTime){
            resolve(true)
          }
          else resolve(false)
        }else resolve(true)
      }
    })
  })
}

//#region User data Controls
function fetchUser({UserId}, List = false) {
  var user = "";
  if (UserId) user = UserId
  else if (!List && auth().currentUser) user = auth().currentUser.uid;

  if (user || List) {
    const ref = "users/" + user;
    return new Promise((resolve, reject) => {
      database().ref(ref).once('value').then((userData) => {
        var data = userData.val()
        if (!List) {
          resolve({type:Actions.UPDATE, path:DataRefs.FIREBASEUSER, data:{data}})
        } else {
          resolve({type:Actions.UPDATE, path:DataRefs.USERS, data:{data}})
        }
      }).catch(error => {
        console.log(error)
        if (error.code === 'database/permission-denied') {
          console.log("User update was permission denied. ");
        }else if (error.code) {
          console.log("User update Error: ", error);
        }
        resolve({error:"An error occurred"})
      })
    })
  }
  else return new Promise((resolve, reject) => {
    console.log("User uid in fecth unknown");
    resolve(null)
  })
}

function updateUserData({UserId, Data}) {
  const user = UserId? UserId: auth().currentUser.uid;
  if (user) {
    const ref = "users/" + user;
    return new Promise((resolve, reject) => {
      database().ref(ref).update(
        Data
      ).then((data) => {
        resolve(null)
      }).catch(error => {
        if (error.code === 'database/permission-denied') {
          console.log("User update was permission denied. ");
        }else if (error.code) {
          console.log("User update Error: ", error);
        }
        resolve({error:"An error occurred"})
      })
    }) 
  } else return new Promise((resolve, reject) => {
    console.log("User uid in fecth unknown");
    resolve(null)
  })
}
//#endregion

//#region Farm info data Controls
function fetchFarm({FarmId}) {
  var ref = "farms/" + FarmId
  return database().ref(ref).once('value').then((_farmData) => {
    //TODO:Multiple farms
    var data = _farmData.val()
    if (FarmId) {
      data = {[FarmId]:data}
    }
    return {type:Actions.UPDATE, path:DataRefs.FARMS, data: data}
  })
  /*} else return new Promise((resolve, reject) => {
    console.log("Farm uid in fecth unknown");
    resolve(null)
  })*/
}

function updateFarm({FarmId, Data}) {
  return new Promise((resolve, reject) => {
    if (FarmId){
      database().ref('farms/'+ FarmId).update({
        ...Data
      }).then(()=> {
        resolve(null)
      }).catch(error => {
        console.log("Create Farm Error: ", error);
        resolve(false)
      })
    } else {
      const farm = database().ref('farms/').push()
      const farmid = farm.key
      farm.set({
        Data
      }).then(() => {
        resolve(farmid)
      }).catch(error => {
        console.log("Create Farm Error: ", error);
        resolve(false)
      })
    }
  })
}

function createFarm({Data}){
  const user = auth().currentUser;
  const ref = "users/" + user.uid;

  return new Promise((resolve, reject) => {
    const farm = database().ref('farms/').push()
    const farmid = farm.key
    
    farm.set({
      email: user.email,
      manager: user.displayName ? user.displayName : user.email,
      name: Data.name
    }).then(() => {
      resolve(farmid)
    }).catch(error => {
      console.log("Create Farm Error: ", error);
      resolve(false)
    })
  })
}

function updateFarmData({FarmId, Data}) {
  if (FarmId) {
    const ref = "farms_data/" + FarmId;
    return new Promise((resolve, reject) => {
      database().ref(ref).update(
        Data
      ).then(() => {
        resolve(null)
      }).catch(error => {
        if (error.code === 'database/permission-denied') {
          console.log("Farm data update was permission denied.");
        }else if (error.code) {
          console.log("User update Error: ", error);
        }

        resolve(false)
      })
    })
  } else return new Promise((resolve, reject) => {
    console.log("Farm uid in fecth unknown");
    resolve(null)
  })
}
//#endregion

//#region Farm data Controls

//#region Sections
function fetchSections({FarmId, SectionId, DataTime}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/sections";
    if (SectionId){
      ref += "/" + SectionId
    }
    return new Promise((resolve, reject) => {
      CheckDataTime("farms_data/" + FarmId + "/sections", DataTime).then((DoFetch) => {
        if (DoFetch) {
          console.log("Sections fetch called")
          return database().ref(ref).once('value').then((_sectionidData) => {
            resolve({type:Actions.UPDATE, path:DataRefs.SECTIONS, data:_sectionidData.val()})
          })
        }
        else {
          resolve(null)
        }
      })
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Sections Error");
    resolve(null)
  })
}
//#endregion

//#region Pens
function fetchPens({FarmId, PenId, DataTime}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pens";
    if (PenId){
      ref += "/" + PenId
    }
    return new Promise((resolve, reject) => {
      CheckDataTime("farms_data/" + FarmId + "/pens", DataTime).then((DoFetch) => {
        if (DoFetch){
          console.log("Pens fetch Called")

          database().ref(ref).once('value').then((_pensData) => {
            if (PenId) {
              var penData = _pensData.val()
              if (!penData) penData = {}
              if (!penData.datas) penData.datas = {}
              //if (!penData.datas.gcurve) penData.datas.gcurve = "default";
      
              var pen = {[PenId]: penData};
              resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:pen})
            } else {
              resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:_pensData.val()})
            }
          })
        }
        else {
          resolve(null)
        }
      })
    })
  } 
  return new Promise((resolve, reject) => {
    console.log("Fecth Pens Error");
    resolve(null)
  })
}

function createNewPen({FarmId, PenId, SectionId}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      if (PenId) {
        var ref2 = ref + "pens/" + PenId + "/"
        database().ref(ref2).set({
          section: SectionId,
        }).then(() => {
          database().ref(ref + "sections/" + SectionId + "/pens").update({
            [PenId]: true,
          }).then(() => {
            database().ref(ref + "sections/").update({
              update_time: database.ServerValue.TIMESTAMP,
            }).then(() => {
              fetchPens({FarmId: FarmId, PenId: PenId, DataTime: "SET"}).then((_Pen) => {
                resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:_Pen.data})
              });
            });
          });
        });
      }
    })
  }
}

function deletePen({FarmId, PenId, SectionId}){
  if (FarmId && PenId){
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      database().ref(ref + "pens/" + PenId).remove().then(() => {
        if (SectionId){
          database().ref(ref + "sections/" + SectionId + "/pens/" + PenId).remove().then(() => {
            database().ref(ref + "sections/").update({
              update_time: database.ServerValue.TIMESTAMP,
            }).then(() => {
              fetchPens({FarmId: FarmId, PenId: PenId, DataTime: "SET"}).then((_Pen) => {
                resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:_Pen.data})
              })
            });
          })
        }else {
          resolve(null)
        }
      })
    })
  }
}

function updatePen({FarmId, PenId, Pen}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    return new Promise((resolve, reject) => {
      if (PenId){
        fetchPens({FarmId: FarmId, PenId: PenId}).then((_Pen) => {
          var ref2 = ref + "pens/" + PenId + "/"
          var data = Pen.datas
          var Datas = {..._Pen.data[PenId].datas, ...data}
          database().ref(ref2).update({
            ...Pen,
            datas: Datas
          }).then(() => {
            if (Pen.section) {
              database().ref(ref + "sections/" + SectionId + "/pens").update({
                [PenId]: true,
              }).then(() => {
                database().ref(ref + "sections/").update({
                  update_time: database.ServerValue.TIMESTAMP,
                }).then(() => {
                  resolve(null)
                });
              });
            }
          });
        })
      }
    })
  }
}
//#endregion

//#region Pigs
function fetchPigs({FarmId, TagId}){
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/pigs";
    if (TagId){
      ref += "/" + TagId
    }

    return database().ref(ref).once('value').then((_pigsData) => {
      if (TagId) {
        var pigData = _pigsData.val()
        if (!pigData) return null;
        if (!pigData.datas) pigData.datas = {}
        if (!pigData.datas.gcurve) pigData.datas.gcurve = "default";

        var pig = {[TagId]: pigData};
        return {type:Actions.UPDATE, path:DataRefs.PIGS, data:pig}
      } else {
        return {type:Actions.UPDATE, path:DataRefs.PIGS, data:_pigsData.val()}
      }
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Pigs Error");
    resolve(null)
  })
}

function updatePigPen({FarmId, TagId, PenId, Action}) {
  if (PenId) {
    var ref2 = "farms_data/" + FarmId + "/" + "pens/" + PenId + "/datas"
    return new Promise((resolve, reject) => {
      database().ref(ref2 + "/count").once('value').then((counts) => {
        var val = counts.val()
        if (!val) val = 0
        if (Action && !isNaN(Action)) {
          val = parseInt(val) + parseInt(Action)
        } else {
          val = parseInt(val) +1
        }
        database().ref(ref2).update({
          count: val,
        }).then(() => {
          fetchPens({FarmId: FarmId, PenId: PenId}).then((pen) => {
            resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:pen.data})
          })
        });
      }).catch(error => {
        console.log("Update pig pen Error: ", error);
        resolve(null)
      })
    })
  }else {
    return new Promise((resolve, reject) => {
      resolve(null)
    })
  }
}

function updatePigdata({FarmId, TagId, PenId, Action}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/";
    if (TagId) {
      return new Promise((resolve, reject) => {
        var ref2 = ref + "pigs/" + TagId + "/"
        fetchPigs({FarmId: FarmId, TagId: TagId}).then((pig) => {
          if (pig && pig.data) {
            pig = pig.data[TagId]?pig.data[TagId]:null
          }
          if (!pig || !pig.hasOwnProperty('pen')){
            database().ref(ref2).update({
              pen: 0,
            }).then(() => {
              fetchPigs({FarmId: FarmId}).then((pigs) => {
                console.log("?", pigs)
                resolve(null)
                //resolve({type:Actions.UPDATE, path:DataRefs.PIGS, data:pigs})
              })
            })
          }
          else if (PenId && PenId != pig.pen) {
            updatePigPen({FarmId: FarmId, TagId: TagId, PenId: pig.pen, Action:-1}).then((data)=>{
              updatePigPen({FarmId: FarmId, TagId: TagId, PenId: PenId, Action:+1}).then((data)=>{
                database().ref(ref2).update({
                  pen: PenId
                }).then(() => {
                  fetchPigs({FarmId: FarmId}).then((pigs)=> {
                    resolve({type:Actions.UPDATE, path:DataRefs.PIGS, data:pigs.data})
                  })
                })
              })
            })
          } else {
            resolve(null)
          }
        })
      })
    } else {
      return new Promise((resolve, reject) => {
        updatePigPen({FarmId: FarmId, TagId: TagId, PenId: PenId, Action:Action}).then((data) => {
          resolve({type:Actions.UPDATE, path:DataRefs.PENS, data:data.data})
        })
      })
    }
  }
}
