//#region Batchs
function fetchBatchs({FarmId, BatchId, Data, DataTime}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/batchs";
    if (BatchId){
      ref += "/" + BatchId
    }

    return new Promise((resolve, reject) => {
      CheckDataTime("farms_data/" + FarmId + "/batchs", DataTime).then((DoFetch) => {
        if (DoFetch) {
          if (!Data) {
            database().ref(ref).once('value').then((_batchdata) => {
              if (BatchId) {
                var batchdata = _batchdata.val()
                if (!batchdata) return null;
                if (!batchdata.datas) batchdata.datas = {}
                if (!batchdata.pens) batchdata.pens = []
                if (!batchdata.datas.gcurve) batchdata.datas.gcurve = "default";

                var batch = {[BatchId]: batchdata};
                resolve({type:Actions.UPDATE, path:DataRefs.BATCHS, data:batch})
              } else {
                resolve({type:Actions.UPDATE, path:DataRefs.BATCHS, data:_batchdata.val()})
              }
            })
          }
          else if (BatchId) {
            switch(Data){
              case DataRefs.BATCHS:
                ref += "/" + "datas"
                return database().ref(ref).once('value').then((_batchdata) => {
                  var data = {[BatchId]: {datas: _batchdata.val()}}
                  resolve({type:Actions.UPDATE, path:DataRefs.BATCHS, data:data})
                })
              case DataRefs.PENS:
                ref += "/" + "pens"
                return database().ref(ref).once('value').then((_batchdata) => {
                  var pens = {[BatchId]: {pens: _batchdata.val()}}
                  resolve({type:Actions.UPDATE, path:DataRefs.BATCHS, data:pens})
                })
              case DataRefs.GRAPH:
                ref += "/" + "gcurve"
                return database().ref(ref).once('value').then((_batchdata) => {
                  var curve = {[BatchId]: {gcurve: _batchdata.val()}}
                  resolve({type:Actions.UPDATE, path:DataRefs.BATCHS, data:curve})
                })
            }
          }

        } else {
          resolve(null)
        }
      })
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Batchs Error");
    resolve(null)
  })
}

function createBatchs({FarmId, BatchId, Batch}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/batchs";
    return new Promise((resolve, reject) => {
      if (Batch && Batch.name) {
        BatchId = Batch.name;
        ref += "/" + Batch.name + "/"
        delete Batch.name;
        database().ref(ref).set({
          ...Batch,
        }).then(() => {
          Object.keys(Batch.pens).forEach(pen => {
            updatePen({FarmId:FarmId, PenId:pen, Pen:{datas:{batch:BatchId}}})
          })
          resolve(fetchBatchs({FarmId: FarmId, DataTime: "SET"}))
        }).catch(error => {
          if (error.code === 'database/permission-denied') {
            console.log("Batch update was permission denied. ");
          }else if (error.code) {
            console.log("Batch update Error: ", error);
          }
          resolve({error:"An error occurred"})
        })
      } else if (BatchId) { //added else ??? still cleanup ??
        console.log("???") //TODO: why id.. mabye cleanup
        ref += "/" + BatchId + "/"
        database().ref(ref).set({
          time: database.ServerValue.TIMESTAMP,
          pens:[],
          datas:{
            devianceMin:1,
            devianceMax:2,
          }
        }).then(() => {
          resolve(fetchBatchs({FarmId: FarmId, DataTime: "SET"}))
        });
      }
    })
  }
}

function updateBatchs({FarmId, BatchId, Batch, Data}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/batchs";
    return new Promise((resolve, reject) => {
      if (BatchId && Batch) {
        ref += "/" + BatchId
        fetchBatchs({FarmId: FarmId, BatchId: BatchId, Data:DataRefs.PENS}).then((_batch) => {
          var Pens = _batch.data[BatchId].pens
          if (Batch.name) delete Batch.name

          var BatchPens = {}
          if (Batch.pens && JSON.stringify(Batch.pens)!==JSON.stringify(Pens)) {
            if (!Pens) Pens = []
            var RemovedPens = Object.keys(Pens).filter(x => !Object.keys(Batch.pens).includes(x) && !Pens[x].enddate);
            var NewPens = Object.keys(Batch.pens).filter(x => !Object.keys(Pens).includes(x) || Pens[x].enddate);
            var SamePens = Object.keys(Pens).filter(x => !Object.keys(RemovedPens).includes(x) && !Object.keys(NewPens).includes(x));

            if (RemovedPens && RemovedPens.length > 0) {
              RemovedPens.forEach(pen => {
                if (Pens[pen]) {
                  var Pen = Pens[pen]
                  Pen.enddate = Date.now()
                  BatchPens[pen] = Pen
                  fetchPens({FarmId: FarmId, PenId:pen}).then((pendata) => {
                    var count = 0
                    if (pendata.data[pen] && pendata.data[pen].datas && pendata.data[pen].datas.count)count=pendata.data[pen].datas.count
                    BatchPens[pen].count = count
                  })
                  updatePen({FarmId:FarmId, PenId:pen, Pen:{datas:{batch:""}}})
                }
              })
            }
            if (NewPens && NewPens.length > 0) {
              NewPens.forEach(pen => {
                var Pen = Pens[pen]?Pens[pen]:{startdate:Date.now()}
                if (Pen.enddate) delete Pen.enddate
                BatchPens[pen] = Pen
                updatePen({FarmId:FarmId, PenId:pen, Pen:{datas:{batch:BatchId}}})
              })
            }

            if (SamePens && SamePens.length > 0) {
              SamePens.forEach(pen => {
                BatchPens[pen] = Pens[pen]
              })
            }
          }
          Batch.pens = BatchPens

          if (Batch.state && Batch.state === DataStates.CLOSED) {
            fetchBatchs({FarmId: FarmId, BatchId: BatchId, Data:DataRefs.GRAPH}).then((curve) => {
              var CurveId = curve.data[BatchId].gcurve
              fetchGraphData({FarmId: FarmId}).then((curve) => {
                Batch.gcurve = curve.data[CurveId]
                Batch.closed = {comments: [], weighings: []}
                
                for (let i = 0, p = Promise.resolve(); i <= Object.keys(Pens).length; i++) {
                  p = p.then(_ => new Promise(_resolve => {
                    setTimeout(function () {
                      if (i !== Object.keys(Pens).length) {
                        var PenId = Object.keys(Pens)[i]
                        Batch.pens[PenId] = Pens[PenId]
                        if (!Batch.pens[PenId].hasOwnProperty("startdate")){
                          Batch.pens[PenId] = {startdate:Batch.time}
                        }

                        fetchPens({FarmId: FarmId, PenId:PenId}).then((pendata) => {
                          var count = 0
                          if (pendata.data[PenId] && pendata.data[PenId].datas && pendata.data[PenId].datas.count) count=pendata.data[PenId].datas.count
                          
                          Batch.pens[PenId].count = count
                        })
                        if (!Batch.pens[PenId].enddate) {
                          Batch.pens[PenId].enddate = Date.now()
                        }
                        updatePen({FarmId:FarmId, PenId:PenId, Pen:{datas:{batch:"", count:0}}})

                        fetchComments({FarmId: FarmId, PenId: PenId}).then((comments) => {
                          Batch.closed.comments[PenId] = comments.data[PenId]
                          database().ref("farms_data/" + FarmId + "/datas/comments/" + PenId).remove()
                          
                          fetchWeights({FarmId: FarmId, PenId: PenId}).then((weights) => {
                            Batch.closed.weighings[PenId] = weights.data[PenId]
                            database().ref("farms_data/" + FarmId + "/weighings/" + PenId).remove()
                            _resolve();
                          })
                        })
                      }
                      else {
                        database().ref(ref).update({
                          ...Batch
                        }).then(()=>{
                          database().ref(ref + "/closed").set(
                            Batch.closed
                          ).then(() => {
                            fetchBatchs({FarmId: FarmId, BatchId: BatchId, DataTime: "SET"}).then((data)=>{
                              resolve(data)
                            })
                          }).catch(error => {
                            if (error.code === 'database/permission-denied') {
                              console.log("Batch update was permission denied. ");
                            }else if (error.code) {
                              console.log("Batch update Error: ", error);
                            }
                            console.log(error)
                            resolve({error:"An error occurred"})
                          })
                        })
                      }
                    })
                  }));
                }
              })
            })
          } else {
            database().ref(ref).update({
              ...Batch,
            }).then(() => {
              fetchBatchs({FarmId: FarmId, BatchId: BatchId, DataTime: "SET"}).then((data)=>{
                resolve(data)
              })
            }).catch(error => {
              if (error.code === 'database/permission-denied') {
                console.log("Batch update was permission denied. ");
              }else if (error.code) {
                console.log("Batch update Error: ", error);
              }
              console.log(error)
              resolve({error:"An error occurred"})
            })
          }
        })
      }else {
        console.log("Update Batchs Error");
        resolve(null)
      }
    })
  }
}
//#endregion

//#region Weights
function fetchWeights({FarmId, PenId, SectionId, limitStart = 0, limitTo = 9999, DataTime, MemSections}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    return new Promise((resolve, reject) => {
      CheckDataTime("farms_data/" + FarmId + "/weighings", DataTime).then((DoFetch) => {
        if (DoFetch) {
          if (PenId) {
            console.log("Weight fetch called, getting Pen")
            ref += "/" + PenId
            database().ref(ref).orderByChild('time').limitToLast(limitTo).once('value', snapshot => {
              var weighing = {[snapshot.key]:{}}
              snapshot.forEach(c => {
                weighing[snapshot.key][c.key] = c.val()
              })
              resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:weighing})
            })
          } else {
            console.log("Weight fetch called, getting Sections")
            database().ref(ref + "/update_time").once('value').then((_weighingTime) => {
              //TODO: Section fetch cast :: in mem check ??
              var Weighings_Time = _weighingTime.val()
              fetchSections({FarmId, SectionId, DataTime: MemSections?MemSections.update_time:null}).then((sections) => {
                var itemsProcessed = 0;
                var length = 0;
                var newWeights = {}
                var Sections = sections?sections.data:MemSections
                Object.keys(Sections).forEach(section => {
                  if (Sections[section] && Sections[section].pens) {
                    length += Object.keys(Sections[section].pens).filter(e => e !== '__jsogObjectId').length
                    Object.keys(Sections[section].pens).filter(e => e !== '__jsogObjectId').forEach(pen => {
                      fetchWeights({FarmId:FarmId, PenId:pen, limitStart:0, limitTo:limitTo}).then((weights) => {
                        itemsProcessed++
                        newWeights = {...newWeights, ...weights.data}
                        if (itemsProcessed === length) {
                          newWeights.update_time = Weighings_Time
                          resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:newWeights})
                        }
                      }).catch(error => {
                        console.log(error)
                        resolve({type:Actions.UPDATE, path:DataRefs.WEIGHING, data:newWeights})
                      })
                    })
                  }
                })
              }).catch(error => {
                console.log(error)
              })
            })
          }

        }
        else {
          resolve(null)
        }
      })
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Weights Error");
    resolve(null)
  })
}

function createWeight({FarmId, PenId, Weight, Quantity, Date}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/weighings";
    if (PenId){
      ref += "/" + PenId + "/"

      return new Promise((resolve, reject) => {
        const weightRef = database().ref(ref).push()
        const weightId = weightRef.key
        weightRef.set({
          time: Date,
          weight: Weight,
          count: Quantity,
        }).then(() => {
          fetchWeights({FarmId:FarmId, PenId:PenId, DataTime: "SET"}).then((weights)=>{
            resolve(weights)
          })
        }).catch(error => {
          console.log("Create Weight Error: ", error);
          resolve(null)
        })
      })
    }
  }
}
