//#region Graphs
function fetchGraphData({FarmId, GraphId, DataTime}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/graph";
    ref = ref + "/" + GraphId;
    return new Promise((resolve, reject) => {
      CheckDataTime("farms_data/" + FarmId + "/graph", DataTime).then((DoFetch) => {
        console.log("Doing Graphs fetch", DoFetch, "Always..")
        //if (DoFetch){

          database().ref(ref).once('value').then((graph) => {
            var Graphs = null
            var GraphVal = graph.val()
            if (GraphVal) {
              if (GraphId) {
                var newGraph = {[graph.key]: GraphVal}
                Graphs = {...Graphs, ...newGraph}
              }else {
                Graphs = {...Graphs, ...GraphVal}
              }
            }
            if (!GraphId) {
              database().ref("farms_data/Agrisys/graph").once('value').then((Agraph) => {
                var AgraphVal = Agraph.val();
                if (AgraphVal) {
                  Graphs = {...AgraphVal, ...Graphs}
                }
                resolve({type:Actions.UPDATE, path:DataRefs.GRAPH, data:Graphs})
              }).catch(error => {
                console.log("Get Agrisys graph Error: ", error);
                resolve({type:Actions.UPDATE, path:DataRefs.GRAPH, data:Graphs})
              })
            }else {
              resolve({type:Actions.UPDATE, path:DataRefs.GRAPH, data:Graphs})
            }
          }).catch(error => {
            console.log("Get farm graph Error: ", error);
            resolve(null)
          })

        /*}
        else {
          resolve({type:Actions.UPDATE, path:DataRefs.GRAPH, data:{}})
        }*/
      })
    })
  }
  return new Promise((resolve, reject) => {
    console.log("Fecth Graph Error");
    resolve(null)
  })
}

function updateGraphData({FarmId, Graph}) {
  if (FarmId) {
    var ref = "farms_data/" + FarmId + "/graph";
    if (Graph.name) {
      ref += "/" + Graph.name + "/"
      return new Promise((resolve, reject) => {
        database().ref(ref).update({
          data: Graph.data,
          periode: Graph.periode
        }).then(() => {
          fetchGraphData({FarmId: FarmId, GraphId: Graph.name}).then((_Graph)=>{
            resolve(_Graph)
          })
        }).catch(error => {
          console.log("Create Graph Error: ", error);
          resolve(null)
        })
      })
    }
  }
}
