import React, { useEffect, useState, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, Dimensions, SafeAreaView, FlatList, Picker } from 'react-native'
import { Input } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject, GetStats_FromBatch, DataStringCheck } from '../../../Stores/Firebase/Selectors'

import { Button, CustomListItem, Datetime_Picker } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

import Icon from 'react-native-vector-icons/MaterialIcons'
import SectionedMultiSelect from 'react-native-sectioned-multi-select'

//import moment from 'moment';
//import SimplerDatePicker from '@cawfree/react-native-simpler-date-picker';
//import DatePicker from 'react-native-simple-date-picker';

//import DateTimePicker from '@react-native-community/datetimepicker';

//Genneral test for submodule merge (please delete this line)

import SelectBox from 'react-native-multi-selectbox'
import { xorBy } from 'lodash'

const window = Dimensions.get("window");
const screen = Dimensions.get("screen");

class Batchs extends Component {
  constructor(props){
    super(props);

    const {BatchList, freePens} = this.setData(props)
    this.state = {
      dimensions: {window, screen},
      BatchList: BatchList,

      Display: 0,

      BatchName: "",
      ShowPicker: false,
      StartD: new Date().getTime(),
      Curve: "",
      TargetWeight: "",
      DevianceMin: "",
      DevianceMax: "",

      freePens: freePens,
      avaiblePens: freePens,
      selectedPens: [],
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;

    var BatchList = localDataStore.batchs
    
    var freePens = []
    if (BatchList && localDataStore.sections) {
      Object.keys(localDataStore.sections).forEach(section => {
        if (localDataStore.sections[section].pens) {
          var pens = []
          Object.keys(localDataStore.sections[section].pens).filter(e => e !== '__jsogObjectId'  && e !== 'update_time').forEach(pen => {
            var add = true
            Object.keys(BatchList).forEach(batch => {
              if (!BatchList[batch].state || BatchList[batch].state !== "Closed") {
                if (BatchList[batch].pens && BatchList[batch].pens[pen] && !BatchList[batch].pens[pen].enddate) {
                  add = false;
                }
              }
            });
            if (add) {
              var item = {
                item: pen,
                id: section + "_" + pen
              }
              freePens.push(item)
            }
          })
        }
      });
    }
    return { BatchList, freePens }
  }

  getData() {
    const localDataStore = this.props.firebase.localDataStore;
    if (!localDataStore.sections) localDataStore.sections = {}
    if (!localDataStore.pens) localDataStore.pens = {}

    this.props.getBatch(localDataStore.batchs?localDataStore.batchs.update_time:null)
    this.props.getSections(localDataStore.sections?localDataStore.sections.update_time:null)
    this.props.getPens(localDataStore.pens?localDataStore.pens.update_time:null)
    this.props.getCurve()
  }

  checkData(props) {
  }

  onChange = ({ window, screen }) => {
    this.setState({
      dimensions: {window, screen},
    })
  };

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        if ((this.props.firebase.localDataStore.batchs && JSON.stringify(this.state.BatchList)!==JSON.stringify(this.props.firebase.localDataStore.batchs)) ||
            (this.props.firebase.localDataStore.pens && JSON.stringify(this.state.pens)!==JSON.stringify(this.props.firebase.localDataStore.pens))) {
          const {BatchList, freePens} = this.setData(this.props)
          this.setState({
            freePens: freePens,
            BatchList: BatchList,
          })
        }
      }
    }
  }

  componentDidMount() {
    Dimensions.addEventListener("change", this.onChange);
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  componentWillUnmount(){
    Dimensions.removeEventListener("change", this.onChange);
  }

  onMultiChange = (item) => {
    var items = xorBy(this.state.selectedPens, [item], 'id')
    this.setState({
      selectedPens: items
    })
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ErrorMessage = this.props.firebase.viewError

    const localDataStore = this.props.firebase.localDataStore

    var wide = this.state.dimensions.window.width>900?true:false
    var App = (Platform.OS === 'ios' || Platform.OS === 'android')||!wide

    return(
      <ScrollView style={[Metrics.verticalMargin, {paddingRight: 5}]}>
        <View
          style={[
            Helpers.mainContainer,
          ]}
        >
          <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
            {ErrorMessage ? (
              <Text style={ApplicationStyles.error}>{ErrorMessage}</Text>
            ):(<></>)}
            {this.state.Display === 0?(
              //#region Normal Front View to display all batches.
              <View style={[Helpers.contentContainer, Helpers.fullSize, {marginHorizontal:0, paddingHorizontal:-20}]}>
                {this.state.BatchList ? (
                  <View style={[Helpers.fill, Helpers.fullWidth, Metrics.smallHorizontalPadding, Metrics.smallVerticalPadding, {maxWidth:800, marginTop:10}]}>
                    <FlatList scrollEnabled={true} style={[Helpers.fill]}
                      data={Object.keys(this.state.BatchList).filter(e => e !== '__jsogObjectId' && e !== 'update_time')}
                      renderItem={({item, i})=>{
                        var name = item;
                        if (this.state.BatchList[item] && this.state.BatchList[item].state && this.state.BatchList[item].state === "Closed") {
                          name += "  Closed"
                        }
                        return(
                          <Button Text={name} onPress={() => {
                            var curveId = "default"
                            if (this.state.BatchList[item] && this.state.BatchList[item].gcurve) {
                              curveId = this.state.BatchList[item].gcurve
                              if (typeof curveId !== 'string') curveId = null
                            }
                            this.props.setCurrentData(item, curveId)
                            
                            NavigationService.navigate('DataPlotter')
                          }}/>
                        )
                      }}
                      keyExtractor={(item, index) => index.toString()}
                    />
                  </View>
                ):(<></>)}
                <View style={[Helpers.buttonContainer, Helpers.borderContainer, Helpers.midWidth]}>
                  <Button Text={"New Batch"} onPress={() => {
                    var graph = 0
                    if (localDataStore && localDataStore.graphs) {
                      graph = Object.keys(localDataStore.graphs).filter(e => e !== '__jsogObjectId'  && e !== 'update_time')[0]
                    }
                    this.setState({
                      Display: 1,

                      DevianceMin: String(2),
                      DevianceMax: String(2),
                      Curve: graph,

                      freePens: this.state.freePens,
                      avaiblePens: this.state.freePens,
                      selectedPens: [],
                    })
                  }}/>
                  {this.state.BatchList?(
                  <Button Text={"Edit or Close Batch"} onPress={() => {
                    var batchName = Object.keys(this.state.BatchList)
                                      .filter(e => e !== '__jsogObjectId'  && e !== 'update_time' && 
                                              (!this.state.BatchList[e].state || this.state.BatchList[e].state !== "Closed")
                                      )[0]
                    var batch = this.state.BatchList[batchName]
                    if (batch) {
                      var Pens = []
                      var pens = sortObject(batch.pens)
                      Object.keys(pens).forEach(element => {
                        if (!batch.pens[element].enddate) {
                          if (localDataStore.pens && localDataStore.pens[element]){
                            var _pen = localDataStore.pens[element]
                            var item = {}
                            item.item = element;
                            item.id = _pen.section + "_" + element;
                            Pens.push(item)
                          }
                        }
                      });

                      this.setState({
                        Display: 2,
                        BatchName: batchName,
                        StartD: batch.time,
                        Curve: batch.gcurve,
                        TargetWeight: String(batch.datas.target),
                        DevianceMin: String(batch.datas.devianceMin),
                        DevianceMax: String(batch.datas.devianceMax),
                        avaiblePens: this.state.freePens.concat(Pens),
                        selectedPens: Pens
                      })
                    }
                  }}/>):(<></>)}
                </View>
              </View>
              //#endregion
            ):(
              //#region Edit or create
              <View style={[Helpers.contentContainer, Helpers.fullSize, {marginHorizontal:0, paddingHorizontal:-20}]}>
                {this.state.Display === 1?( // Create
                  <Input label={"BatchName"}
                    containerStyle={[{borderWidth:1, marginTop:10}]}
                    style={[ApplicationStyles.input, ]}
                    onChangeText={text => {
                      if (text){
                        if (DataStringCheck(text)){
                          this.setState({BatchName: text})
                        }
                      }else {
                        this.setState({BatchName: text})
                      }
                    }}
                    value={
                      this.state.BatchName
                    }
                  />
                ):( // Edit
                  <View style={[Helpers.borderContainer, Helpers.fullWidth, {height:60, borderRadius:0, alignItems:"center"}]}>
                    <Picker
                      mode="dropdown"
                      selectedValue={this.state.BatchName}
                      style={[Helpers.fullWidth, { height: 50, justifyContent:"flex-end"}]}
                      onValueChange={(itemValue, itemIndex) => {
                        if (this.state.BatchList && this.state.BatchList[itemValue]) {
                          var batch = this.state.BatchList[itemValue]

                          var Pens = []
                          var pens = sortObject(batch.pens)
                          Object.keys(pens).forEach(element => {
                            if (!batch.pens[element].enddate) {
                              if (localDataStore.pens && localDataStore.pens[element]){
                                var _pen = localDataStore.pens[element]
                                var item = {}
                                item.item = element;
                                item.id = _pen.section + "_" + element;
                                Pens.push(item)
                              }
                            }
                          });

                          this.setState({
                            BatchName: itemValue,
                            StartD: batch.time,
                            Curve: batch.gcurve,
                            DevianceMin: String(batch.datas.devianceMin),
                            DevianceMax: String(batch.datas.devianceMax),
                            avaiblePens: this.state.freePens.concat(Pens),
                            selectedPens: Pens,
                          })
                        }
                      }}
                    >
                      {Object.keys(this.state.BatchList)
                                      .filter(e => e !== '__jsogObjectId'  && e !== 'update_time' && 
                                              (!this.state.BatchList[e].state || this.state.BatchList[e].state !== "Closed")
                                      ).map((graph, i) => {
                        return (<Picker.Item label={graph} value={graph} key={i}/>)
                      })}
                    </Picker>
                  </View>
                )}
                <View style={[Helpers.fullWidth, App?{}:Helpers.row, {zIndex:999}]}>
                  <View style={[App?{}:Helpers.halvWidth, {borderWidth:1 , marginTop:10, paddingHorizontal:5, paddingTop:0, borderRadius:0, alignItems:"center", zIndex:1}]}>
                    <View style={[Helpers.fullWidth, {alignItems:"flex-start", marginBottom: -5}]}>
                      <Text style={[ApplicationStyles.text, {marginHorizontal:5, marginTop:2, color:Colors.input, fontSize: 15, fontWeight: "bold"}]}>Start date:</Text>
                    </View>
                    <View style={[Helpers.fullWidth, App?{marginBottom:10}:{},{alignItems:'flex-start',}]}>
                      <Datetime_Picker Selected={this.state.StartD}
                        onValueChange={(date)=>{
                          this.setState({
                            StartD: date.getTime()
                          })
                      }}/>
                    </View>
                  </View>
                  
                  <View style={[App?{}:Helpers.halvWidth, {borderWidth:1, marginTop:10, paddingHorizontal:5, paddingTop:0, borderRadius:0, alignItems:"center", zIndex:1}]}>
                    <View style={[Helpers.fullWidth, {alignItems:"flex-start", marginBottom: -5}]}>
                      <Text style={[ApplicationStyles.text, {marginHorizontal:5, marginTop:2, color:Colors.input, fontSize: 15, fontWeight: "bold"}]}>Growth curve:</Text>
                    </View>
                    <View style={[Helpers.fullWidth, {alignItems:"center"}]}>
                      <Picker
                        mode="dropdown"
                        selectedValue={this.state.Curve}
                        style={[Helpers.fullWidth, {height: 30}]}
                        onValueChange={(itemValue, itemIndex) => {
                          this.setState({
                            Curve: itemValue
                          })
                        }}
                      >
                        {Object.keys(localDataStore.graphs).filter(e => e !== '__jsogObjectId'  && e !== 'update_time').map((graph, i) => {
                          return (<Picker.Item label={graph} value={graph} key={i}/>)
                        })}
                      </Picker>
                    </View>
                  </View>
                </View>
                <View style={[Helpers.fullWidth, Helpers.row]}>
                <Input label={"Target weight"}
                    type='number'
                    step="0.1"

                    containerStyle={[Helpers.halvWidth,{borderWidth:1, marginTop:10}]}
                    style={[ApplicationStyles.input, {marginBottom:-10}]}
                    onChangeText={text => {
                      if (!isNaN(text) || text.endsWith('.') && text.split('.').length <= 2) {
                        this.setState({
                          TargetWeight: text
                        })
                      }
                    }}
                    value={
                      this.state.TargetWeight
                    }
                  />
                </View>
                <View style={[Helpers.fullWidth, Helpers.row]}>
                  <Input label={"Deviance Min alert procentage"}
                    type='number'
                    step="0.1"

                    containerStyle={[Helpers.halvWidth,{borderWidth:1, marginTop:10}]}
                    style={[ApplicationStyles.input, {marginBottom:-10}]}
                    onChangeText={text => {
                      if (!isNaN(text) || text.endsWith('.') && text.split('.').length <= 2) {
                        this.setState({
                          DevianceMin: text
                        })
                      }
                    }}
                    value={
                      this.state.DevianceMin
                    }
                  />
                  <Input label={"Deviance Max alert procentage"}
                    type='number'
                    step="0.1"

                    containerStyle={[Helpers.halvWidth,{borderWidth:1, marginTop:10}]}
                    style={[ApplicationStyles.input, {marginBottom:-10}]}
                    onChangeText={text => {
                      if (!isNaN(text) || text.endsWith('.') && text.split('.').length <= 2) {
                        this.setState({
                          DevianceMax: text
                        })
                      }
                    }}
                    value={
                      this.state.DevianceMax
                    }
                  />
                </View>
                <View style={[Helpers.fullWidth, {borderWidth:1, marginTop:10, paddingHorizontal:10, paddingTop:5}]}>
                  <SelectBox
                    label="Pens in Batch.."
                    labelStyle={{color:Colors.input, fontSize: 15, fontWeight: "bold"}}
                    optionsLabelStyle={{minWidth:100, color:"black", fontSize: 12, fontWeight: "bold"}}

                    options={this.state.avaiblePens}
                    selectedValues={this.state.selectedPens}
                    onMultiSelect={this.onMultiChange}
                    onTapClose={this.onMultiChange}
                    isMulti
                  />
                </View>
                <View style={[Helpers.buttonContainer, Helpers.borderContainer, Helpers.midWidth, {marginTop:10}]}>
                  {this.state.Display === 2?(
                    <Button Text={"Close Batch"} onPress={() => {
                      var Batch = localDataStore.batchs[this.state.BatchName];
                      if (Batch) {
                        var {StartDate, EndDate, TargetWeight, PenCount, Animals, AverageWeight, AverageGrowth, isClosed, isVirtual} = GetStats_FromBatch(Batch, localDataStore)
                        if (Animals){
                          this.props.setViewError("The batch still contains active pens, please clear pens before closing the batch")
                        }else {
                          var newBatch = {name:this.state.BatchName, time:StartDate, state:"Closed"}
                          this.props.updateBatch(newBatch)
                          this.setState({
                            Display: 0
                          })
                          this.props.getPens(localDataStore.pens.update_time)
                        }
                      }
                    }}/>
                  ):(
                    <></>
                  )}
                  <Button Text={"Save"} onPress={() => {
                    var update = true;

                    if (this.state.BatchName !== "") {
                      var Name = this.state.BatchName;
                      if (!Object.keys(this.state.BatchList).includes(Name)){
                        if (this.state.Display === 2) { // update on non existing
                          update = false 
                          this.props.setViewError("BatchName error, batch does not exists")
                        }
                      } 
                      else {
                        if (this.state.Display === 1) { // create on allready existing
                          update = false 
                          this.props.setViewError("BatchName error, batch allready exists")
                        }
                      }
                    } else {
                      update = false 
                      this.props.setViewError("BatchName error")
                    }

                    if (this.state.StartD !== "") {
                      /*var StartD = this.state.StartD.split(".")
                      if (StartD.length !== 3 || StartD[0].length !== 4 || isNaN(StartD[0]) || 
                                                (StartD[1].length <= 0 && StartD[1].length >= 3) || isNaN(StartD[1]) ||
                                                (StartD[2].length <= 0 && StartD[2].length >= 3)|| isNaN(StartD[2])) {
                        update = false 
                        this.props.setViewError("Startdate error")
                      }*/
                    }else {
                      update = false 
                      this.props.setViewError("Startdate error")
                    }

                    if (this.state.TargetWeight !== "") {
                      var Dev = this.state.TargetWeight
                      if (Dev.length > 3 || isNaN(Dev)) {
                        update = false 
                      }
                    }else {
                      update = false 
                      this.props.setViewError("Tartget weight setting error")
                    }

                    if (this.state.DevianceMin !== "") {
                      var Dev = this.state.DevianceMin
                      if (Dev.length > 3 || isNaN(Dev) || Dev > 100){
                        update = false 
                      }
                    }else {
                      update = false 
                      this.props.setViewError("DevianceMin setting error")
                    }

                    if (this.state.DevianceMax !== "") {
                      var Dev = this.state.DevianceMax
                      if (Dev.length > 3 || isNaN(Dev) || Dev > 100){
                        update = false 
                      }
                    }else {
                      update = false 
                      this.props.setViewError("DevianceMax setting error")
                    }

                    if (this.state.Curve === ""){
                      update = false 
                      this.props.setViewError("Growth curve setting error, please reselect a curve")
                    }

                    var Pens = {}
                    if (this.state.selectedPens && this.state.selectedPens.length > 0) {
                      this.state.selectedPens.forEach(element => {
                        Pens[element.item] = true
                      });
                    }
                    if (Object.keys(Pens).length !== this.state.selectedPens.length) {
                      update = false
                      this.props.setViewError("An error occurred during Pen assigning (please update the page)")
                    }

                    if (update) {
                      this.props.setViewLoading(true)
                      var _Date = new Date(this.state.StartD)
                      var StartD = new Date(_Date.getFullYear(), _Date.getMonth(), _Date.getDate()).getTime()
                      var newBatch = {name:this.state.BatchName, gcurve:this.state.Curve, pens:Pens, time: StartD, datas:{target: this.state.TargetWeight, devianceMin: this.state.DevianceMin, devianceMax: this.state.DevianceMax}}
                      if (this.state.Display === 1) {
                        this.props.setBatch(newBatch)
                      }
                      else if (this.state.Display === 2) {
                        this.props.updateBatch(newBatch)
                      }
                      this.setState({
                        Display: 0
                      })
                      this.props.getPens(localDataStore.pens.update_time)
                      this.props.setViewError(null)
                      this.props.setViewLoading(false)
                    }
                  }}/>
                </View>
              </View>
              //#endregion
            )}
          </View>
          {(IsLoading || this.props.firebase.viewIsLoading) &&
            <ActivityIndicator style={Helpers.loadingContainer} size='large' />
          }
        </View>
      </ScrollView>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    getCurve: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.GRAPH, {})),

    getSections: (DataTime) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.SECTIONS, { DataTime: DataTime})),
    getPens: (DataTime) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.PENS, {DataTime: DataTime})),
    getBatch: (DataTime) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.BATCHS, {DataTime: DataTime})),

    setBatch: (batch) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.BATCHS, {Batch: batch})),
    updateBatch: (batch) => dispatch(FirebaseActions.actionStarting(Actions.UPDATE, DataRefs.BATCHS, {BatchId: batch.name, Batch: batch})),
    
    setCurrentData: (batchId, curveId) => dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, batchId:batchId, curveId: curveId})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Batchs);
//export default Batchs;
