import React, { useEffect, useState, Component } from 'react'
import { Platform, Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, Dimensions, SafeAreaView, FlatList, Picker } from 'react-native'
import { Input, Icon } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, DataStringCheck } from '../../../Stores/Firebase/Selectors'

import { Button, TitleElement, CustomListItem, ContentProp } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Images } from '../../../Theme'
import CurveElement from './CurveElement'

class GrowthCurve extends Component {
  constructor(props){
    super(props);

    const {CurveList} = this.setData(props)
    var D = new Date()
    this.state = {
      CurveList: CurveList,

      Display: 0,

      CurveName: "",
      DataPoints: [],
      Periode: "Days",

      Selected: {dataindex:null, index:null, error:false}
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;

    var CurveList = localDataStore.graphs
    return { CurveList }
  }

  getData(){
    this.props.getCurve()
  }

  checkData(props){
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        if (this.props.firebase.localDataStore.graphs && JSON.stringify(this.state.CurveList)!==JSON.stringify(this.props.firebase.localDataStore.graphs)) {
          const {CurveList} = this.setData(this.props)
          this.setState({
            CurveList: CurveList,
          })
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ErrorMessage = this.props.firebase.viewError

    var AverageGrowth = 0
    for(var i = 0; i < this.state.DataPoints.length; i++) {
      if (!this.state.DataPoints[i] || !this.state.DataPoints[i].hasOwnProperty("value")) continue;
      var value = this.state.DataPoints[i].value
      if (value === "" || value === null) value = 0
      value = parseFloat(value)

      for(var i2 = i-1; i2 >= 0; i2--) {
        if (this.state.DataPoints[i2] && this.state.DataPoints[i2].hasOwnProperty("value")){
          var Growth = value - this.state.DataPoints[i2].value
          AverageGrowth = (AverageGrowth + Growth) / 2
          break;
        }
      }
    }
    AverageGrowth = AverageGrowth.toFixed(3)

    var App = Platform.OS === "android" || Platform.OS === "ios" ? true: false
    return(
      <ScrollView style={[Helpers.fullSize, Helpers.fill]} nestedScrollEnabled={true}>
        <View
          style={[
            Helpers.mainContainer,
          ]}
        >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
          {ErrorMessage ? (
            <Text style={ApplicationStyles.error}>{ErrorMessage}</Text>
          ):(<></>)}
          {this.state.Display === 0?(
            <View style={[Helpers.contentContainer, Helpers.fullSize, {marginHorizontal:0, paddingHorizontal:-20}]}>
            {this.state.CurveList ? (
              <View style={[Helpers.fill, Helpers.fullWidth, Metrics.smallHorizontalPadding, Metrics.smallVerticalPadding, {maxWidth:800, marginTop:10}]}>
                <FlatList nestedScrollEnabled={true}
                  data={Object.keys(this.state.CurveList).filter(e => e !== '__jsogObjectId' && e !== 'update_time')}
                  renderItem={({item})=>{
                    return(
                      <Button Text={item} onPress={() => {
                        var CurveData = this.state.CurveList[item]

                        var DataPoints = []
                        if (!CurveData || !CurveData.hasOwnProperty("data")){
                          this.props.viewError("DataFetch error, please try to refresh")
                        }else {
                          var _index = 0;
                          Object.keys(CurveData.data).filter(e => e !== '__jsogObjectId').map((index) => {
                            DataPoints[index] = { value: CurveData.data[index]}
                            for(var i = index-1; i > _index;i--){
                              DataPoints[i] = { value:null}
                            }
                            _index = index
                          })
                        }
                        this.setState({
                          Display: 2,
                          CurveName: item,
                          DataPoints: DataPoints,
                          Periode: CurveData.periode
                        })
                      }}/>
                    )
                  }}
                  keyExtractor={(item, index) => index.toString()}
                />
              </View>
            ):(<></>)}
            <View style={[Helpers.buttonContainer, Helpers.borderContainer, Helpers.midWidth]}>
              <Button Text={"New Curve"} onPress={() => {
                this.setState({
                  Display: 1,
                  DataPoints: [{value:"0"},{value:"0"}]
                })
              }}/>
            </View>
          </View>
          ):(
            //#region Edit or create
            <View style={[Helpers.contentContainer, Helpers.fullSize, {marginHorizontal:0, paddingHorizontal:-20}]}>
              {this.state.Display === 1?( // Create
                <Input label={"CurveName"}
                  containerStyle={[{borderWidth:1, marginTop:10}]}
                  style={[ApplicationStyles.input, ]}
                  onChangeText={text => {
                    if (text){
                      if (DataStringCheck(text)){
                        this.setState({CurveName: text})
                      }
                    }else {
                      this.setState({CurveName: text})
                    }
                  }}
                  value={
                    this.state.CurveName
                  }
                />
              ):( //Edit
                <TitleElement Text={this.state.CurveName}/>
              )}
              <View style={[Helpers.fill, Helpers.fullWidth]}>
                <ContentProp texts={["Periode: " + this.state.Periode]} datas={["Average Growth: " + AverageGrowth]} />
                <View style={[Helpers.borderContainer, Helpers.fillCenter, Helpers.fullWidth, {alignSelf:'center'}]}>
                  <CurveElement Display={true} DataPoints={this.state.DataPoints} Height={200} Width={455}/>
                </View>
                <View>
                  <FlatList nestedScrollEnabled={true}
                    data={this.state.DataPoints}
                    style={[App?styles.AppStyle:styles.WebStyle]}
                    renderItem={({item, index}) => {
                      if (!item || item.value === null || item.value === undefined) return null;
                      var Selected = index===this.state.Selected.dataindex
                      return(
                        <View style={[Helpers.fullWidth, Helpers.row]} 
                          onFocus={() => {
                            this.setState({
                              Selected: {dataindex:index, index:index, error:false}
                            })
                          }}
                          onBlur={() => {
                            this.setState({
                              Selected: {dataindex:null, index:null, error:false}
                            })
                          }}
                        >
                          <Input label={"Day:"}
                            type='number'
                            step="0.1"

                            containerStyle={[{width:120, borderWidth:1, marginTop:10, marginRight:10}]}
                            style={[ApplicationStyles.input, {width:100}]}
                            
                            errorMessage={Selected&&this.state.Selected.error?"Value error":""}
                            editable={index===0?false:true}
                            onChangeText={text => {
                              if (!isNaN(text)) {
                                var _Selected = this.state.Selected
                                _Selected.index = text
                                _Selected.error = false
                                if (this.state.DataPoints[text] && this.state.DataPoints[text].hasOwnProperty("value") && this.state.DataPoints[text].value) {
                                  _Selected.error = true
                                }
                                this.setState({
                                  Selected: _Selected
                                });
                              }
                            }}

                            onBlur={() => {
                              if (!this.state.Selected.error && this.state.Selected.dataindex !== this.state.Selected.index) {
                                var DataPoints = [ ...this.state.DataPoints ];
                                DataPoints[this.state.Selected.index] = {value: item.value.toString()};
                                DataPoints[this.state.Selected.dataindex].value = null

                                var FirstData = false
                                for(var i = DataPoints.length-1; i>1;i--) {
                                  if (!DataPoints[i] || !DataPoints[i].hasOwnProperty("value") || !DataPoints[i].value) {
                                    if (FirstData) {
                                      DataPoints[i] = {value: null}
                                    }
                                    else DataPoints.pop()
                                  } else {
                                    FirstData = true;
                                  }
                                }
                                this.setState({DataPoints});
                              }
                            }}

                            value={
                              "" + (Selected?this.state.Selected.index:index)
                            }
                          />
                          <Input label={"Weight:"}
                            type='number'
                            step="0.1"

                            containerStyle={[Helpers.fill,{borderWidth:1, marginTop:10}]}
                            style={[ApplicationStyles.input]}
                            
                            onChangeText={text => {
                              if (!isNaN(text) || text.endsWith('.') && text.split('.').length <= 2) {
                                var DataPoints = [ ...this.state.DataPoints ];
                                DataPoints[index].value = text;
                                this.setState({DataPoints});
                              }
                            }}

                            onBlur={(e:any) => {
                              var text = e.nativeEvent.text
                              if (text === undefined || text === null || text === "") {

                                var DataPoints = [ ...this.state.DataPoints ];
                                DataPoints[index].value = "0";
                                this.setState({DataPoints});
                              }
                            }}

                            value={
                              "" + item.value
                            }
                          />
                        </View>
                      )
                    }}
                    keyExtractor={(item, index) => index.toString()}
                    //Setting the number of column
                    numColumns={1}
                  />
                </View>
                <View style={{height:20}} />
                <Button Text={"New Point"} onPress={() => {
                  var DataPoints = [ ...this.state.DataPoints ];
                  var value = this.state.DataPoints&&this.state.DataPoints.length >= 1?this.state.DataPoints[this.state.DataPoints.length-1].value:"0"
                  DataPoints.push({value:value})
                  this.setState({
                    DataPoints
                  })
                }}/>
              </View>

              <View style={[Helpers.buttonContainer, Helpers.borderContainer, Helpers.midWidth, {marginTop:10}]}>
                <Button Text={"Save"} onPress={() => {
                  var update = true;

                  if (this.state.CurveName !== ""){
                    var Name = this.state.CurveName;
                    if (!Object.keys(this.state.CurveList).includes(Name)) {
                      if (this.state.Display === 2) { // update on non existing
                        this.props.setViewError("Cannot update non-existing Curve. (Please refresh)")
                        update = false 
                      }
                    } 
                    else {
                      if (this.state.Display === 1) { // create on allready existing
                        this.props.setViewError("Cannot create allready existing Curve.")
                        update = false 
                      }
                    }
                  }else {
                    this.props.setViewError("Curve template need a name.")
                    update = false
                  }

                  var lastDataIndex = 0
                  this.state.DataPoints.forEach((element,index) => {
                    if (update && element.value !== null && element.value !== undefined){
                      if (index > 0 && parseInt(element.value) <= parseInt(this.state.DataPoints[lastDataIndex].value)) {
                        var Eindex = lastDataIndex
                        if (element.value === this.state.DataPoints[lastDataIndex].value) Eindex = index
                        this.props.setViewError("Curve template needs to be incremental: error at day: " + Eindex)
                        update = false;
                      } else {
                        lastDataIndex = index
                      }
                    }
                  })

                  if (update) {
                    this.props.setViewError(null)
                    this.props.setViewLoading(true)
                    var newCurve = {name:this.state.CurveName, periode:this.state.Periode}
                    var Data = []
                    var index = 0;
                    this.state.DataPoints.forEach(element => {
                      if (element.value) Data[index] = parseFloat(element.value)
                      index++;
                    });
                    newCurve.data = Data;
                    this.props.updateCurve(newCurve)
                    
                    this.setState({
                      Display: 0
                    })
                  }
                }}/>
              </View>
            </View>
            //#endregion
          )}
        </View>
        {(IsLoading || this.props.firebase.viewIsLoading) &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
      </ScrollView>
    )
  }
}

const styles = StyleSheet.create({
  AppStyle: {
    
  },
  WebStyle: {
    height:400
  },
});

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    getCurve: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.GRAPH, {GraphId: ""})),
    updateCurve: (curve) => dispatch(FirebaseActions.actionStarting(Actions.UPDATE, DataRefs.GRAPH, {Graph: curve})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(GrowthCurve);
//export default GrowthCurve;
