import React from 'react';
import { StyleSheet, View, TouchableOpacity, Text} from 'react-native';
import { ListItem } from 'react-native-elements'

import { ApplicationStyles, Helpers, Metrics, Fonts, Colors } from '../Theme'

const CustomListItem = props => {
  const key = props.key? props.key : 0
  return (
    <ListItem key={key} containerStyle={[styles.listitem, {flexDirection:"column", borderRadius:0}, props.itemStyle]} button onPress={props.onPress}>
      <ListItem.Content>
        <ListItem.Title>{props.Title}</ListItem.Title>
        {props.hasOwnProperty("Subtitle") ? <ListItem.Subtitle>{props.Subtitle}</ListItem.Subtitle>
        :(<></>)}
      </ListItem.Content>
    </ListItem>
  )
};

const styles = StyleSheet.create({
  listitem: {
    flex: 1,
    ...Helpers.borderContainer,
    ...Helpers.center,
    backgroundColor: '#fff',
    paddingVertical: 5,
    paddingHorizontal: 5,
  },
});

export default CustomListItem;