import React, { Component } from 'react'
import { Platform, Text, View, ScrollView, ActivityIndicator } from 'react-native'
import { Input } from 'react-native-elements'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, UserStates, DataRefs, ErrorTypes, DataStringCheck } from '../../../Stores/Firebase/Selectors'

import { Button, TitleElement } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../../Theme'

class UserInformation extends Component{
  constructor(props) {
    super(props);

    const {FirstN, LastN, Contact, ContactP, Address} = this.setData(props)
    this.state = {
      FirstN: FirstN,
      LastN: LastN,
      Contact: Contact,
      ContactP: ContactP,
      Address: Address,
      Country: "",
    }
  }

  setData(Props) {
    var FirstN = ""
    var LastN = ""
    var Contact = ""
    var ContactP = ""
    var Address = ""

    const firebaseData = this.props.firebase;
    if (firebaseData){
      const firebaseUser = firebaseData.firebaseUser
      if (firebaseUser) {
        if (firebaseUser.name && DataStringCheck(firebaseUser.name)){
          FirstN = firebaseUser.name
        }
        if (firebaseUser.lastN){
          LastN = firebaseUser.lastN
        }
        if (firebaseUser.contact){
          Contact = firebaseUser.contact
        }
        if (firebaseUser.phoneNumber) {
          ContactP = firebaseUser.phoneNumber
        }
        if (firebaseUser.address){
          Address = firebaseUser.address
        }
      }
    }
    return {FirstN, LastN, Contact, ContactP, Address}
  }

  getData() {
    
  }

  checkData(props) {
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)

    this.props.getUser(null)

    this.checkData(this.props)
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);

      if (this.props.firebase.firebaseUser) {
        if ((this.props.firebase.firebaseUser.name != this.state.FirstN && this.state.FirstN === "") ||
            (this.props.firebase.firebaseUser.lastN != this.state.lastN && this.state.lastN === "") ||
            (this.props.firebase.firebaseUser.contact != this.state.Contact && this.state.Contact === "") ||
            (this.props.firebase.firebaseUser.address != this.state.Address && this.state.Address === "")
          ) {
          const {FirstN, LastN, Contact, ContactP, Address} = this.setData(this.props)
          this.setState({
            FirstN: FirstN,
            LastN: LastN,
            Contact: Contact,
            ContactP: ContactP,
            Address: Address,
          })
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ErrorMessage = this.props.firebase.viewError

    //TODO: Test more with input error display ??
    const F_FirstN = React.createRef();
    const F_LastN = React.createRef();
    const F_Contact = React.createRef();
    const F_ContactP = React.createRef();
    const F_Address = React.createRef();

    return(
      <ScrollView style={[Metrics.verticalMargin, {paddingRight: 5}]}>
        <View
          style={[
            Helpers.mainContainer,
          ]}
        >
          <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
            {ErrorMessage && (!ErrorMessage.type || ErrorMessage.type === ErrorTypes.SYSTEM) ? (
              <Text style={ApplicationStyles.error}>{ErrorMessage.message?ErrorMessage.message:ErrorMessage}</Text>
            ):(<></>)}
            <TitleElement Text="User Informations"/>
            <View style={ [Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
              <Input ref={F_FirstN}
                label="First Name"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (text){
                    if (DataStringCheck(text)){
                      this.setState({FirstN:text})
                    }
                  }else {
                    this.setState({FirstN:text})
                  }
                }}
                value={this.state.FirstN}

              />
              <Input ref={F_LastN}
                label="Last Name"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (text){
                    if (DataStringCheck(text)){
                      this.setState({LastN:text})
                    }
                  }else {
                    this.setState({LastN:text})
                  }
                }}
                value={this.state.LastN}
              />
              <Input ref={F_Contact}
                label="Contact Email"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (text){
                    var test = text
                    test = test.replace('@','a')
                    test = test.replace('.','d')
                    if (DataStringCheck(test)) {
                      this.setState({Contact:text})
                    }
                  }else {
                    this.setState({Contact:text})
                  }
                }}
                value={this.state.Contact}
              />
              <Input ref={F_ContactP}
                label="Contact Phone"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (text){
                    if (DataStringCheck(text, false)){
                      this.setState({ContactP:text})
                    }
                  }else {
                    this.setState({ContactP:text})
                  }
                }}
                value={this.state.ContactP}
              />
              <Input ref={F_Address}
                label="Address"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (text){
                    if (DataStringCheck(text)){
                      this.setState({Address:text})
                    }
                  }else {
                    this.setState({Address:text})
                  }
                }}
                value={this.state.Address}
              />
              <Input
                label="Country"
                style={ApplicationStyles.input}
                onChangeText={text => {
                  if (text){
                    if (DataStringCheck(text)){
                      this.setState({Country:text})
                    }
                  }else {
                    this.setState({Country:text})
                  }
                }}
                value={this.state.Country}
              />
            </View>
            <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
              <Button Text="Save User information" 
                onPress={() => {
                  var _UserData = {}

                  if (!this.state.FirstN || this.state.FirstN === ""){
                    this.props.setViewError("First name is needed")
                    /*F_FirstN.current.setNativeProps({ errorProps:"First name is needed" })
                    F_FirstN.current.shake();*/
                  } else if (!DataStringCheck(this.state.FirstN)) {
                    this.props.setViewError("First name contains invalid characters")
                    /*F_FirstN.current.setNativeProps({ errorProps: "First name contains invalid characters" })
                    F_FirstN.current.shake();*/
                  }
                  
                  else if (!this.state.LastN || this.state.LastN === ""){
                    this.props.setViewError("Last name is needed")
                  } else if (!DataStringCheck(this.state.LastN)) {
                    this.props.setViewError("Last name contains invalid characters")
                  }
                  
                  else {
                    _UserData = {name: this.state.FirstN, lastN: this.state.LastN}
                    if (this.state.Contact !== "") _UserData.contact = this.state.Contact
                    if (this.state.ContactP !== "") _UserData.phone = this.state.ContactP
                    if (this.state.Address !== "") _UserData.address = this.state.Address
                    if (this.state.Country !== "") _UserData.country = this.state.Country

                    this.props.setViewLoading(true)
                    this.props.updateUser(_UserData)
                    if (this.props.OnDone) {
                      this.props.OnDone(true)
                    }
                  }
                }}/>
              </View>
          </View>
          {(IsLoading || this.props.firebase.viewIsLoading) &&
            <ActivityIndicator style={Helpers.loadingContainer} size='large' />
          }
        </View>
      </ScrollView>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    //setViewError: (error, type) => dispatch(FirebaseActions.setViewError(error, type)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    getUser: (UserId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.USERSDATA, {UserId: UserId})),
    updateUser: (Data) => dispatch(FirebaseActions.actionStarting(Actions.UPDATE, DataRefs.USERSDATA, {Data: Data}))
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(UserInformation);
//export default UserInformation;