import axios from 'axios'
import { Config, GetToken_URL, GetCheckToken_URL, GetConsumtionData_URL } from '../Config'
import { is, curryN, gte } from 'ramda'

const isWithin = curryN(3, (min, max, value) => {
  const isNumber = is(Number)
  return isNumber(min) && isNumber(max) && isNumber(value) && gte(value, min) && gte(max, value)
})
const in200s = isWithin(200, 299)

function Create_Axios(baseURL, Acc_Token = null) {
  var Consys_ApiClient;
  if (Acc_Token){
    Consys_ApiClient = axios.create({
      /**
       * Import the config from the App/Config/index.js file
       */
      baseURL: baseURL,
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
        'Authorization': `Bearer ${Acc_Token}`
      },
      timeout: 3000,
    })
  }else {
    Consys_ApiClient = axios.create({
      /**
       * Import the config from the App/Config/index.js file
       */
      baseURL: baseURL,
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
      timeout: 3000,
    })
  }
  return Consys_ApiClient
}

function getAccessToken(SiteUrl, Ref_Token){
  return new Promise((resolve, reject) => {
    Create_Axios(GetToken_URL(SiteUrl, Ref_Token)).get().then((response) => {
      if (response && response.data && response.data.access_token) {
        resolve(response.data.access_token)
      }else {
        resolve(null)
      }
    })
  })
}

function checkRenew_AccessToken(SiteUrl, Ref_Token, Acc_Token=null) {
  return new Promise((resolve, reject) => {
    if (Acc_Token) {
      Create_Axios(GetCheckToken_URL(SiteUrl),Acc_Token).get().then((response) => {
        if (response.status === 200){
          resolve(Acc_Token)
        } else {
          resolve(getAccessToken(SiteUrl, Ref_Token))
        }
      })
    }else {
      resolve(getAccessToken(SiteUrl, Ref_Token))
    }
  })
}

function fetch_ValveConsumption(SiteUrl, Ref_Token, limit, SystemId=1, starttime=null, stoptime=null, offset=null, lastid=null, Acc_Token=null){
  return new Promise((resolve, reject) => {
    if (!SystemId) SystemId = 1
    checkRenew_AccessToken(SiteUrl, Ref_Token, Acc_Token).then((Token)=>{
      if (Token) {
        var baseURL = GetConsumtionData_URL(SiteUrl, 100, SystemId, offset, limit, starttime, stoptime, lastid)
        console.log("fetch", baseURL, Token)
        Create_Axios(baseURL, Token).get().then((response)=>{
          if (response && response.data) {
            var Feed_Data = {}
            var ResData = response.data
            if (ResData.result === "OK"){
              //console.log(ResData)
              if (ResData.consumptiondata) {
                ResData.consumptiondata.forEach(element => {
                  var weight = element.amount /1000
                  Feed_Data[element.dataid] = {valve:element.valve, unit:element.unit, silono:element.silono, amount: weight, timestamp: element.timestamp}
                });
              }
            }

            resolve({data: Feed_Data, token:Token})
          } else {
            resolve(null)
          }
        }).catch(error => {
          console.log("fetch AutoPig Error: ", error);
          resolve(null)
        })
      }else {
        resolve(null)
      }
    })
  })
}

function fetch_AutoPig(SiteUrl, Ref_Token, limit, SystemId=1, starttime=null, stoptime=null, offset=null, lastid=null, Acc_Token=null) {
  return new Promise((resolve, reject) => {
    if (!SystemId) SystemId = 1
    checkRenew_AccessToken(SiteUrl, Ref_Token, Acc_Token).then((Token)=>{
      if (Token) {
        var baseURL = GetConsumtionData_URL(SiteUrl, 700, SystemId, offset, limit, starttime, stoptime, lastid)
        console.log("fetch", baseURL, Token)
        Create_Axios(baseURL, Token).get().then((response)=>{
          if (response && response.data) {
            var Scale_Data = {}
            var ResData = response.data
            if (ResData.result === "OK"){
              //console.log(ResData)
              if (ResData.consumptiondata) {
                ResData.consumptiondata.forEach(element => {
                  var weight = element.amount /1000
                  Scale_Data[element.dataid] = {amount: weight, timestamp: element.timestamp}
                });
              }
            }

            resolve({data: Scale_Data, token:Token})
          } else {
            resolve(null)
          }
        }).catch(error => {
          console.log("fetch AutoPig Error: ", error);
          resolve(null)
        })
      }else {
        resolve(null)
      }
    })
  })
}

export const consysService = {
  fetch_ValveConsumption,
  fetch_AutoPig,
}
