import React, { Component } from 'react'
import { Text, View, ActivityIndicator, Image } from 'react-native'
import { Input } from 'react-native-elements'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs } from '../../../Stores/Firebase/Selectors'

import { Button } from '../../../Components'
import Style from './AuthStyle'
import { ApplicationStyles, Helpers, Metrics, Images } from '../../../Theme'

class UserLoginScreen extends Component {
  constructor(props){
    super(props);
    this.state = {
      Email: "",
      Pass: "",
      CreateUser: false,
    }
  }

  getData() {
  }

  checkData(props) {
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);
      if (this.props.firebase.viewError != prevProps.firebase.viewError) {
        this.forceUpdate()
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ErrorMessage = this.props.firebase.viewError
    
    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
          {ErrorMessage ? (
            <Text style={ApplicationStyles.error}>{ErrorMessage}</Text>
          ):(<></>)}

          {!this.state.CreateUser?(
            <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
              <View style={[Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
                <Input
                  label="Username"
                  style={ApplicationStyles.input}
                  onChangeText={text => {
                    this.setState({Email: text})
                  }}
                  value={this.state.Email}
                />
                <Input
                  label="Password"
                  style={ApplicationStyles.input}
                  onChangeText={text => {
                    this.setState({Pass:text})
                  }}
                  value={this.state.Pass}
                  secureTextEntry={true}
                />
                <Button Text="Login" icon={Images.User}
                  onPress={() => {
                    if (!this.state.Email || this.state.Email === "") this.props.setViewError("Email is needed")
                    else if (this.state.Email === "CLEAR") this.props.clearDataMem()
                    else if (!this.state.Pass || this.state.Pass === "") this.props.setViewError("Password is needed")
                    else {
                      this.props.setViewLoading(true)
                      this.props.signIN(this.state.Email, this.state.Pass)
                    }
                  }}/>
              </View>

              <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
                <Button Text="Send reset password"
                  onPress={() => {
                    this.props.setViewError(null)
                    if (this.state.Email) {
                      this.props.setViewLoading(true)
                      this.props.resetPass(this.state.Email)
                    } else {
                      this.props.setViewError("Please provide an Email")
                    }
                  }}/>
                <Button Text="Create New User"
                  onPress={() => {
                    this.props.setViewError(null)
                    this.setState({
                      Email: "",
                      Pass: "",
                      CreateUser: true,
                    })
                  }}/>
              </View>
            </View>
          ):(
            <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
              <View style={ [Helpers.borderContainer, Helpers.midWidth, {marginBottom:20}] }>
                <Input
                  label="Email"
                  style={ApplicationStyles.input}
                  onChangeText={text => {this.setState({Email:text})}}
                  value={this.state.Email}
                />
                <Input
                  label="Password"
                  style={ApplicationStyles.input}
                  onChangeText={text => {this.setState({Pass:text})}}
                  value={this.state.Pass}
                  secureTextEntry={true}
                />
              </View>
              <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
                <Button Text="Create User" 
                  onPress={() => { 
                    if (!this.state.Email || this.state.Email === "") this.props.setFirebaseError("Email is needed")
                    else if (!this.state.Pass || this.state.Pass === "") this.props.setFirebaseError("Password is needed")
                    else {
                      this.props.setViewLoading(true)
                      this.props.createUser(this.state.Email,this.state.Pass)
                    }
                  }}
                />
              </View>
            </View>
          )}

          {(IsLoading || this.props.firebase.viewIsLoading) &&
            <ActivityIndicator style={Helpers.loadingContainer} size='large' />
          }
        </View>
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),
    clearDataMem: () => dispatch(FirebaseActions.actionStarting(Actions.CLEAR, DataRefs.USERSDATA)),

    signIN: (Email, Pass) => dispatch(FirebaseActions.actionStarting(Actions.SIGN, DataRefs.USERSDATA, {Email: Email, Pass: Pass})),
    resetPass: (Email) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.USERS, {Email: Email})),
    createUser: (Email, Pass) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.USERS, {Email: Email, Pass: Pass})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(UserLoginScreen);
//export default UserLoginScreen;