import React, { Component } from 'react'
import { StyleSheet, View, TouchableOpacity, Picker, Text, Image} from 'react-native';

import { ApplicationStyles, Helpers, Metrics, Fonts, Colors, Images } from '../Theme'

const monthNames = ["January", "February", "March", "April", "May", "June",
  "July", "August", "September", "October", "November", "December"
];

class DateTime_Picker extends Component {
  constructor(props) {
    super(props);
      
    const {year, month, day} = this.getStates(props)
    this.state = {
      year: year,
      month: month,
      day: day,
    }
  }

  getStates(props) {
    var Today = new Date();
    var Selected = new Date();
    if (props.Selected) {
      Selected = new Date(props.Selected)
    }

    var year, month, day;
    year = Selected.getFullYear()
    month = Selected.getMonth()
    day = Selected.getDate()

    if (!props.Selected) {
      if (props.Year){
        year = this.props.Year
      }
      if (props.Month){
        month = this.props.Month
      }
      if (props.Day){
        day = this.props.Day
      }
    }
    return {year, month, day}
  }

  componentDidUpdate(prevProps, prevState) {
    if (prevProps.Selected){
      if (prevProps.Selected != this.props.Selected){
        const {year, month, day} = this.getStates(this.props)
        this.setState({
          year: year,
          month: month,
          day: day,
        }, this.forceUpdate())
      }
    } else {
      var changed = false;
      if (prevProps.Year != this.props.Year) changed = true
      if (prevProps.Month != this.props.Month) changed = true
      if (prevProps.Day != this.props.Day) changed = true
      if (changed) {
        this.forceUpdate()
      }
    }
  }

  render() {
    var Today = new Date();

    var year = Today.getFullYear()
    var YearItems = []
    for (let index = year + 25; index > year - 25; index--) {
      var val = String(index)
      YearItems.push(<Picker.Item key={"Year_" + index} value={index} label={val} />)
    }
      
    var MonthItems = []
    var month = Today.getMonth()
    for (let index = 0; index < monthNames.length; index++) {
      var val = String(index)
      MonthItems.push(<Picker.Item key={"Month_" + index} value={index} label={monthNames[index]} />)
    }
  
    var DayItems = []
    var days = new Date(year, month + 1, 0).getDate()
    for (let index = 1; index <= days; index++) {
      var val = String(index)
      DayItems.push(<Picker.Item key={"Day_" + index} value={index} label={val} />)
    }

    return(
      <View style={[Helpers.fullWidth, Helpers.row, {marginBottom:-10,}, this.props.style]}>
        <Picker selectedValue={this.state.year} style={[{minWidth: 101, marginRight:5}]}
          onValueChange={(_year) => {
            year = _year
            this.props.onValueChange(new Date(year, this.state.month, this.state.day))
          }}>
          {YearItems}
        </Picker>

        <Picker selectedValue={this.state.month} style={[{minWidth: 110, marginRight:5}]}
          onValueChange={(_month) => {
            month = _month
            this.props.onValueChange(new Date(this.state.year, month, this.state.day))
          }}>
          {MonthItems}
        </Picker>

        <Picker selectedValue={this.state.day} style={[{minWidth: 83}]}
          onValueChange={(_day) => {
            days = parseInt(_day)
            this.props.onValueChange(new Date(this.state.year, this.state.month, days))
          }}>
          {DayItems}
        </Picker>
      </View>
    )
  }
}
export default DateTime_Picker;