import React, { Component } from 'react'
import { Platform, Dimensions, Text, CheckBox, View, ScrollView, ActivityIndicator, TouchableOpacity } from 'react-native'
import { Input } from 'react-native-elements'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, UserStates, DataRefs, ErrorTypes, DataStringCheck } from '../../../Stores/Firebase/Selectors'

import { Button, TitleElement } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'

const window = Dimensions.get("window");
const screen = Dimensions.get("screen");

class Subscription extends Component{
  constructor(props) {
    super(props);

    const {CurrentSub} = this.setData(props)
    this.state = {
      dimensions: {window, screen},
      CurrentSub: CurrentSub,

      Billing: 0,
    }
  }

  setData(Props) {
    var CurrentSub = ""

    const firebaseData = this.props.firebase;
    if (firebaseData){
      const firebaseUser = firebaseData.firebaseUser
      if (firebaseUser) {
        if (firebaseUser.state && firebaseUser.state != UserStates.STARTER){
          CurrentSub = firebaseUser.state
        }
      }
    }
    return { CurrentSub }
  }

  getData() {
    
  }

  checkData(props) {
  }

  onChange = ({ window, screen }) => {
    this.setState({
      dimensions: {window, screen},
    })
  };

  componentDidMount() {
    Dimensions.addEventListener("change", this.onChange);
    this.props.setViewLoading(false)
    this.props.setViewError(null)

    this.props.getUser(null)

    this.checkData(this.props)
    this.getData();
  }

  componentWillUnmount(){
    Dimensions.removeEventListener("change", this.onChange);
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);

      if (this.props.firebase.firebaseUser) {
        if ((this.props.firebase.firebaseUser.state != this.state.CurrentSub && this.state.CurrentSub === "")
          ) {
          const {CurrentSub} = this.setData(this.props)
          this.setState({
            CurrentSub: CurrentSub,
          })
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ErrorMessage = this.props.firebase.viewError

    var wide = this.state.dimensions.window.width>900?true:false
    var App = (Platform.OS === 'ios' || Platform.OS === 'android')||!wide
    
    return(
      <ScrollView style={[Metrics.verticalMargin, {paddingRight: 5}]}>
        <View
          style={[
            Helpers.mainContainer,
          ]}
        >
          <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
            {ErrorMessage && (!ErrorMessage.type || ErrorMessage.type === ErrorTypes.SYSTEM) ? (
              <Text style={ApplicationStyles.error}>{ErrorMessage.message?ErrorMessage.message:ErrorMessage}</Text>
            ):(<></>)}
            
            <TitleElement Text="Subscription plan"/>
            <View style={[!App?Helpers.fillRowCenter:{}, {marginBottom:20}]}>
              <TouchableOpacity style={[Helpers.borderContainer, {width:200, height:300}, this.state.CurrentSub===UserStates.WORKER?{backgroundColor: Colors.AgriGreen}:{}]} 
                onPress={() => {
                  this.setState({
                    CurrentSub: UserStates.WORKER
                  }, this.forceUpdate())
              }}>
                <View style={[Helpers.row, Helpers.fullWidth, {alignContent:'center', alignItems:'center', justifyContent:'center', height:35}]}>
                  <Text style={{fontWeight:"bold", fontSize:20}}>Farm Staff</Text>
                  {this.state.CurrentSub===UserStates.WORKER?(
                    <CheckBox 
                      value={this.state.CurrentSub===UserStates.WORKER?true:false}
                    />
                  ):(<></>)}
                </View>
                <View style={[Helpers.fill, Helpers.borderContainer]}>
                  
                </View>
                <View style={[Helpers.borderContainer, {height:75}]}>
                  <Text style={{fontWeight:"bold"}}>Free</Text>
                  <Text>Access to assign as staff of an existing farm</Text>
                </View>
              </TouchableOpacity>

              <TouchableOpacity style={[Helpers.borderContainer, {width:200, height:300}, this.state.CurrentSub===UserStates.FREE?{backgroundColor: Colors.AgriGreen}:{}]}
                onPress={() => {
                  this.setState({
                    CurrentSub: UserStates.FREE
                  }, this.forceUpdate())
              }}>
                <View style={[Helpers.row, Helpers.fullWidth, {alignContent:'center', alignItems:'center', justifyContent:'center', height:35}]}>
                  <Text style={{fontWeight:"bold", fontSize:20}}>Farm Owner</Text>
                  {this.state.CurrentSub===UserStates.FREE?(
                    <CheckBox 
                      value={this.state.CurrentSub===UserStates.FREE?true:false}
                    />
                  ):(<></>)}
                </View>
                <View style={[Helpers.fill, Helpers.borderContainer]}>
                  
                </View>
                <View style={[Helpers.borderContainer, {height:75}]}>
                  <Text style={{fontWeight:"bold"}}>Free</Text>
                  <Text>Access to create and manage own farm</Text>
                </View>
              </TouchableOpacity>

              <TouchableOpacity style={[Helpers.borderContainer, {width:200, height:300}, this.state.CurrentSub===UserStates.OWNER?{backgroundColor: Colors.AgriGreen}:{}]}
                onPress={() => {
                  this.setState({
                    CurrentSub: UserStates.OWNER
                  }, this.forceUpdate())
              }}>
                <View style={[Helpers.row, Helpers.fullWidth, {alignContent:'center', alignItems:'center', justifyContent:'center', height:35}]}>
                  <Text style={{fontWeight:"bold", fontSize:20}}>Farm Manager</Text>
                  {this.state.CurrentSub===UserStates.OWNER?(
                    <CheckBox 
                      value={this.state.CurrentSub===UserStates.OWNER?true:false}
                    />
                  ):(<></>)}
                </View>
                <View style={[Helpers.fill, Helpers.borderContainer]}>
                  
                </View>
                <View style={[Helpers.borderContainer, {height:75}]}>
                  <Text style={{fontWeight:"bold"}}>9999.99€</Text>
                  <Text>Access to manage own farm, and assing staff</Text>
                </View>
              </TouchableOpacity>

              <TouchableOpacity style={[Helpers.borderContainer, {width:200, height:300}, this.state.CurrentSub==="Contact"?{backgroundColor: Colors.AgriGreen}:{}]}
                onPress={() => {
                  this.setState({
                    CurrentSub: "Contact"
                  }, this.forceUpdate())
              }}>
                <View style={[Helpers.row, Helpers.fullWidth, {alignContent:'center', alignItems:'center', justifyContent:'center', height:35}]}>
                  <Text style={{fontWeight:"bold", fontSize:20}}>Get contacted</Text>
                  {this.state.CurrentSub==="Contact"?(
                    <CheckBox 
                      value={this.state.CurrentSub==="Contact"?true:false}
                    />
                  ):(<></>)}
                </View>
                <View style={[Helpers.fill, Helpers.borderContainer]}>
                  
                </View>
                <View style={[Helpers.borderContainer, {height:75}]}>
                <Text style={{fontWeight:"bold", height:20}}></Text>
                  <Text>We will contact you, and tell more about our offers</Text>
                </View>
              </TouchableOpacity>
            </View>

            <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
              <Button Text="Submit" 
                onPress={() => {
                  if (!this.state.CurrentSub || this.state.CurrentSub === ""){
                    this.props.setViewError("Please choose a subscription type.")
                  }

                  else if (this.props.OnDone) {
                    this.props.OnDone(this.state.CurrentSub)
                  } else {
                    //TODO: something.. validate buy and update user state
                  }
                }}/>
              </View>
          </View>
          {(IsLoading || this.props.firebase.viewIsLoading) &&
            <ActivityIndicator style={Helpers.loadingContainer} size='large' />
          }
        </View>
      </ScrollView>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    //setViewError: (error, type) => dispatch(FirebaseActions.setViewError(error, type)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    getUser: (UserId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.USERSDATA, {UserId: UserId})),

    //updateUser: (Data) => dispatch(FirebaseActions.actionStarting(Actions.UPDATE, DataRefs.USERSDATA, {Data: Data}))
    //createUserData: (Userdata, Farm, Sections, Pens) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.USERSDATA, {UserData: Userdata, Farms:{name:Farm}, Sections:Sections, Pens:Pens})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(Subscription);
//export default Subscription;