import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, sortObject, format_Time } from '../../../Stores/Firebase/Selectors'

import { Button, Button_Panel, ContentProp, OperationsPanel } from '../../../Components'

import { ApplicationStyles, Helpers, Metrics, Images, Colors } from '../../../Theme'

class DataDisplay_Pigs extends Component {
  constructor(props) {
    super(props);

    this.state = {
      currentData: props.currentData,
    }

    const {CurrentData, WeighingsList, Weighings} = this.setData(props)
    this.state = {
      currentData: CurrentData,

      WeighingsList: WeighingsList,
      Weighings: Weighings,
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;

    var CurrentData = this.state.currentData;
    var Pigs = sortObject(localDataStore.pigs)
    if (Pigs[CurrentData.TagId]){
      CurrentData.data = Pigs[CurrentData.TagId]
    }

    var WeighingsList = [];
    var Weighings = [];
    var App = Platform.OS === "android" || Platform.OS === "ios" ? true: false
    if (localDataStore && localDataStore.weighings) {
      if (localDataStore.weighings[CurrentData.penId]) {
        WeighingsList = localDataStore.weighings[CurrentData.penId];
        for(var i = 0; i < Object.keys(WeighingsList).length; i++) {
          var name = Object.keys(WeighingsList)[i]
          if (!name) continue;
          var object = WeighingsList[name];
          if (!object || !object.time) continue;
          Weighings.push({[name]:object})
        }
        Weighings = Weighings.sort((a, b) => (a.time > b.time) ? 1 : -1)
        if (App) {
          Weighings = Weighings.slice(0,5)
        }
      }
    }
    if (CurrentData.data && !CurrentData.data.datas) CurrentData.data.datas = {}

    return {CurrentData, WeighingsList, Weighings}
  }

  getData() {
    if (this.props.firebase && this.props.firebase.localDataStore && this.state.currentData.penId) {
      const localDataStore = this.props.firebase.localDataStore
      if (!localDataStore.pens) localDataStore.pens = {}
      this.props.getPens(localDataStore.pens.update_time)
      this.props.getWeights(this.state.currentData.penId)
      this.props.getBatch(null)
    }
  }

  checkData(props) {
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);

      if (this.props.firebase.localDataStore) {
        const localDataStore = this.props.firebase.localDataStore
        if ((JSON.stringify(prevProps.currentData)!==JSON.stringify(this.state.currentData)) ||
            (localDataStore.sections && JSON.stringify(this.state.Sections)!==JSON.stringify(sortObject(localDataStore.sections))) ||
            (localDataStore.weighings && JSON.stringify(this.state.Weighings)!==JSON.stringify(sortObject(localDataStoree.weighings)))
        ) {
          const {CurrentData, WeighingsList, Weighings} = this.setData(this.props)
          this.setState({
            currentData: CurrentData,

            WeighingsList: WeighingsList,
            Weighings: Weighings,
          })
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading

    const localDataStore = this.props.firebase.localDataStore
    const CurrentData = this.state.currentData

    if (!CurrentData.data) {
      return(
        <View style={[Helpers.fillRowCenter, Helpers.center, Helpers.logoContainer]}>
          <Image style={Helpers.fullSize} source={Images.Hexa_icon} resizeMode={'contain'} />
        </View>
      )
    } else {
      var pigcount = CurrentData.data.datas.count?CurrentData.data.datas.count:0
      var penWeight = []
      if (this.state.Weighings.length > 0) {
        var count = pigcount?pigcount:1
        penWeight = this.state.Weighings[0][Object.keys(this.state.Weighings[0])].weight / this.state.Weighings[0][Object.keys(this.state.Weighings[0])].count
      }

      var batch_name = CurrentData.data.datas.batch?CurrentData.data.datas.batch:"No Batch"
      var batch = {}

      //TODO: Cleanup load mem throws..
      var age = "";
      var displayWeight = penWeight;
      if (CurrentData.data.datas.batch && localDataStore.batchs && localDataStore.batchs[CurrentData.data.datas.batch]) {
        batch = localDataStore.batchs[CurrentData.data.datas.batch];
        var StartTime = batch.time;
        age = Math.floor(((Date.now()-StartTime)/(1000*60*60*24)));
        CurrentData.data.datas.gcurve = batch.gcurve;

        var Predict = CurvePredict(localDataStore, CurrentData.penId)
        displayWeight = Predict.Now
      }

      if (!batch.datas) batch.datas = {};
      if (!batch.datas.devianceMin) batch.datas.devianceMin = 0;
      if (!batch.datas.devianceMax) batch.datas.devianceMax = 0;

      return(
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
          <View style={[Helpers.buttonContainer, Helpers.midWidth, Metrics.bottomMargin, Helpers.rowCenter] }>
            <View style={{padding:6, paddingRight: 15}}>
              <Icon name="md-arrow-back" type='ionicon' size={25} color="black" onPress={() => onPageSwipe(this, localDataStore, CurrentData, false)}/>
            </View>
            <View style={[Helpers.buttonContainer, Helpers.midWidth] }>
              <Button Text={CurrentData.penId} buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
                onPress={() => {} }/>
            </View>
            <View style={{padding:6, paddingLeft: 15}}>
              <Icon name="md-arrow-forward" type='ionicon' size={25} color="black" onPress={() => onPageSwipe(this, localDataStore, CurrentData, true)}/>
            </View>
          </View>
          
          <View style={[Helpers.fullWidth]}>
            <ContentProp texts={["Pig count", "Batch", "Age Days", "Weight", "Growth Cruve", "Alarm Max", "Alarm Min", "Mixtur", "Correction", "Portion in kg"]} onPress={(element) => onDataPress(1, element)} 
              datas={[pigcount, batch_name, age, /*penWeight*/ displayWeight, CurrentData.data.datas.gcurve?CurrentData.data.datas.gcurve:"default", batch.datas.devianceMin + " %", batch.datas.devianceMax + " %", "-", "-", "-"]} />
          </View>

          <View style={{height:20}} />
          <View style={[Helpers.fill, App?Helpers.fullWidth:Helpers.midWidth, App?{}:{height:215}]}>
            <ContentProp texts={["Last weighings"]} datas={[]} />
            <View style={{height:10}} />
            <FlatList
              data={this.state.Weighings}
              renderItem={({item, index}) => {
                if (item === "__jsogObjectId") return (<></>);
                var Weight = this.state.WeighingsList[Object.keys(item)[0]]
                return(
                  <ContentProp texts={[format_Time(Weight.time)]}
                              datas={[Weight.weight]} 
                  />
                )}
              }
                //Setting the number of column
                numColumns={1}
                keyExtractor={(item, index) => String(index)}
              />
          </View>

          <View style={{height:30}} />

          <View style={[Helpers.borderContainer, Helpers.midWidth]}>
            <OperationsPanel />
          </View>
        </View>
      )
    }
  }
}

function onPageSwipe(Control, localDataStore, CurrentData, dir) {
  const section = localDataStore.pens[CurrentData.penId].section
  const Pens = sortObject(localDataStore.sections[section].pens)

  if (CurrentData.type === DataRefs.PENS && CurrentData.data && Pens[CurrentData.penId]) {
    var index = Object.keys(Pens).indexOf(CurrentData.penId)

    switch (dir) {
      case true: //forward
        index = index +1
        break;
      case false: //backward
        index = index -1
        break;
    }

    if (index < 0) index = Object.keys(Pens).length - 1
    else if (index >= Object.keys(Pens).length) index = 0

    Control.props.setCurrentData(Object.keys(Pens)[index])
    NavigationService.replace("DataDisplay")
  }
}

function onDataPress(sender, element){
  switch(sender){
    case 1:
      switch(element.index){
        case 0:
          console.log("test")
          break;
      }
      break;
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    getPens: (DataTime) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.PENS, {DataTime: DataTime})),
    getWeights: (penId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.WEIGHING, {PenId: penId})),
    getBatch: (batchId) => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.BATCHS, {BatchId: batchId})),

    setCurrentData: penId => dispatch(FirebaseActions.setCurrentData({type:DataRefs.PENS, penId: penId})),
    setCurrentDataElement: element => dispatch(FirebaseActions.setCurrentData(element)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(DataDisplay_Pigs);
