/**
 * Reducers specify how the application's state changes in response to actions sent to the store.
 *
 * @see https://redux.js.org/basics/reducers
 */

import { createReducer } from 'reduxsauce'
import { append } from 'funcadelic';

import { Actions, DataRefs } from './Selectors'
import { FirebaseTypes } from './Actions'

import { INITIAL_STATE } from './InitialState'

export const firebaseIsLoading = (state, { loadingstate }) => ({
  ...state,
  firebaseIsLoading: loadingstate,
})

export const setFirebaseError = (state, { errorMessage }) => ({
  ...state,
  firebaseErrorMessage: errorMessage,
})

export const setViewError = (state, { errorMessage }) => {
  return({
    ...state,
    viewError: errorMessage,
  })
}

export const viewIsLoading = (state, { loadingstate }) => ({
  ...state,
  viewIsLoading: loadingstate,
})

export const setCurrentData = (state, {data}) => {
  return({
    ...state,
    currentData: data
  })
}

function IDGenerate(){
  return Math.random().toString(36).substr(2, 9);
}

export const actionStarting = (state) => ({
  ...state,
  //firebaseIsLoading: true,
  firebaseErrorMessage: null,
})

export const fetchStarting = (state) => ({
  ...state,
  //firebaseIsLoading: true,
  firebaseErrorMessage: null,
})

function mergeDeep(target, source) {
  const isObject = (obj) => obj && typeof obj === 'object';

  if (!isObject(target) || !isObject(source)) {
    return source;
  }

  Object.keys(source).forEach(key => {
    const targetValue = target[key];
    const sourceValue = source[key];

    if (Array.isArray(targetValue) && Array.isArray(sourceValue)) {
      //target[key] = targetValue.concat(sourceValue);  //Mercing value of arrays would creates doubles.. like curve data..
    } else if (isObject(targetValue) && isObject(sourceValue)) {
      target[key] = mergeDeep(Object.assign({}, targetValue), sourceValue);
    } else {
      target[key] = sourceValue;
    }
  });

  return target;
}

export const dataFetchSuccess = (state, { params }) => {
  const {type, path, data} = params
  var NewDataStore = state.localDataStore;
  var NewFirebaseUser = {...state.firebaseUser};
  switch(type) {
    case Actions.UPDATE:
    case Actions.ADD:
      var newData = {...data}
      if (path === DataRefs.FIREBASEUSER) {
        if (data && data.data) newData = {...data.data}
        if (data && data._user) {
          Object.keys(data._user).forEach(element => {
            newData[element] = data._user[element]
          })
          delete newData._user
          delete newData._auth
        }
        if (newData && Object.keys(newData).length > 0) {
          var Values = ["displayName", "name", "lastN", "email", "emailVerified", "address", "country", "farms", "isAnonymous", "phoneNumber", "photoURL", "refreshToken", "state", "uid"]
          Object.keys(newData).forEach(element => {
            for (var index = 0; index < Values.length; index++) {
              if (Values[index] === element) {
                NewFirebaseUser[element] = newData[element]
                break;
              }
            }
          });
        }else {
          NewFirebaseUser = newData;
        }
      }
      if (!data) break;
      switch(path){
        case DataRefs.FIREBASEUSER:
          //Done above
          break;
        case DataRefs.USERSDATA:
          Object.keys(newData).forEach(element => {
            if (!NewDataStore.userData) NewDataStore.userData = {};
            NewDataStore.userData[element] = newData[element]
          });
          break;
        case DataRefs.USERS:
          if (!NewDataStore.users) NewDataStore.users = {};
          if (data.data) newData = data.data
          if (newData.users) newData = newData.users
          Object.keys(newData).forEach(element => {
            NewDataStore.users[element] = newData[element]
          });
          break;
        case DataRefs.FARMS:
          if (!NewDataStore.farms) NewDataStore.farms = {};
          if (data.farms) newData = data.farms
          Object.keys(newData).forEach(element => {
            NewDataStore.farms[element] = newData[element]
          });
          break;
        case DataRefs.BATCHS:
          if (!NewDataStore.batchs) NewDataStore.batchs = {};
          if (data.batchs) newData = data.batchs
          Object.keys(newData).forEach(element => {
            NewDataStore.batchs[element] = newData[element]
          });
          break;
        case DataRefs.SECTIONS:
          if (!NewDataStore.sections) NewDataStore.sections = {};
          if (data.sections) newData = data.sections
          Object.keys(newData).forEach(element => {
            NewDataStore.sections[element] = newData[element]
          });
          break;
        case DataRefs.PENS:
          if (!NewDataStore.pens) NewDataStore.pens = {};
          if (data.pens) newData = data.pens
          Object.keys(newData).forEach(element => {
            NewDataStore.pens[element] = newData[element]
          });
          break;
        case DataRefs.PIGS:
          if (!NewDataStore.pigs) NewDataStore.pigs = {};
          if (data.pigs) newData = data.pigs
          Object.keys(newData).forEach(element => {
            NewDataStore.pigs[element] = newData[element]
          });
          break;
        case DataRefs.COMMENTS:
          if (!NewDataStore.comments) NewDataStore.comments = {};
          if (data.comments) newData = data.comments
          Object.keys(newData).forEach(element => {
            NewDataStore.comments[element] = newData[element]
          });
          break;
        case DataRefs.GRAPH:
          if (!NewDataStore.graphs) NewDataStore.graphs = {};
          if (data.graphs) newData = data.graphs
          Object.keys(newData).forEach(element => {
            NewDataStore.graphs[element] = newData[element]
          });
          break;
        case DataRefs.WEIGHING:
          if (!NewDataStore.weighings) NewDataStore.weighings = {};
          if (data.weighings) newData = data.weighings
          Object.keys(newData).forEach(element => {
            NewDataStore.weighings[element] = newData[element]
          });
          break;
        case DataRefs.CONSYS:
          if (!NewDataStore.consys) NewDataStore.consys = {};
          if (data.consys) newData = data.consys
          Object.keys(newData).forEach(element => {
            NewDataStore.consys[element] = newData[element]
          });
          break;
      }
      //console.log(path, data)
      break;
    case Actions.CLEAR:
      switch(path){
        case DataRefs.USERS:
          if (!NewDataStore.userData) NewDataStore.userData = {};
          NewDataStore.userData.action = "CLEAR";
          break;
        case DataRefs.USERSDATA:
          console.log("CLEAR")
          if (NewDataStore.batchs) NewDataStore.batchs = {}
          if (NewDataStore.sections) NewDataStore.sections = {}
          if (NewDataStore.pens) NewDataStore.pens = {}
          if (NewDataStore.pigs) NewDataStore.pigs = {}
          if (NewDataStore.graphs) NewDataStore.graphs = {}
          if (NewDataStore.weighings) NewDataStore.weighings = {}
          if (NewDataStore.consys) NewDataStore.consys = {}
          if (NewDataStore.userData && NewDataStore.userData.action) {
            NewDataStore.userData.action = {}
            //NewDataStore.userData = {}
          }
          break;
      }
      break;
    default:
      console.log("DataFetch Not created / default")
      break;
  }
  return({
    ...state,
    firebaseUser: NewFirebaseUser,
    localDataStore: NewDataStore,
    firebaseIsLoading: false,
    firebaseErrorMessage: null,
    viewIsLoading: false,
  })
}

export const fetchSuccess = (state, { data }) => {
  var newlocalDataStore = {...state.localDataStore}
  var newState = {...state}

  if (data) {
    let {localDataStore, ..._newState} = data;

    if (localDataStore) {
      if (Object.keys(localDataStore).length !== 0) {
        newlocalDataStore = mergeDeep(localDataStore, newlocalDataStore)
      }
      else {
        newlocalDataStore = {...localDataStore}
      }
    }
    newState = append(state,_newState)
  }
  return({
    ...newState,
    localDataStore: newlocalDataStore,
    firebaseIsLoading: false,
    firebaseErrorMessage: null,
    viewIsLoading: false,
  })
}

export const fetchFailure = (state, { errorMessage }) => ({
  ...state,
  firebaseIsLoading: false,
  firebaseErrorMessage: errorMessage,
  viewIsLoading: false,
})

/**
 * @see https://github.com/infinitered/reduxsauce#createreducer
 */
export const HANDLERS = {
  //Response...
  [FirebaseTypes.FIREBASE_IS_LOADING]: firebaseIsLoading,
  [FirebaseTypes.SET_FIREBASE_ERROR]: setFirebaseError,

  [FirebaseTypes.SET_VIEW_ERROR]: setViewError,
  [FirebaseTypes.VIEW_IS_LOADING]: viewIsLoading,

  [FirebaseTypes.SET_CURRENT_DATA]: setCurrentData,

  [FirebaseTypes.ACTION_STARTING]: actionStarting,
  [FirebaseTypes.FETCH_STARTING]: fetchStarting,
  [FirebaseTypes.DATA_FETCH_SUCCESS]: dataFetchSuccess,
  [FirebaseTypes.FETCH_SUCCESS]: fetchSuccess,
  [FirebaseTypes.FETCH_FAILURE]: fetchFailure,
}

export const reducer = createReducer(INITIAL_STATE, HANDLERS)