import { Platform } from 'react-native';

import { put, call } from 'redux-saga/effects'

import NavigationService from '../Services/NavigationService'

import ExampleActions from '../Stores/Example/Actions'
import DeviceActions from '../Stores/Device/Actions'
import FirebaseActions from '../Stores/Firebase/Actions'

/**
 * The startup saga is the place to define behavior to execute when the application starts.
 */
export function* startup() {
  // Dispatch a redux action using `put()`
  // @see https://redux-saga.js.org/docs/basics/DispatchingActions.html
  console.log("Starting sagas")
  
  // Add more operations you need to do at startup here
  //yield put(ExampleActions.fetchUser())
  if (Platform.OS === 'ios' || Platform.OS === 'android') {
    yield put(DeviceActions.deviceStartup())
  }
  yield put(FirebaseActions.firebaseStartup())

  // When those operations are finished we redirect to the main screen
  NavigationService.navigateAndReset('MainStack')
}
