import React, { Component } from 'react'
import { Text, View, ActivityIndicator, Platform } from 'react-native'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs } from '../../../Stores/Firebase/Selectors'

import { Button } from '../../../Components'
import Style from './AuthStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../../Theme'

let auth;
if (Platform.OS === 'ios' || Platform.OS === 'android') {
  //#region App version imports
  auth = require('@react-native-firebase/auth').default;
  //#endregion
} else {
  //#region Web Version imports
  var firebase = require('firebase/app').default;
  require('firebase/auth').default;

  auth = firebase.auth;
  //#endregion
}

class VerifyScreen extends Component{
  constructor(props){
    super(props);
    
    this.state = {
    }
  }

  setData(Props) {
  }

  getData() {
  }

  checkData(props) {
    if (props.firebase && props.firebase.firebaseUser) {
      var User = auth().currentUser;

      if (User && !props.firebase.firebaseUser.emailVerified && !props.firebase.viewIsLoading){
        User.reload().then((data)=>{
          if(User.emailVerified) {
            this.props.setViewLoading(true)
            this.props.setUser(User.emailVerified)
            console.log("Verified")
            NavigationService.navigate("AuthStack")
          }
        })
      }
      
      if (props.firebase.firebaseUser.emailVerified) NavigationService.navigate("AuthStack")
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);
    }
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  componentWillUnmount(){
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const firebaseUser = this.props.firebase.firebaseUser
    const ErrorMessage = this.props.firebase.viewError

    return(
      <View
        style={[
          Helpers.mainContainer,
        ]}
      >
        <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fillColMain]}>
          {ErrorMessage ? (
            <Text style={ApplicationStyles.error}>{ErrorMessage}</Text>
          ):(<></>)}

          <View>
            <Text>Please verify your account by accepting the email, sent to your current account</Text>
            <Text>This page will not refresh itself...</Text>
            <Button Text="Resend verification"
              onPress={() => {
                console.log("send", firebaseUser, this.state.User)
                if (firebaseUser && this.state.User) {
                  this.state.User.sendEmailVerification()
                }
              }}/>
            <Button Text="Refresh"
              onPress={() => {
                this.checkData(this.props, this.state)
              }}/>
          </View>
          
        </View>
        {(IsLoading || this.props.firebase.viewIsLoading) &&
          <ActivityIndicator style={Helpers.loadingContainer} size='large' />
        }
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),

    setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),

    setUser: (Verified) => dispatch(FirebaseActions.actionStarting(Actions.UPDATE, DataRefs.USERS, {emailVerified:Verified, })),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(VerifyScreen);
//export default VerifyScreen;