import { StyleSheet } from 'react-native'
import { Colors, Fonts, Metrics, } from '../Theme'

export default {
  //********************** col sets */
  column: {
    flexDirection: 'column',
  },
  colMain: {
    flexDirection: 'column',
    justifyContent: 'center',
  },
  colCenter: {
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
  },
  colCross: {
    flexDirection: 'column',
    alignItems: 'center',
  },
  columnReverse: {
    flexDirection: 'column-reverse',
  },

  //********************** Row sets */
  row: {
    flexDirection: 'row',
  },
  rowMain: {
    flexDirection: 'row',
    justifyContent: 'center',
  },
  rowCenter: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
  },
  rowCross: {
    flexDirection: 'row',
    alignItems: 'center',
  },
  rowReverse: {
    flexDirection: 'row-reverse',
  },

  //********************** Cross sets */
  center: {
    alignItems: 'center',
    justifyContent: 'center',
  },
  crossCenter: {
    alignItems: 'center',
  },
  crossEnd: {
    alignItems: 'flex-end',
  },
  crossStart: {
    alignItems: 'flex-start',
  },
  crossStretch: {
    alignItems: 'stretch',
  },

  //********************** Fill sets */
  fill: {
    flex: 1,
  },
  fillCenter: {
    flex: 1,
    alignItems: 'center',
    justifyContent: 'center',
  },
  fillCol: {
    flex: 1,
    flexDirection: 'column',
  },
  fillColCenter: {
    flex: 1,
    flexDirection: 'column',
    alignItems: 'center',
    justifyContent: 'center',
  },
  fillColCross: {
    flex: 1,
    flexDirection: 'column',
    alignItems: 'center',
  },
  fillColMain: {
    flex: 1,
    flexDirection: 'column',
    justifyContent: 'center',
  },
  fillColReverse: {
    flex: 1,
    flexDirection: 'column-reverse',
  },
  fillRow: {
    flex: 1,
    flexDirection: 'row',
  },
  fillRowCenter: {
    flex: 1,
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
  },
  fillRowCross: {
    flex: 1,
    flexDirection: 'row',
    alignItems: 'center',
  },
  fillRowMain: {
    flex: 1,
    flexDirection: 'row',
    justifyContent: 'center',
  },
  fillRowReverse: {
    flex: 1,
    flexDirection: 'row-reverse',
  },

  //********************** Size sets */
  fullSize: {
    height: '100%',
    width: '100%',
  },
  halvWidth: {
    width: '50%',
  },
  midWidth: {
    width: '75%',
  },
  fullWidth: {
    width: '100%',
  },

  //********************** Main sets */
  mainCenter: {
    justifyContent: 'center',
  },
  mainEnd: {
    justifyContent: 'flex-end',
  },
  mainSpaceAround: {
    justifyContent: 'space-around',
  },
  mainSpaceBetween: {
    justifyContent: 'space-between',
  },
  mainStart: {
    justifyContent: 'flex-start',
  },

  //********************** Container sets */
  mainContainer:{
    flex: 1,
    flexDirection: 'column',
    alignItems: 'center',
    ...Metrics.mediumHorizontalMargin,
  },
  contentContainer:{
    flex:1,
    flexDirection: 'column',
    alignItems: 'center',
    ...Metrics.mediumHorizontalMargin,
    marginTop: Metrics.small,
    ...Metrics.bottomMargin,
  },
  borderContainer: {
    borderWidth: 1,
    borderColor: '#000',
    borderRadius: 15,
    ...Metrics.tinyHorizontalPadding,
    ...Metrics.tinyVerticalPadding,
    maxWidth:450,
    zIndex: 1,
  },
  textContainer:{
    ...Fonts.normal,
    marginBottom: Metrics.normal,
    alignItems: 'center',
  },
  buttonContainer: {
    marginBottom: Metrics.small,
    maxWidth:450,
  },
  loadingContainer: {
    position: 'absolute',
    left: 0,
    right: 0,
    top: 0,
    bottom: 0,
    alignItems: 'center',
    justifyContent: 'center',
    backgroundColor: '#F5FCFF88',
  },
  logoContainer: {
    width: '100%',
    height: 220,
  },

  //**********************  */
  backgroundReset: {
    backgroundColor: Colors.transparent,
  },

  rotate90: {
    transform: [{ rotate: '90deg' }],
  },
  rotate90Inverse: {
    transform: [{ rotate: '-90deg' }],
  },
  mirror: {
    transform: [{ scaleX: -1 }],
  },

  scrollSpaceAround: {
    flexGrow: 1,
    justifyContent: 'space-around',
  },
  scrollSpaceBetween: {
    flexGrow: 1,
    justifyContent: 'space-between',
  },

  selfStretch: {
    alignSelf: 'stretch',
  },

  textCenter: {
    textAlign: 'center',
  },
  textJustify: {
    textAlign: 'justify',
  },
  textLeft: {
    textAlign: 'left',
  },
  textRight: {
    textAlign: 'right',
  },
};
