import { Platform, PermissionsAndroid } from 'react-native'
import { Config } from '../Config'

async function isPermit_Android() {
  if (Platform.OS === 'android' && Platform.Version >= 23) {
    const Have_Coarse_Location = await PermissionsAndroid.check(PermissionsAndroid.PERMISSIONS.ACCESS_COARSE_LOCATION);
   if (!Have_Coarse_Location) {
      const Got_Access = await PermissionsAndroid.request(PermissionsAndroid.PERMISSIONS.ACCESS_COARSE_LOCATION);
      if (Got_Access !== PermissionsAndroid.RESULTS.GRANTED) {
        console.log("No Ble Permission granted : Android");
        return false;
      }
    }
  }
  return true;
}

function isBlePermitted() {
  return new Promise((resolve, reject) => {
    if (Platform.OS === 'android' && Platform.Version >= 23) {
      if (!isPermit_Android()){
        resolve('This application needs premissions to be using BLE');
      }
    }
    resolve(null)
  })
}

export const deviceService = {
  isBlePermitted,
  //isPermit_Android,
}
