import React, { useEffect, Component } from 'react'
import { Text, View, ActivityIndicator, Image, StyleSheet, ScrollView, FlatList, SafeAreaView } from 'react-native'
import { Input, Icon } from 'react-native-elements'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, DataRefs, ConsysRefs, GetCurve_FromBatch, CurvePredict, GetBatchEnd, AccumulateTime, format_Time, GetStats_FromBatch, GetStats_FromPen } from '../../../Stores/Firebase/Selectors'

import { Button, TitleElement } from '../../../Components'
import Style from './UserPanelStyle'
import { ApplicationStyles, Helpers, Metrics, Colors } from '../../../Theme'
import CurveElement, { WeightsToGraphData } from './CurveElement'
import { BarChart } from 'react-native-chart-kit'

import PureChart from 'react-native-pure-chart';
import Chart from 'react-apexcharts'

class AutoPig extends Component {
  constructor(props) {
    super(props);

    var { FarmData, ValveData, ScaleData, categories, series } = this.setData(props)
    this.state = {
      FarmData: FarmData,
      ValveData: ValveData,
      ScaleData: ScaleData,

      Limit: 300,
      SysId: 2,
      STime: undefined,
      ETime: undefined,
      LastId: undefined,

      options: {
        chart: {
          id: 'AutoPigChart'
        },
        plotOptions: {
          bar: {
            columnWidth: "50%"
          }
        },
        stroke: {
          width: [2, 0, 0]
        },
        markers: {
          size: 3.5,
          strokeWidth: 2,
          fillOpacity: 0,
          strokeOpacity: 0,
          hover: {
            size: 6
          }
        },
        yaxis: {
          tickAmount: 20,
        },
        xaxis: {
          type: 'datetime'
          //categories: categories,
        }
      },
      series: series,
    }
  }

  setData(Props) {
    const localDataStore = Props.firebase.localDataStore;
    const FirebaseUser = this.props.firebase.firebaseUser;
    var Site = null, refToken = null, accessToken = null;
    var FarmData = null, ValveData = {}, ScaleData = {};
    var categories = [], series = [];

    if (FirebaseUser && FirebaseUser.farms && localDataStore && localDataStore.farms) {
      const FarmsData = localDataStore.farms
      if (FarmsData && FarmsData[Object.keys(FirebaseUser.farms)[0]]) {
        FarmData = FarmsData[Object.keys(FirebaseUser.farms)[0]]
        /*if (FarmData.site && FarmData.refToken) {
          Site = FarmData.site
          refToken = FarmData.refToken
          if (FarmData.token) accessToken = FarmData.token
        }*/
      }
    }
    if (localDataStore && localDataStore.consys) {
      if (localDataStore.consys.feedData) {
        ValveData = localDataStore.consys.feedData

        Object.keys(ValveData).filter(e => (e !== '__jsogObjectId')).forEach(element => {
          var data = ValveData[element]
          if (series) {
            var DataSet = series.find(x => x.name === data.valve)
            if (DataSet) {
              DataSet.data.push(data.amount)
            } else {
              series.push({name:data.valve, type:"column", data:[{x:data.timestamp, y:data.amount}]})
            }
          }
        });
      }
      if (localDataStore.consys.scaleData){
        ScaleData = localDataStore.consys.scaleData

        var DataSet2 = []
        Object.keys(ScaleData).filter(e => (e !== '__jsogObjectId')).forEach(element => {
          var data = ScaleData[element]
          console.log(new Date(data.timestamp * 1000));
          DataSet2.push({x: (data.timestamp*1000), y:data.amount})
        });
        if (DataSet2.length > 0) series.push({name:'AutoScale', type:"line", data:DataSet2})
      }
    }
    console.log(series)
    return { FarmData, ValveData, ScaleData, categories, series }
  }

  getData() {
    const localDataStore = this.props.firebase.localDataStore;
    const FirebaseUser = this.props.firebase.firebaseUser;

    this.props.getFarm()

    /*if (FirebaseUser && FirebaseUser.farms && localDataStore && localDataStore.farms) { 
      const FarmsData = localDataStore.farms
      if (FarmsData && FarmsData[Object.keys(FirebaseUser.farms)[0]]) {
        const FarmData = FarmsData[Object.keys(FirebaseUser.farms)[0]]
        if (FarmData.site && FarmData.refToken) {
          this.props.getAutoPig()
        }
      }
    }*/
  }

  checkData(props) {
    const localDataStore = this.props.firebase.localDataStore;
    const FirebaseUser = this.props.firebase.firebaseUser;

    //THIS check is used many times and in future wil be unnessasary as this page is not shown unless all is true
    //Can provoke loopback pages..
    if (FirebaseUser && FirebaseUser.farms && localDataStore && localDataStore.farms) { 
      const FarmsData = localDataStore.farms
      if (FarmsData && FarmsData[Object.keys(FirebaseUser.farms)[0]]) {
        const FarmData = FarmsData[Object.keys(FirebaseUser.farms)[0]]
        if (!FarmData.site || !FarmData.refToken) {
          NavigationService.nacigationPop()
        }
      }
    }
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);
      if (this.props.firebase.localDataStore && prevProps.firebase.localDataStore) {
        const localDataStore = this.props.firebase.localDataStore;
        const firebaseUser = this.props.firebase.firebaseUser
        
        if (localDataStore.farms && firebaseUser.farms && localDataStore.farms[Object.keys(firebaseUser.farms)[0]]) {
          var Farm = localDataStore.farms[Object.keys(firebaseUser.farms)[0]]
          if ((this.state.FarmData.site !== Farm.site || this.state.FarmData.refToken !== Farm.refToken || this.state.FarmData.token !== Farm.token) ||
              (localDataStore.consys && localDataStore.consys.scaleData && JSON.stringify(this.state.ScaleData) !== JSON.stringify(localDataStore.consys.scaleData)) ||
              (localDataStore.consys && localDataStore.consys.feedData && JSON.stringify(this.state.ValveData) !== JSON.stringify(localDataStore.consys.feedData))) {
           
            var { FarmData, ValveData, ScaleData, categories, series } = this.setData(this.props)
            console.log(ValveData, ScaleData)
            this.setState({
              FarmData: FarmData,
              ValveData: ValveData,
              ScaleData: ScaleData,

              options: {
                ...this.state.options,
                xaxis: {
                  ...this.state.options.xaxis,
                  categories: categories
                }
              },
              series:series
            })
          }
        }
      }
    }
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    this.props.setViewError(null)
    this.checkData(this.props);
    this.getData();
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading
    const ErrorMessage = this.props.firebase.viewError

    const localDataStore = this.props.firebase.localDataStore

    var Token = (this.state.FarmData.token)?this.state.FarmData.token:null

    return(
      <ScrollView style={[Helpers.fullSize, Helpers.fill]}>
        <View
          style={[
            Helpers.mainContainer,
          ]}
        >
          <View style={[Helpers.contentContainer, Helpers.fullSize, Helpers.fill]}>
            {ErrorMessage ? (
              <Text style={ApplicationStyles.error}>{ErrorMessage}</Text>
            ):(<></>)}
            <TitleElement Text={"AutoPigs"}/>
            <View>
              <View style={[Helpers.rowCenter]}>
                <Input label="SysId"
                  containerStyle={[{maxWidth:100}]}
                  style={[ApplicationStyles.input,{maxWidth:90}]}
                  onChangeText={text => {
                    if (!isNaN(text)) {
                      this.setState({
                        SysId:text
                      })
                    }
                  }}
                  value={this.state.SysId}
                />
                <Input label="Data Limit"
                  containerStyle={[{maxWidth:150}]}
                  style={[ApplicationStyles.input,{maxWidth:140}]}
                  onChangeText={text => {
                    if (!isNaN(text) || text === "") {
                      if (text === "") text = undefined
                      this.setState({
                        Limit:text
                      })
                    }
                  }}
                  value={this.state.Limit}
                />
                <Input label="LastId (Ids newer than)"
                  containerStyle={[{maxWidth:200}]}
                  style={[ApplicationStyles.input,{maxWidth:190}]}
                  onChangeText={text => {
                    if (text === "") text = undefined
                    if (text) {
                      //if (DataStringCheck(text)){
                        this.setState({LastId:text})
                      //}
                    }else {
                      this.setState({LastId:text})
                    }
                  }}
                  value={this.state.LastId}
                />
              </View>
              <View style={[Helpers.rowCenter]}>
                <Input label="Start Time : Epoch-sec (newest)"
                  containerStyle={[Helpers.halvWidth]}
                  style={[ApplicationStyles.input]}
                  onChangeText={text => {
                    if (text === "") {
                      this.setState({
                        STime:undefined
                      })
                    }
                    else if (!isNaN(text)) {
                      this.setState({
                        STime:text
                      })
                    }
                  }}
                  value={this.state.STime}
                />
                <Input label="End Time : Epoch-sec (oldest)"
                  containerStyle={[Helpers.halvWidth]}
                  style={[ApplicationStyles.input]}
                  onChangeText={text => {
                    if (text === "") {
                      this.setState({
                        ETime:undefined
                      })
                    }
                    else if (!isNaN(text)) {
                      this.setState({
                        ETime:text
                      })
                    }
                  }}
                  value={this.state.ETime}
                />
              </View>
              <View style={[Helpers.rowCenter]}>
                <Button Text="Fetch Scale_Data"
                  onPress={() => {
                    this.props.getAutoPig(this.state.Limit, this.state.SysId, this.state.STime, this.state.ETime, null, this.state.LastId)
                  }}/>
                <Button Text="Fetch Valve_Data"
                  onPress={() => {
                    this.props.getValveFeed(this.state.Limit, this.state.SysId, this.state.STime, this.state.ETime, null, this.state.LastId)
                  }}/>
              </View>
            </View>
            <View style={[Helpers.borderContainer, Helpers.fillCenter, Helpers.fullWidth, { minHeight:650, maxWidth:1237, width:1237, alignSelf:'center'}]}>
              <Chart options={this.state.options} series={this.state.series} width={1240} height={700} />
            </View>
          </View>
          {(IsLoading || this.props.firebase.viewIsLoading) &&
            <ActivityIndicator style={Helpers.loadingContainer} size='large' />
          }
        </View>
      </ScrollView>
    )
  }
}
//<CurveElement Display={true} DataPoints={DataSet} Height={600} Width={1250}/>

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),
    setViewError: error => dispatch(FirebaseActions.setViewError(error)),
    
    getFarm: () => dispatch(FirebaseActions.actionStarting(Actions.FETCH, DataRefs.FARMS, {})),
    getValveFeed: (limit, sysId, start, stop, offset, lastId) => dispatch(FirebaseActions.actionStarting(ConsysRefs.VALVEFEED, DataRefs.CONSYS, {limit:limit, sysId:sysId, start:start, stop:stop, offset:offset, lastId:lastId})),
    getAutoPig: (limit, sysId, start, stop, offset, lastId) => dispatch(FirebaseActions.actionStarting(ConsysRefs.SCALE, DataRefs.CONSYS, {limit:limit, sysId:sysId, start:start, stop:stop, offset:offset, lastId:lastId})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(AutoPig);
//export default AutoPig;