import React, {useRef, useState, useEffect} from 'react';
import { Text, View, StyleSheet, Animated } from 'react-native';

import { ApplicationStyles, Helpers, Metrics } from '../Theme'

const ProgressBar = props => {
  let animation = useRef(new Animated.Value(0));
  const progress = props.progress
  const Color = props.color?props.color:"#8BED4F"
  
  useEffect(() => {
    Animated.timing(animation.current, {
      toValue: progress,
      duration: 100
    }).start();
  },[progress])

  const width = animation.current.interpolate({
    inputRange: [0, 100],
    outputRange: ["0%", "100%"],
    extrapolate: "clamp"
  })
  return (
    <View style={[Helpers.borderContainer, Helpers.fullWidth, {borderWidth:0, minHeight:45, paddingBottom:10}]}>
      <View style={[Helpers.fill, Helpers.fullWidth, Helpers.colMain, {alignContent:'center'}]}>
        {props.text?(
        <Text style={{alignSelf:'flex-start', minHeight:20}}>
          {props.text}
        </Text>):(<></>)}
        <View style={styles.progressBar}>
          <Animated.View style={[StyleSheet.absoluteFill], {backgroundColor: Color, width }}/>
        </View>
      </View>
    </View>
  )
}

export default ProgressBar;

const styles = StyleSheet.create({
  container: {
    flex: 1,
    flexDirection: 'column',
    justifyContent: 'center',
    alignItems: 'center',
    paddingTop: 5,
    padding: 8,
  },
  progressBar: {
    flexDirection: 'row',
    height: 20,
    width: '100%',
    borderColor: '#000',
    borderWidth: 2,
    borderRadius: 5
  }
});
