import React, { useEffect, Component } from 'react'
import { Text, View, Image, Platform } from 'react-native'
import { useDispatch, useSelector, connect } from 'react-redux'
import { bindActionCreators } from 'redux';

import NavigationService from '../../Services/NavigationService'

let messaging;
if (Platform.OS === 'ios' || Platform.OS === 'android') {
  //#region App version imports
  messaging = require('@react-native-firebase/messaging').default;
  //#endregion
} else {
  //#region Web Version imports
  var firebase = require('firebase/app').default;
  require('firebase/messaging').default;

  if (firebase.messaging.isSupported()) {
    messaging = firebase.messaging();
  }
  //#endregion
}

import Style from './SplashScreenStyle'
import { Helpers, Images } from '../../Theme'

class SplashScreen extends Component {
  constructor(props){
    super(props);
    /*this.state = {
    }*/
  }

  getData(){
    if ((Platform.OS === "android" || Platform.OS === "ios") && messaging) {
      messaging().onNotificationOpenedApp(remoteMessage => {
        console.log(
          'Notification caused app to open from background state:',
          remoteMessage.notification,
        );
        NavigationService.navigate(remoteMessage.data.type);
      });

      // Check whether an initial notification is available
      messaging().getInitialNotification().then(remoteMessage => {
        if (remoteMessage) {
          console.log(
            'Notification caused app to open from quit state:',
            remoteMessage.notification,
          );
          NavigationService.navigate(remoteMessage.data.type); // e.g. "Settings"
        }
      });
    }
  }

  checkData(props){
  }

  componentDidMount(){
    //this.props.setFirebaseError(null)
    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps){
      this.checkData(this.props);
    }
  }

  render() {
    return(
      <View style={[Helpers.fillRowCenter, Style.container]}>
        <View style={[Helpers.center, Helpers.logoContainer]}>
          <Image style={Helpers.fullSize} source={Images.Hexa_icon} resizeMode={'contain'} />
        </View>
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    //setFirebaseError: error => dispatch(FirebaseActions.setFirebaseError(error)),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(SplashScreen);
//export default SplashScreen;
