import React from 'react';
import { StyleSheet, View, TouchableOpacity, Text, FlatList} from 'react-native';

import { ApplicationStyles, Helpers, Metrics, Fonts, Colors } from '../Theme'

const ContentProp = props => {
  var texts = [];
  if (props.texts){
    props.texts.forEach((element,i) => {
      texts.push(
        (<View style={[styles.contentBox, props.textsStyle]} key={"text_" + i}>
          <Text>{element}</Text>
        </View>)
      )
    });
  }

  var datas = [];
  if (props.datas) {
    props.datas.forEach((element,i) => {
      datas.push(
        (<TouchableOpacity style={[styles.contentBox, props.datasStyle]} onLongPress={() => {
          if (props.onPress){
            props.onPress({data: element, index: i})
          }
        }} key={"data_" + i}>
          <Text>{element}</Text>
        </TouchableOpacity>)
      )
    });
  }

  var length = texts.length > datas.length? texts.length : datas.length
  
  var view = [];
  for(var i = 0; i < length; i++) {
    var dataRow = [];
    if (i < texts.length) {
      dataRow.push(texts[i])
    } else {
      dataRow.push((<View style={[Helpers.fill, Helpers.colCenter]} key={"leftSpace_" + i}/>))
    }

    if (datas.length > 0) {
      dataRow.push((<View style={[Helpers.fill, Helpers.colCenter, {maxWidth:20}]} key={"seperator_" + view.length}/>))

      if (i < datas.length) {
        dataRow.push(datas[i])
      } else {
        dataRow.push((<View style={[Helpers.fill, Helpers.colCenter]} key={"rightSpace_" + i}/>))
      }
    }

    view.push(
      (<View style={[props.datas?Helpers.rowCenter:Helpers.halvWidth, Metrics.tinyVerticalMargin]} key={"row_" + view.length}>
        {dataRow}
      </View>)
    )
  }
  
  return (view);
};

const styles = StyleSheet.create({
  contentBox: {
    ...Helpers.fill,
    ...Helpers.colCenter,
    ...Helpers.borderContainer,
    borderRadius: 0,
    minHeight:30,
  }
});


export default ContentProp;