import React, { Component } from 'react'
import { Platform, Text, View, ScrollView, ActivityIndicator } from 'react-native'
import { Input } from 'react-native-elements'
import { connect } from 'react-redux'
import { bindActionCreators } from 'redux';
import NavigationService from '../../../Services/NavigationService'

import FirebaseActions from '../../../Stores/Firebase/Actions'
import { Actions, UserStates, DataRefs, DataStringCheck } from '../../../Stores/Firebase/Selectors'

import { Button } from '../../../Components'
import Style from './AuthStyle'
import { ApplicationStyles, Helpers, Metrics } from '../../../Theme'

import UserInformation from '../UserPanel/UserInformation'
import Subscription from '../UserPanel/Subscription'
import FarmInformation from '../UserPanel/FarmInformation'

class CreateNewUserData extends Component{
  constructor(props){
    super(props);
    this.state = {
      ViewState: 1,
    }
  }

  getData() {
  }

  checkData(props) {
  }

  componentDidMount() {
    this.props.setViewLoading(false)
    //this.props.setViewError(null)

    this.checkData(this.props);
    this.getData();
  }

  componentDidUpdate(prevProps, prevState) {
    if (this.props !== prevProps) {
      this.checkData(this.props);
      /*if (this.props.firebase.viewError != prevProps.firebase.viewError) {
        this.forceUpdate()
      }*/
      if (this.props.firebase.firebaseUser !== prevProps.firebase.firebaseUser) {
        if (prevProps.firebase.firebaseUser.state !== UserStates.STARTER && this.state.ViewState !== 3) {
          NavigationService.nacigationPop()
        }
      }
    }
  }

  render() {
    const IsLoading = this.props.firebase.firebaseIsLoading

    var Display = [];
    if (this.state.ViewState === 1) {
      Display = [(<UserInformation key={"View1"} OnDone={res => {
        if (res) {
          this.setState({
            ViewState:2
          }, this.forceUpdate())
        }else {
          NavigationService.nacigationPop()
        }
      }}/>)]
    } else if (this.state.ViewState === 2) {
      Display = [(<Subscription key={"View2"} OnDone={res => {
        if (res && res != "") {
          var FarmName = "Not set"
          if (this.props.firebase.firebaseUser.lastN) FarmName = this.props.firebase.firebaseUser.lastN;
          this.props.precreateUserData(res, FarmName)

          this.setState({
            ViewState:3
          }, this.forceUpdate())
        }else {
          NavigationService.nacigationPop()
        }
      }}/>)]
    } else if (this.state.ViewState === 3) {
      Display = [(<FarmInformation key={"View3"} OnDone={res => {
        if (res && res != "") {
          this.props.clearDataMem()
          NavigationService.navigateAndReset('AuthStack')
        }
      }}/>)]
    } else {
      this.props.clearDataMem()
      NavigationService.navigateAndReset('AuthStack')
    }

    return(
      <View>
        {Display}
      </View>
    )
  }
}

const mapStateToProps = (state) => {
  const { firebase } = state
  return { firebase }
};

const mapDispatchToProps  = (dispatch) => {
  return {
    setViewLoading: state => dispatch(FirebaseActions.viewIsLoading(state)),

    clearDataMem: () => dispatch(FirebaseActions.actionStarting(Actions.CLEAR, DataRefs.USERSDATA)),
    updateUser: (Data) => dispatch(FirebaseActions.actionStarting(Actions.UPDATE, DataRefs.USERSDATA, {Data: Data})),
    precreateUserData: (Subsc, FarmName) => dispatch(FirebaseActions.actionStarting(Actions.CREATE, DataRefs.USERSDATA, {UserData: {state:Subsc}, Farms:{name:FarmName}, Sections:{}, Pens:{}})),
  }
};

export default connect(mapStateToProps, mapDispatchToProps)(CreateNewUserData);
//export default CreateNewUserData;