import React from 'react';
import { StyleSheet, View, TouchableOpacity, Text, Image} from 'react-native';

import Button from './Button';

import { ApplicationStyles, Helpers, Metrics, Fonts, Colors, Images } from '../Theme'

const TitleElement = props => {
  return(
    <View style={[Helpers.buttonContainer, Helpers.midWidth, Metrics.bottomMargin,] }>
      <Button Text={props.Text} icon={props.icon} buttonStyle={{backgroundColor: Colors.AgriDarkblue}}
        onPress={() => {
          if (props.onPress){
            props.onPress()
          }
        } }/>
    </View>
  )
};

export default TitleElement;