import React from 'react'
import { createAppContainer } from 'react-navigation'
import { createStackNavigator } from 'react-navigation-stack'
import { TouchableOpacity, Icon, View, Text } from 'react-native'

import Front from '../Containers/Agrisys/FrontScreen'
import Example from '../Containers/Example/ExampleScreen'
import Device from '../Containers/Device/DeviceScreen'

import { ApplicationStyles } from '../Theme'
/**
 * The root screen contains the application's navigation.
 *
 * @see https://reactnavigation.org/docs/en/hello-react-navigation.html#creating-a-stack-navigator
 */
const StackNavigator = createStackNavigator(
  {
    // The main application screen is our "ExampleScreen". Feel free to replace it with your
    // own screen and remove the example.
    FrontScreen: {
      screen: Front,
      navigationOptions: () => ({
        headerMode: 'none',
        headerShown: false,
      }),
      path: 'main',
    },
    
    DeviceScreen: {
      screen: Device,
      navigationOptions: () => ({
        title: 'Device',
        headerTitleStyle:{
          ...ApplicationStyles.navigationHeader
        },
      }),
    },

    ExampleScreen: {
      screen: Example,
      navigationOptions: () => ({
        headerMode: 'none',
      }),
      path: 'example',
    }
  },
  {
    initialRouteName: 'FrontScreen',
    // See https://reactnavigation.org/docs/en/stack-navigator.html#stacknavigatorconfig
    headerMode: 'float',
    defaultNavigationOptions:{
      headerTitleAlign: 'center',
      headerRight: () => (<View style={{padding:6}}></View>),
    },
  }
)

export default StackNavigator
